import api from '../api'
import {getField, updateField} from 'vuex-map-fields';
import helper from './common/helper'
import router from "../router";

export const getters = Object.assign({getField}, helper.getters)

export const state = Object.assign({
  id: 0,
  yfp_store_review_id: '',
  section_1: false,
  section_2: false,
  section_3: false,
  section_4: false,
  yfp_store_review_created_at: '',
  yfp_store_review_title: '',
  yfp_store_review_vat: '',
  yfp_store_review_code: '',
  yfp_store_review_customer_business_code: '',
  yfp_store_review_customer_trust_number: '',
  yfp_store_review_manager: '',
  yfp_store_review_touch_man: '',
  yfp_store_review_touch_id: '',
  yfp_store_review_touch_title: '',
  yfp_store_review_touch_tel: '',
  yfp_store_review_touch_mobil: '',
  yfp_store_review_address: '',
  yfp_store_review_touch_mail: '',
  yfp_store_review_tel: '',
  yfp_store_review_touch_fax: '',
  yfp_store_review_trust_number: '',
  yfp_store_review_trust_par: '',
  yfp_store_review_trust_length_mon: '',
  yfp_store_review_contract_from: '',
  yfp_store_review_contract_to: '',
  yfp_store_review_bank_name: '',
  yfp_store_review_apply_bank: '',
  yfp_store_review_bank_number: '',
  yfp_store_review_atm_par: '',
  yfp_store_review_card_par: '',
  yfp_store_review_status: '',
  yfp_store_review_reject_message: ''
}, helper.state)

export const actions = Object.assign({
  pass({commit, state}) {
    commit('ui/loading', {isLoading: true}, {root: true})
    api.review.pass({
      id: state.id,
    }).then(() => {
      router.push('/admin/merchantdatabuild')
    }).catch((error) => {
      commit('ui/ajaxModalError', {error: error}, {root: true})
    }).finally(() => {
      commit('ui/loading', {isLoading: false}, {root: true})
    })
  },
  reject({commit, state}) {
    commit('ui/loading', {isLoading: true}, {root: true})
    api.review.reject({
      id: state.id,
      yfp_store_review_reject_message: state.yfp_store_review_reject_message
    }).then(() => {
      router.push('/admin/merchantdatabuild')
    }).catch((error) => {
      commit('ui/ajaxModalError', {error: error}, {root: true})
    }).finally(() => {
      commit('ui/loading', {isLoading: false}, {root: true})
    })
  },
  apply({commit, state}) {
    commit('ui/loading', {isLoading: true}, {root: true})
    api.review.apply({
      id: state.id,
      yfp_store_review_customer_trust_number: state.yfp_store_review_customer_trust_number,
      yfp_store_review_customer_business_code: state.yfp_store_review_customer_business_code,
      yfp_store_review_trust_number: state.yfp_store_review_trust_number,
      yfp_store_review_trust_par: state.yfp_store_review_trust_par,
      yfp_store_review_trust_length_mon: state.yfp_store_review_trust_length_mon,
      yfp_store_review_contract_from: state.yfp_store_review_contract_from,
      yfp_store_review_contract_to: state.yfp_store_review_contract_to,
      yfp_store_review_bank_name: state.yfp_store_review_bank_name,
      yfp_store_review_apply_bank: state.yfp_store_review_apply_bank,
      yfp_store_review_bank_number: state.yfp_store_review_bank_number,
      yfp_store_review_atm_par: state.yfp_store_review_atm_par,
      yfp_store_review_card_par: state.yfp_store_review_card_par,
    }).then(() => {
      router.push('/admin/merchantdatabuild')
    }).catch((error) => {
      commit('ui/ajaxModalError', {error: error}, {root: true})
    }).finally(() => {
      commit('ui/loading', {isLoading: false}, {root: true})
    })
  },
  saveAll({commit, state}) {
    commit('ui/loading', {isLoading: true}, {root: true})
    api.review.saveAll({
      id: state.id,
      yfp_store_review_customer_trust_number: state.yfp_store_review_customer_trust_number,
      yfp_store_review_customer_business_code: state.yfp_store_review_customer_business_code,
      yfp_store_review_trust_number: state.yfp_store_review_trust_number,
      yfp_store_review_trust_par: state.yfp_store_review_trust_par,
      yfp_store_review_trust_length_mon: state.yfp_store_review_trust_length_mon,
      yfp_store_review_contract_from: state.yfp_store_review_contract_from,
      yfp_store_review_contract_to: state.yfp_store_review_contract_to,
      yfp_store_review_bank_name: state.yfp_store_review_bank_name,
      yfp_store_review_apply_bank: state.yfp_store_review_apply_bank,
      yfp_store_review_bank_number: state.yfp_store_review_bank_number,
      yfp_store_review_atm_par: state.yfp_store_review_atm_par,
      yfp_store_review_card_par: state.yfp_store_review_card_par,
    }).then(() => {
      commit('setValue', {
        key: 'section_1',
        val: false
      })
      commit('setValue', {
        key: 'section_2',
        val: false
      })
      commit('setValue', {
        key: 'section_3',
        val: false
      })
      commit('setValue', {
        key: 'section_4',
        val: false
      })
    }).catch((error) => {
      commit('ui/ajaxModalError', {error: error}, {root: true})
    }).finally(() => {
      commit('ui/loading', {isLoading: false}, {root: true})
    })
  },
  save1({commit, state}) {
    commit('ui/loading', {isLoading: true}, {root: true})
    api.review.save1({
      id: state.id,
      yfp_store_review_customer_trust_number: state.yfp_store_review_customer_trust_number,
      yfp_store_review_customer_business_code: state.yfp_store_review_customer_business_code,
    }).then(() => {
      commit('setValue', {
        key: 'section_1',
        val: false
      })
    }).catch((error) => {
      commit('ui/ajaxModalError', {error: error}, {root: true})
    }).finally(() => {
      commit('ui/loading', {isLoading: false}, {root: true})
    })
  },
  save2({commit, state}) {
    commit('ui/loading', {isLoading: true}, {root: true})
    api.review.save2({
      id: state.id,
      yfp_store_review_trust_number: state.yfp_store_review_trust_number,
      yfp_store_review_trust_par: state.yfp_store_review_trust_par,
      yfp_store_review_trust_length_mon: state.yfp_store_review_trust_length_mon,
      yfp_store_review_contract_from: state.yfp_store_review_contract_from,
      yfp_store_review_contract_to: state.yfp_store_review_contract_to,

    }).then(() => {
      commit('setValue', {
        key: 'section_2',
        val: false
      })
    }).catch((error) => {
      commit('ui/ajaxModalError', {error: error}, {root: true})
    }).finally(() => {
      commit('ui/loading', {isLoading: false}, {root: true})
    })
  },
  save3({commit, state}) {
    commit('ui/loading', {isLoading: true}, {root: true})
    api.review.save3({
      id: state.id,
      yfp_store_review_bank_name: state.yfp_store_review_bank_name,
      yfp_store_review_apply_bank: state.yfp_store_review_apply_bank,
      yfp_store_review_bank_number: state.yfp_store_review_bank_number,
    }).then(() => {
      commit('setValue', {
        key: 'section_3',
        val: false
      })
    }).catch((error) => {
      commit('ui/ajaxModalError', {error: error}, {root: true})
    }).finally(() => {
      commit('ui/loading', {isLoading: false}, {root: true})
    })
  },
  save4({commit, state}) {
    commit('ui/loading', {isLoading: true}, {root: true})
    api.review.save4({
      id: state.id,
      yfp_store_review_atm_par: state.yfp_store_review_atm_par,
      yfp_store_review_card_par: state.yfp_store_review_card_par,
    }).then(() => {
      commit('setValue', {
        key: 'section_4',
        val: false
      })
    }).catch((error) => {
      commit('ui/ajaxModalError', {error: error}, {root: true})
    }).finally(() => {
      commit('ui/loading', {isLoading: false}, {root: true})
    })
  },
  init({commit, state}) {
    commit('ui/loading', {isLoading: true}, {root: true})
    api.review.init({
      id: state.id,
    }).then((rep) => {
      commit('setValue', {
        key: 'yfp_store_review_status',
        val: rep.data.body.yfp_store_review_status
      })
      commit('setValue', {
        key: 'yfp_store_review_touch_tel',
        val: rep.data.body.yfp_store_review_touch_tel
      })
      commit('setValue', {
        key: 'yfp_store_review_touch_man',
        val: rep.data.body.yfp_store_review_touch_man
      })
      commit('setValue', {
        key: 'yfp_store_review_touch_mobil',
        val: rep.data.body.yfp_store_review_touch_mobil
      })
      commit('setValue', {
        key: 'yfp_store_review_id',
        val: rep.data.body.yfp_store_review_id
      })
      commit('setValue', {
        key: 'yfp_store_review_customer_trust_number',
        val: rep.data.body.yfp_store_review_customer_trust_number
      })
      commit('setValue', {
        key: 'yfp_store_review_created_at',
        val: rep.data.body.yfp_store_review_created_at
      })
      commit('setValue', {
        key: 'yfp_store_review_title',
        val: rep.data.body.yfp_store_review_title
      })
      commit('setValue', {
        key: 'yfp_store_review_vat',
        val: rep.data.body.yfp_store_review_vat
      })
      commit('setValue', {
        key: 'yfp_store_review_code',
        val: rep.data.body.yfp_store_review_code
      })
      commit('setValue', {
        key: 'yfp_store_review_customer_business_code',
        val: rep.data.body.yfp_store_review_customer_business_code
      })
      commit('setValue', {
        key: 'yfp_store_review_manager',
        val: rep.data.body.yfp_store_review_manager
      })
      commit('setValue', {
        key: 'yfp_store_review_touch_id',
        val: rep.data.body.yfp_store_review_touch_id
      })
      commit('setValue', {
        key: 'yfp_store_review_touch_title',
        val: rep.data.body.yfp_store_review_touch_title
      })
      commit('setValue', {
        key: 'yfp_store_review_address',
        val: rep.data.body.yfp_store_review_address
      })
      commit('setValue', {
        key: 'yfp_store_review_touch_mail',
        val: rep.data.body.yfp_store_review_touch_mail
      })
      commit('setValue', {
        key: 'yfp_store_review_tel',
        val: rep.data.body.yfp_store_review_tel
      })
      commit('setValue', {
        key: 'yfp_store_review_touch_fax',
        val: rep.data.body.yfp_store_review_touch_fax
      })
      commit('setValue', {
        key: 'yfp_store_review_trust_number',
        val: rep.data.body.yfp_store_review_trust_number
      })
      commit('setValue', {
        key: 'yfp_store_review_trust_par',
        val: rep.data.body.yfp_store_review_trust_par
      })
      commit('setValue', {
        key: 'yfp_store_review_trust_length_mon',
        val: rep.data.body.yfp_store_review_trust_length_mon>=12 ? rep.data.body.yfp_store_review_trust_length_mon : 12
      })
      commit('setValue', {
        key: 'yfp_store_review_contract_from',
        val: rep.data.body.yfp_store_review_contract_from
      })
      commit('setValue', {
        key: 'yfp_store_review_contract_to',
        val: rep.data.body.yfp_store_review_contract_to
      })
      commit('setValue', {
        key: 'yfp_store_review_bank_name',
        val: rep.data.body.yfp_store_review_bank_name
      })
      commit('setValue', {
        key: 'yfp_store_review_apply_bank',
        val: rep.data.body.yfp_store_review_apply_bank
      })
      commit('setValue', {
        key: 'yfp_store_review_bank_number',
        val: rep.data.body.yfp_store_review_bank_number
      })
      commit('setValue', {
        key: 'yfp_store_review_atm_par',
        val: rep.data.body.yfp_store_review_atm_par
      })
      commit('setValue', {
        key: 'yfp_store_review_card_par',
        val: rep.data.body.yfp_store_review_card_par
      })
    }).catch((error) => {
      commit('ui/ajaxError', {error: error}, {root: true})
    }).finally(() => {
      commit('ui/loading', {isLoading: false}, {root: true})
    })
  },
}, helper.actions);

export const mutations = Object.assign({updateField}, helper.mutations)



