import ajax from 'axios'

export const list = (data) => {
  return ajax.post(process.env.API_URL + 'appropriate_list/list', data, {
    headers: {
      'Authorization': 'Bearer ' + window.localStorage.getItem('jwt')
    }
  })
}

export const pass = (data) => {
  return ajax.post(process.env.API_URL + 'appropriate_list/pass', data, {
    headers: {
      'Authorization': 'Bearer ' + window.localStorage.getItem('jwt')
    }
  })
}

export const reject = (data) => {
  return ajax.post(process.env.API_URL + 'appropriate_list/reject', data, {
    headers: {
      'Authorization': 'Bearer ' + window.localStorage.getItem('jwt')
    }
  })
}



