import ajax from 'axios'

export const list = (data) => {
  return ajax.post(process.env.API_URL + 'user/users', data, {
    headers: {
      'Authorization': 'Bearer ' + window.localStorage.getItem('jwt')
    }
  })
}

export const trash = (data) => {
  return ajax.post(process.env.API_URL + 'user/delete_user', data, {
    headers: {
      'Authorization': 'Bearer ' + window.localStorage.getItem('jwt')
    }
  })
}

export const save = (data) => {
  return ajax.post(process.env.API_URL + 'user/save_user', data, {
    headers: {
      'Authorization': 'Bearer ' + window.localStorage.getItem('jwt')
    }
  })
}




