<template>
  <div class="">
    <button @click="exportExcel" class="btn btn-success d-flex justify-content-center align-items-center border-0  excel ml-auto">
      <img
        src="../../assets/img/xls.png"
        alt="import_excel"
        class="mr-3"
      >
      <span class="fz13 font-weight-bold">匯出EXCEL</span>
    </button>
  </div>
</template>

<script>
  export default {
    props: [
      "exportExcel",
    ],
  };
</script>

<style lang="scss">
  .excel {
    width: 170px;
    height: 30px;
    border-radius: 3px;
    img[alt="import_excel"] {
      width: 20px;
      height: auto;
    }
    .fz13 {
      font-size: 13px;
      line-height: 13px;
    }
  }
</style>



