<template>
  <div>
    <div class="d-flex align-items-cente px-40 CTRB_outerbox">
      <!-- 角色建立用 -->
      <div class="pl-4 d-flex align-items-center" v-if="location === '#/admin/buildcharacter'" data-toggle="modal"
           data-target="#addCharacterModal">
        <p class="plus mb-0">+</p>
        <p class="mb-0 font-weight-bold text-page ml-2 title">新增角色</p>
      </div>
      <!-- 管理使用者用 -->
      <div @click="addAdminUser()" class="pl-4 d-flex align-items-center" v-if="location === '#/admin/adminuser'">
        <p class="plus mb-0">+</p>
        <p class="mb-0 font-weight-bold text-page ml-2 title">新增使用者</p>
      </div>
      <!-- 角色建立用 -->
      <div class="input-group mb-0 w-25 ml-auto" v-if="location === '#/admin/buildcharacter'">
        <input @keyup.enter="getList"
               type="text" v-model="buildcharacterSearch"
               class="form-control border-log border-right-0 filterHeightControl fz14 pl-3"
               placeholder="| 搜尋角色名稱"
        >
        <div class="input-group-append">
          <button @click="getList" class="btn btn-outline-log  border-left-0" id="basic-addon2">
            <i class="fas fa-search"/>
          </button>
        </div>
      </div>
      <!-- 管理使用者用 -->
      <div class="input-group mb-0 w-25 ml-auto" v-if="location === '#/admin/adminuser'">
        <input @keyup.enter="getList" v-model="adminUserSearch"
               type="text"
               class="form-control border-log border-right-0 filterHeightControl fz14 pl-3"
               placeholder="| 搜尋帳號或名稱"
        >
        <div class="input-group-append">
          <button @click="getList"
                  class="btn btn-outline-log  border-left-0"
                  id="basic-addon2"
          >
            <i class="fas fa-search"/>
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

  // paginationSearch

  import {mapFields} from "vuex-map-fields";
  import {mapActions} from "vuex";

  export default {
    props: [
      "getList",
      "search"
    ],
    computed: {
      ...mapFields("buildcharacter", ["buildcharacterSearch"]),
      ...mapFields("adminuser", ["adminUserSearch", "yfp_bank_user_id", "yfp_bank_user_name", "yfp_bank_user_account", "yfp_bank_user_yfp_bank_user_roles_id", "yfp_bank_user_password"])
    },
    data() {
      return {
        location: ''
      }
    },
    methods: {
      ...mapActions("ui", [
        "openModal",
      ]),
      addAdminUser() {
        this.yfp_bank_user_id = ''
        this.yfp_bank_user_name = ''
        this.yfp_bank_user_account = ''
        this.yfp_bank_user_yfp_bank_user_roles_id = ''
        this.yfp_bank_user_password = ''
        this.openModal({id: '#addUserModal'})
      }
    },
    created() {
      const vm = this;
      vm.location = window.location.hash;
    },
  }
</script>

<style lang="scss" scoped>
  .fz14 {
    font-size: 14px;
  }

  .CTRB_outerbox {
    background: #F7F7F7;
    padding: 24px 0;
  }

  .plus {
    width: 30px;
    height: 30px;
    box-sizing: border-box;
    color: white;
    font-size: 25px;
    line-height: 25px;
    background: #1E84F6;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
  }

  .title {
    font-size: 22px;
    line-height: 22px;
  }

  // filter
  // filter height
  .filterHeightControl {
    height: 45px;
    box-sizing: border-box;
    border-top-left-radius: 25px;
    border-bottom-left-radius: 25px;
  }

  .input-group > .input-group-append > .btn {
    border-top-right-radius: 25px;
    border-bottom-right-radius: 25px;
    background: white;

    &:hover {
      background: #1b79f4;
      color: white;
    }
  }
</style>



