<template>
  <div class="voucherdetailinfo">
    <BlockUI
      v-if="isLoading"
      :message="msg"
      :html="html"
    />
    <div class="container-fluid">
      <div class="row py-3 headerUi px-40 align-items-center">
        <router-link
          class="text-decoration-none"
          :to="'/voucherstorelistdetail/'+$route.params.id"
        >
          <div class="arrowLeftLayout d-flex align-items-center justify-content-center">
            <i class="fas fa-arrow-left text-tablebtn" />
          </div>
        </router-link>
        <p class="mb-0 ml-3 text-light">返回</p>
        <p class="mb-0 ml-auto text-light font-weight-bold">票券庫存明細</p>
      </div>
    </div>
    <!-- breadcrumb -->
    <breadcrumb
      :company="yfp_store_title"
      :voucher="yfp_ticket_name"
      :vouchernumber="yfp_ticket_content_id"
      :id="yfp_ticket_content_store_id"
    />
    <!-- 票券狀態 -->
    <div class="container-fluid voucherStatusBox">
      <div class="voucherStatus d-flex">
        <p class="title">票券狀態</p>
        <div class="voucherStatusDetail d-flex justify-content-start align-items-center">
          <p class="mb-0 dashedLine" />
          <p class="mb-0 detailContentPoint bg-log">
            <span class="status">{{yfp_ticket_content_trust_status}}</span>
            <span class="time fz14 text-center">{{time}}</span>
          </p>
        </div>
      </div>

    </div>
    <!-- 票券詳細資訊 -->
    <div class="main">
      <div class="container">
        <h4 class="font-weight-bold mb-3">票券詳細資訊</h4>
        <div class="row mx-0">
          <div class="col-sm-12 px-40 bg-white mainContent pt-40 pb-3">
            <div class="d-flex">
              <div class="col-6">
                <p class="bg-log text-white fz14 d-flex align-items-center py-1 ticketSubtitle justify-content-center">
                  票券資訊</p>
                <div class="d-flex">
                  <div class="left d-flex flex-column">
                    <span class="font-weight-bold mb-30">
                      票券訂單編號
                    </span>
                    <span class="font-weight-bold mb-30">票券編號</span>
                    <span class="font-weight-bold mb-30">票券型態</span>
                    <span class="font-weight-bold mb-30">票券條碼</span>
                    <span class="font-weight-bold">票券商品名稱</span>
                  </div>
                  <div class="right d-flex flex-column ml-70">
                    <span class="text-page mb-30">{{yfp_ticket_content_order_id}}</span>
                    <span class="text-page mb-30">{{yfp_ticket_content_id}}</span>
                    <span class="text-log mb-30 bg-loglight fz14 px-3 py-1   voucherTypeBadge">電子自銷</span>
                    <span class="text-page mb-30">{{yfp_ticket_content_sn}}</span>
                    <span class="text-page">{{yfp_ticket_name}}</span>
                  </div>
                </div>

              </div>
              <div class="col-6">
                <p class="bg-log text-white trustInfo bg-darkOrange fz14 py-1 d-flex align-items-center justify-content-center">
                  信託資訊</p>
                <div class="d-flex">
                  <div class="left d-flex flex-column">
                    <span class="font-weight-bold mb-30">
                      商品原價
                    </span>
                    <span class="font-weight-bold mb-30">信託額</span>
                    <span class="font-weight-bold mb-30">信託到期日</span>
                    <span class="d-flex justify-content-center align-items-center py-1 bg-log text-white voucherStatusChange fz14 mb-30">票券異動時間</span>
                    <span class="font-weight-bold mb-30">票券使用時間</span>
                    <span class="font-weight-bold mb-30">票券作廢時間</span>
                  </div>
                  <div class="right d-flex flex-column ml-70">
                    <span class="text-page mb-30">NT {{yfp_ticket_price | currency}}</span>
                    <span class="text-page mb-30">NT {{yfp_ticket_content_price | currency}}</span>
                    <span class="text-light bg-darkOrange fz14 px-3 py-1   trustDueDate">
                      {{yfp_ticket_content_trust_deadline}}
                    </span>
                    <span class="text-page mb-30">
                      {{yfp_ticket_content_used_time ? yfp_ticket_content_used_time : '-'}}
                    </span>
                    <span class="text-page">
                      {{yfp_ticket_content_cancel_time ? yfp_ticket_content_cancel_time : '-'}}
                    </span>
                  </div>
                </div>
              </div>
            </div>
            <div class="d-flex publisher">
              <div class="col-12 border-top pt-30">
                <p class=" text-white fz14 d-flex align-items-center py-1 ticketSubtitle justify-content-center companyInfo">
                  發行商資料</p>
                <div class="d-flex">
                  <div class="left d-flex flex-column">
                    <span class="font-weight-bold mb-30">發行商家</span>
                    <span class="font-weight-bold mb-30">發行票券廠商統編</span>
                  </div>
                  <div class="right d-flex flex-column ml-40">
                    <span class="mb-30 text-page">
                      {{yfp_store_title}}
                    </span>
                    <span class="text-log d-flex align-items-center font-weight-bold">
                      {{yfp_store_vat}}
                      <i class="fas fa-copy fa-xs" />
                    </span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="container mt-30 mb-3">
        <div class="row">
          <div class="col-6">
            <h4 class="font-weight-bold mb-3">付款詳情</h4>
            <div class="w-100 bg-white p-40 payDetail">
              <p class="bg-log text-white trustInfo bg-darkOrange fz14 py-1 d-flex align-items-center justify-content-center">
                付款資訊</p>
              <div class="d-flex">
                <div class="left">
                  <div class="d-flex flex-column">
                    <span class="font-weight-bold mb-30">豐收款交易時間</span>
                    <span class="font-weight-bold mb-30">豐收款交易編號</span>
                    <span class="font-weight-bold">豐收款交易方式</span>
                  </div>
                </div>
                <div class="right">
                  <div class="d-flex flex-column">
                    <span class="text-page mb-30">
                      {{yfp_ticket_content_purchase_time ? yfp_ticket_content_purchase_time : '-'}}
                    </span>
                    <span class="text-page mb-30">
                      {{yfp_ticket_content_tsno}}
                    </span>
                    <span class="text-page">
                      {{yfp_ticket_content_payment_type}}
                    </span>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="col-6">
            <h4 class="font-weight-bold mb-3">請款詳情</h4>
            <div class="w-100 p-40 bg-white applyDetail">
              <p class="bg-log text-white trustInfo bg-darkOrange fz14 py-1 d-flex align-items-center justify-content-center">
                請款資訊</p>
              <div class="d-flex">
                <div class="left">
                  <div class="d-flex flex-column">
                    <span class="font-weight-bold mb-30 ">請款狀態</span>
                    <span class="font-weight-bold mb-30">請款單號</span>
                    <span class="font-weight-bold mb-30">請款時間</span>
                    <span class="font-weight-bold">審核時間</span>
                  </div>
                </div>
                <div class="right">
                  <div class="d-flex flex-column">
                    <span class="text-page mb-30">
                      {{status}}
                    </span>
                    <span class="text-page mb-30">
                      {{yfp_ticket_content_request_time ? yfp_ticket_content_apply_sernum : '-'}}
                    </span>
                    <span class="text-page mb-30">
                      {{yfp_ticket_content_request_time ? yfp_ticket_content_request_time : '-'}}
                    </span>
                    <span class="text-page">
                      {{yfp_ticket_content_complete_time ? yfp_ticket_content_complete_time : '-'}}
                    </span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import breadcrumb from "./breadcrumb";
  import { mapFields } from "vuex-map-fields";
  import { mapActions, mapGetters, mapState } from "vuex";

  export default {
    components: {
      breadcrumb
    },
    data() {
      return {
        msg: "Loading",
        html: '<i class="fa fa-cog fa-spin fa-3x fa-fw"/>',
        company: "全國電子股份有限公司",
        voucher: "全國電子足感心伍佰圓商品兌換券",
        vouchernumber: "票券編號000003"
      };
    },
    methods: {
      ...mapActions("voucherDetailInfo", ["init"])
    },
    computed: {
      ...mapState("voucherDetailInfo", [
        "yfp_ticket_content_sn",
        "yfp_ticket_content_id",
        "yfp_ticket_content_ticket_id",
        "yfp_ticket_content_status",
        "yfp_ticket_content_user_id",
        "yfp_ticket_content_store_id",
        "yfp_ticket_content_purchase_time",
        "yfp_ticket_content_used_time",
        "yfp_ticket_content_request_time",
        "yfp_ticket_content_cancel_time",
        "yfp_ticket_content_complete_time",
        "yfp_ticket_content_price",
        "yfp_ticket_content_trust_deadline",
        "yfp_ticket_content_trust_status",
        "yfp_ticket_content_payment_type",
        "yfp_ticket_content_order_id",
        "yfp_ticket_content_tsno",
        "yfp_ticket_content_apply_sernum",
        "yfp_ticket_content_branch_id",
        "yfp_ticket_content_invoice_number",
        "yfp_ticket_content_random_code",
        "yfp_ticket_content_staff",
        "yfp_ticket_content_invoice_index",
        "yfp_ticket_name",
        "yfp_store_title",
        "yfp_ticket_price",
        "yfp_store_vat",
        "status",
        "time"
      ]),
      ...mapFields("voucherDetailInfo", ["id"]),
      ...mapState("ui", ["isLoading"])
    },
    mounted() {
      this.id = this.$route.params.ticketId;
      this.init();
    }
  };
</script>

<style lang="scss">
  .voucherdetailinfo {
    .headerUi {
      background: transparent
        linear-gradient(89deg, #1b79f4 0%, #27489a 53%, #3b44a9 100%) 0% 0%
        no-repeat padding-box;
    }

    .arrowLeftLayout {
      width: 33px;
      height: 33px;
      box-sizing: border-box;
      background: rgba(256, 256, 256, 0.4);
      border-radius: 50%;
    }

    .fz14 {
      font-size: 14px;
      line-height: 14px;
    }

    // 票券狀態
    .voucherStatusBox {
      padding: 50px 0;
      background: #efecec;
      box-shadow: 0px 3px 6px #000029;
      border-bottom: 1px solid #dfdcdc;
      box-sizing: border-box;
    }

    .voucherStatus {
      width: 634px;
      height: 128px;
      box-sizing: border-box;
      margin: 0 auto;
      border-radius: 64px;
      border: 2px solid #3b579d;

      .title {
        margin-bottom: 0;
        width: 254px;
        border-radius: 64px 0 0 64px;
        color: white;
        font-weight: bold;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 20px;
        background: transparent
          linear-gradient(
            132deg,
            #1b79f4 0%,
            #1d70e3 5%,
            #2551aa 50%,
            #27489a 63%,
            #3b44a9 100%
          )
          0% 0% no-repeat padding-box;
      }
    }

    .voucherStatusDetail {
      background: white;
      width: 380px;
      height: 124px;
      border-radius: 0 64px 64px 0;

      .dashedLine {
        width: 173px;
        border-top: 2px dashed #c1c1c1;
      }

      .detailContentPoint {
        width: 15px;
        height: 15px;
        background: #1b79f4;
        border-radius: 50%;
        position: relative;

        span.status {
          position: absolute;
          top: -23px;
          left: -20px;
          color: #555151;
          font-weight: normal;
          font-size: 16px;
          width: 48px;
        }

        span.time {
          position: absolute;
          color: #838080;
          font-size: 14px;
          width: 202px;
          font-weight: normal;
          bottom: -25px;
          left: -90px;
        }
      }
    }

    //  票券詳細資訊
    .main {
      padding-top: 40px;
      padding-bottom: 40px;
      background: #eaf1fa;

      .mainContent {
        border: 2px solid #efecec;
        box-sizing: border-box;
        border-radius: 3px;
      }

      .ticketSubtitle,
      .trustInfo {
        width: 115px;
        height: 29px;
        border-radius: 3px;
        box-sizing: border-box;
      }

      .ticketSubtitle,
      .trustInfo {
        font-weight: bold;
      }

      .voucherTypeBadge {
        width: 90px;
        font-weight: bold;
        border-radius: 3px;
      }

      .trustDueDate {
        width: 105px;
        margin-bottom: 89px;
        border-radius: 3px;
        font-weight: bold;
      }

      .voucherStatusChange {
        border-radius: 3px;
        width: 115px;
        height: 29px;
        box-sizing: border-box;
        font-weight: bold;
      }

      .companyInfo {
        background: #6e65de;
        border-radius: 3px;
      }

      // copy icon
      .fa-copy {
        width: 20px;
        height: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        background: rgba(193, 193, 193, 0.5);
        border-radius: 4px;
        margin-left: 24px;
        cursor: pointer;

        &:hover {
          background: rgba(193, 193, 193, 0.8);
        }
      }

      // 付款詳情、請款詳情
      .payDetail {
        height: 300px;
        box-sizing: border-box;
        border: 2px solid #efecec;
        box-sizing: border-box;
        border-radius: 3px;

        .trustInfo {
          margin-left: 15px;
        }

        .left {
          padding-left: 15px;
          box-sizing: border-box;
          margin-right: 56px;
        }
      }

      .applyDetail {
        height: 300px;
        border: 2px solid #efecec;
        box-sizing: border-box;
        border-radius: 3px;

        .left {
          margin-right: 100px;
        }
      }
    }
  }
</style>



