const getters = {}

const state = {
  searchText: '',
  searchStartDate: '',
  searchEndDate: '',
  searchSelect: '',
}

const actions = {
  startSearch({commit, dispatch}) {
    commit('currentPage', {page: 1})
    dispatch('getList')
  },
  setSearchText({commit, state, dispatch}, {searchText}) {
    commit('searchText', {searchText: searchText.trim()})
  },
  setSearchStartDate({commit, state, dispatch}, {searchStartDate}) {
    commit('searchStartDate', {searchStartDate: searchStartDate.trim()})
  },
  setSearchEndDate({commit, state, dispatch}, {searchEndDate}) {
    commit('searchEndDate', {searchEndDate: searchEndDate.trim()})
  },
  setSearchSelect({commit, state, dispatch}, {searchSelect}) {
    commit('searchSelect', {searchSelect: searchSelect.trim()})
  },
}

const mutations = {
  searchText(state, {searchText}) {
    state.searchText = searchText
  },
  searchStartDate(state, {searchStartDate}) {
    state.searchStartDate = searchStartDate
  },
  searchEndDate(state, {searchEndDate}) {
    state.searchEndDate = searchEndDate
  },
  searchSelect(state, {searchSelect}) {
    state.searchSelect = searchSelect
  },

}

export default {
  actions,
  state,
  mutations,
  getters,
}



