<template>
  <!-- breadcrumb -->
  <nav
    aria-label="breadcrumb"
    class=""
  >
    <ol class="breadcrumb px-40 bg-white fz14 mb-0">
      <router-link
        class="breadcrumb-item px-30 text-decoration-none text-log font-weight-bold align-items-center ml-2 text-decoration-none"
        to="/admin"
      >票券庫存總表</router-link>
      <!-- {{company}} -->
      <router-link
        class="breadcrumb-item px-30 text-log font-weight-bold align-items-center ml-2 text-decoration-none"
        :to="'/voucherstorelistdetail/'+id"
      >{{voucher}}</router-link>
      <li class="
        breadcrumb-item
        text-log
        font-weight-bold
        align-items-center
        px-30
        ml-2
        last">{{vouchernumber}}</li>
    </ol>
  </nav>
</template>

<script>
  export default {
    props: ["company", "voucher", "vouchernumber", "id"]
  };
</script>

<style lang="scss">
  // bread crumb
  .breadcrumb-item + .breadcrumb-item::before {
    content: "";
  }
  .fz14 {
    font-size: 14px;
  }
  .breadcrumb-item {
    background: #eaf1fa;
    box-shadow: 0px 0px 6px #00000029;
    border-radius: 39px;
    height: 20px;
    box-sizing: border-box;
    display: flex;
  }
  .breadcrumb .breadcrumb-item:not(.last) {
    &::after {
      content: ">";
      margin-left: 30px;
      font-size: 14px;
      color: #707070;
    }
  }
</style>


