import api from '../api'
import pagination from './common/pagination'
import {getField, updateField} from 'vuex-map-fields';
import helper from './common/helper'
import router from "../router";
import ajax from "axios";

export const getters = Object.assign({getField}, pagination.getters, helper.getters)

export const state = Object.assign({
  id: 0,
  yfp_store_request_created_at: '',
  yfp_store_request_store_id: '',
  yfp_store_request_price: '',
  yfp_store_request_status: '',
  yfp_store_request_message: '',
  yfp_store_request_name: '',
  yfp_store_request_number: '',
  yfp_store_request_check_time: '',
  yfp_store_request_sernum: '',
  yfp_store_request_auditors: '',
  yfp_store_title: '',
  count: 0
}, pagination.state, helper.state)

export const actions = Object.assign({
  pass({commit, state}) {
    commit('ui/loading', {isLoading: true}, {root: true})
    api.shopAppropriateList.pass({
      id: state.id,
    }).then(() => {
      router.push('/admin/shopAppropriateReview')
    }).catch((error) => {
      commit('ui/ajaxModalError', {error: error}, {root: true})
    }).finally(() => {
      commit('ui/loading', {isLoading: false}, {root: true})
    })
  },
  reject({commit, state}) {
    commit('ui/loading', {isLoading: true}, {root: true})
    api.shopAppropriateList.reject({
      id: state.id,
      message: state.yfp_store_request_message
    }).then((rep) => {
      router.push('/admin/shopAppropriateReview')
    }).catch((error) => {
      commit('ui/ajaxModalError', {error: error}, {root: true})
    }).finally(() => {
      commit('ui/loading', {isLoading: false}, {root: true})
    })
  },
  getList({commit, state}) {
    commit('ui/loading', {isLoading: true}, {root: true})
    api.shopAppropriateList.list({
      id: state.id,
    }).then((rep) => {
      console.log(rep.data.body.data)
      commit('paginationRows', {rows: rep.data.body.list})
      commit('setValue', {
        key: 'yfp_store_request_auditors',
        val: rep.data.body.data.yfp_store_request_auditors
      })
      commit('setValue', {
        key: 'count',
        val: rep.data.body.data.count
      })
      commit('setValue', {
        key: 'yfp_store_title',
        val: rep.data.body.data.yfp_store_title
      })
      commit('setValue', {
        key: 'yfp_store_request_created_at',
        val: rep.data.body.data.yfp_store_request_created_at
      })
      commit('setValue', {
        key: 'yfp_store_request_store_id',
        val: rep.data.body.data.yfp_store_request_store_id
      })
      commit('setValue', {
        key: 'yfp_store_request_price',
        val: rep.data.body.data.yfp_store_request_price
      })
      commit('setValue', {
        key: 'yfp_store_request_status',
        val: rep.data.body.data.yfp_store_request_status
      })
      commit('setValue', {
        key: 'yfp_store_request_message',
        val: rep.data.body.data.yfp_store_request_message
      })
      commit('setValue', {
        key: 'yfp_store_request_name',
        val: rep.data.body.data.yfp_store_request_name
      })
      commit('setValue', {
        key: 'yfp_store_request_number',
        val: rep.data.body.data.yfp_store_request_number
      })
      commit('setValue', {
        key: 'yfp_store_request_check_time',
        val: rep.data.body.data.yfp_store_request_check_time
      })
      commit('setValue', {
        key: 'yfp_store_request_sernum',
        val: rep.data.body.data.yfp_store_request_sernum
      })
    }).catch((error) => {
      commit('ui/ajaxError', {error: error}, {root: true})
    }).finally(() => {
      commit('ui/loading', {isLoading: false}, {root: true})
    })
  },
  exportExcel({commit, state}) {
    commit('ui/loading', {isLoading: true}, {root: true})
    ajax.post(process.env.API_URL + 'appropriate_list/excel', {
      id: state.id,
    }, {
      headers: {
        'Authorization': 'Bearer ' + window.localStorage.getItem('jwt')
      }
    }).then((rep) => {
      let link = window.document.createElement("a");
      link.href = rep.data;
      link.download = "Excel.xls";
      link.click();
    }).catch((error) => {
      commit('ui/ajaxError', {error: error}, {root: true})
    }).finally(() => {
      commit('ui/loading', {isLoading: false}, {root: true})
    })
  },
}, pagination.actions, helper.actions);

export const mutations = Object.assign({updateField}, pagination.mutations, helper.mutations)



