<template>
  <div class="voucherstoresearch">
    <div class="searchBar">
      <searchbar
        :setSearchEndDate="setSearchEndDate"
        :setSearchStartDate="setSearchStartDate"
        :searchEndDate="searchEndDate"
        :searchStartDate="searchStartDate"
        :searchText="searchText"
        :startSearch="startSearch"
        :setSearchText="setSearchText"
        :duration="duration"
        :setSearchSelect="setSearchSelect"
        :searchSelect="searchSelect"
      />
    </div>
    <div class="container-fluid detailContent bg-white pt-0">
      <div class="row px-40">
        <p class="titleFontDetail text-primary font-weight-bold mb-3 mt-3 ml-2">
          票券庫存總表
        </p>
        <div class="table-responsive px-2  table-fixed">
          <table class="table border-bottom table-striped table-hover">
            <thead>
            <tr class="bg-thead text-light w-100 headTop">
              <!-- <td
                class="border-top-0 h64 up"
              ></td> -->
              <td
                rowspan="2"
                class="fz18 text-center border-top-0 h64 font-weight-bold up"
                width="150"
              >
                商家名稱
              </td>
              <td
                rowspan="2"
                class="fz18 text-center border-top-0 h64 font-weight-bold up"
              >
                票券信託總張數（張）
              </td>
              <td
                rowspan="2"
                class="fz18 text-center border-top-0 h64 font-weight-bold up"
              >
                票券信託總金額
              </td>
              <td
                colspan="2"
                class="fz14 text-center border-top-0 h64 font-weight-bold up5 py-0"
              >
                <p class="mb-0 w-100 h32  border-left">未使用</p>
                <div class="row ml-0">
                  <p class="mb-0 h32 headBottom  border-left pr-2">張數</p>
                  <p class="mb-0 h32 headBottom">金額</p>
                </div>
              </td>
              <td
                colspan="2"
                class="fz14 text-center border-top-0 h32 font-weight-bold up5  py-0"
              >
                <p class="mb-0 w-100 h32">已使用</p>
                <div class="row">
                  <p class="mb-0 h32 headBottom">張數</p>
                  <p class="mb-0 h32 headBottom">金額</p>
                </div>
              </td>
              <td
                colspan="2"
                class="fz14 py-0 text-center border-top-0  h32 font-weight-bold up5 pr-0 last"
              >
                <p class="mb-0 w-100 h32 rounded-right">已作廢</p>
                <div class="row mr-0">
                  <p class="mb-0 h32 headBottom">張數</p>
                  <p class="mb-0 h32 headBottom">金額</p>
                </div>
              </td>
            </tr>
            </thead>
            <tbody class="bg-light">
            <!-- 商家 -->
            <tr
              v-for="(item,index) in listRows"
              :key="index"
            >
              <!-- <td class="text-center py-2">
              </td> -->
              <td class="text-center py-2">
                {{item['name']}}
              </td>
              <td class="text-center py-2 text-tablebtn font-weight-bold">
                <router-link
                  class="text-decoration-none"
                  :to="'/voucherstorelistdetail/'+item['id']"
                >{{item['count']}}
                </router-link>
              </td>
              <td class="text-center py-2">
                {{item['total']}}
              </td>
              <td class="text-center py-2 text-tablebtn pr-0">
                {{item['unusedCount']}}
              </td>
              <td class="text-center py-2 pr-0">
                {{item['unused']}}
              </td>
              <td class="text-center py-2 text-tablebtn">
                {{item['useCount']}}
              </td>
              <td class="text-center py-2">
                {{item['use']}}
              </td>
              <td class="text-center py-2 text-tablebtn">
                {{item['cancelCount']}}
              </td>
              <td class="text-center py-2">
                {{item['cancel']}}
              </td>
            </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>

    <!-- footer -->
    <!-- <footer class="container-fluid fixed-bottom bg-light">
      <pagination
        :perPage="perPage"
        :totalPage="totalPage"
        :currentPage="currentPage"
        :totalCount="totalCount"
        :pages="pages"
        :setCurrentPage="setCurrentPage"
        :prevOrNext="prevOrNext"
        :getList="getList"
        :startPage="startPage"
        :endPage="endPage"
        :setPerPage="setPerPage"
      />
    </footer> -->
  </div>
</template>

<script>
  import searchbar from "./searchbar";
  import pagination from "./pagination";
  import {mapGetters, mapActions} from "vuex";
  import {mapFields} from "vuex-map-fields";

  export default {
    components: {
      searchbar,
      pagination
    },
    data() {
      return {
        duration: "票券發行區間"
      };
    },
    computed: {
      ...mapFields("stores", [
        "perPage",
        "totalPage",
        "currentPage",
        "totalCount",
        "listRows",
        "searchText",
        "searchStartDate",
        "searchEndDate",
        "searchSelect"
      ]),
      ...mapGetters("stores", ["pages", "startPage", "endPage"])
    },
    methods: {
      ...mapActions("stores", [
        "setCurrentPage",
        "prevOrNext",
        "getList",
        "setPerPage",
        "setSearchText",
        "setSearchStartDate",
        "setSearchEndDate",
        "startSearch",
        "setSearchSelect"
      ])
    },
    mounted() {
      this.searchText = ''
      this.searchStartDate = ''
      this.searchEndDate = ''
      this.setCurrentPage({page: 1})
      this.getList()
      // console.log(this.listRows);
    }
  };
</script>

<style lang="scss" scoped>
  .voucherstoresearch .searchBar {
    .form-control::placeholder {
      color: #c1c1c1;
    }
  }

  .voucherstoresearch {
    .searchBar {
      padding: 24px 52px;
      background: #f7f7f7;
      box-sizing: border-box;
      margin-top: 2px;
    }
  }

  .fz14 {
    font-size: 14px;
  }

  .fz18 {
    font-size: 18px;
  }

  .detailContent {
    padding-top: 16px;
    // height: calc(100vh - 60px - 57px - 93px);
    overflow-y: scroll;
    // box-shadow: 3px 0px 6px #efecec;
    // border: 1px solid #efecec;
    &::-webkit-scrollbar {
      display: none;
    }

    .titleFontDetail {
      font-size: 22px;
    }
  }

  .table {
    thead {
      display: block;
      width: 100%;

      .headTop {
        width: 100%;
        display: flex;
        border-radius: 5px 5px 0 0;
      }

      .up {
        display: flex;
        justify-content: center;
        align-items: center;
        width: calc(100% / 9);
      }

      .up5 {
        // display: flex;
        justify-content: center;
        align-items: center;
        width: calc(100% / 4);

        p {
          box-sizing: border-box;
          display: flex;
          justify-content: center;
          align-items: center;
        }
      }

      .headBottom {
        background: #7687b4;
        width: 50%;
      }
    }

    tbody {
      // display: block;
      // overflow: auto;
      // height: 500px;
      // width: 100%;
      &::-webkit-scrollbar {
        display: none;
      }

      tr {
        width: 100%;
        display: flex;
      }

      td {
        display: flex;
        align-items: center;
        justify-content: center;
        width: calc(100% / 9);
        box-sizing: border-box;
        height: 65px;
      }
    }

    .h64 {
      height: 64px;
    }

    .h32 {
      height: 32px;
    }

    .h76 {
      height: 76px;
      box-sizing: border-box;
    }

    td {
      vertical-align: middle;
      padding-top: 2px;
      padding-bottom: 2px;
      font-size: 14px;
    }
  }

  // footer
  // .footer {
  //   height: 50px;
  //   box-sizing: border-box;

  //   .form-control {
  //     width: 80px;
  //     height: 30px;
  //     font-size: 14px;
  //   }
  // }
</style>



