<template>
  <div class="shopAppropriateList">
    <BlockUI
      v-if="isLoading"
      :message="msg"
      :html="html"
    />
    <div class="container-fluid">
      <div class="row align-items-center px-40 header pt-3">
        <router-link
          class="text-decoration-none"
          to="/admin/shopAppropriateReview"
        >
          <div class="arrowLeftLayout d-flex align-items-center justify-content-center">
            <i class="fas fa-arrow-left text-tablebtn"></i>
          </div>
        </router-link>
        <p class="mb-0 ml-3 text-light font-weight-bold">返回</p>
        <span class="text-light font-weight-bold ml-auto ">商家撥款明細</span>
      </div>
    </div>
    <!-- top table -->
    <div class="container-fluid py-40 topTableBox">
      <div class="row px-40 mx-0">
        <div class="table-responsive topTable pr-2">
          <table class="table border-bottommb-0">
            <thead class="d-block w-100">
              <tr class="d-flex w-100">
                <th class="text-center text-white bg-thead border-top-0 fz14 d-flex justify-content-center align-items-center border-bottom-0">商家名稱</th>
                <th class="text-center text-white bg-thead border-top-0 fz14 d-flex justify-content-center align-items-center border-bottom-0">提出請款時間</th>
                <th class="text-center text-white bg-thead border-top-0 fz14 d-flex justify-content-center align-items-center border-bottom-0">請款審核時間</th>
                <th class="text-center text-white bg-thead border-top-0 fz14 d-flex justify-content-center align-items-center border-bottom-0">請款單號</th>
                <th class="text-center text-white bg-thead border-top-0 fz14 d-flex justify-content-center align-items-center border-bottom-0">請款總張數</th>
                <th class="text-center text-white bg-thead border-top-0 fz14 d-flex justify-content-center align-items-center border-bottom-0">請款總金額</th>
                <th class="text-center text-white bg-thead border-top-0 fz14 d-flex justify-content-center align-items-center border-bottom-0">撥款狀態</th>
                <th class="text-center text-white bg-thead border-top-0 fz14 d-flex justify-content-center align-items-center border-bottom-0">審核人員</th>
              </tr>
            </thead>
            <tbody class="d-block w-100">
              <tr class="w-100 d-flex">
                <td class="text-center fz14 border-top-0">{{yfp_store_title}}</td>
                <td class="text-center fz14 border-top-0">{{yfp_store_request_created_at}}</td>
                <td class="text-center fz14 border-top-0">
                  {{yfp_store_request_check_time ? yfp_store_request_check_time : '-'}}
                </td>
                <td class="text-center fz14 border-top-0">{{yfp_store_request_sernum}}</td>
                <td class="text-center fz14 border-top-0">{{count}}</td>
                <td class="text-center fz14 border-top-0">{{yfp_store_request_price}}</td>
                <!-- <td
                  class="text-center fz14 border-top-0"
                  :class="{'text-headSaveBtn' : yfp_store_request_status === '待審核','text-undo' : yfp_store_request_status === '已審核','rejected':yfp_store_request_status === '退回'}"
                >{{yfp_store_request_status}}
                </td> -->
                <td class="text-center fz14 border-top-0" v-if="yfp_store_request_status==='退回'">
                  已駁回
                </td>
                <td class="text-center fz14 border-top-0" v-else>
                  {{yfp_store_request_status}}
                </td>
                <td class="text-center fz14 border-top-0">
                  {{yfp_store_request_auditors}}
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
    <!-- detail table -->
    <div class="container-fluid detailContent px-0 pt-0">
      <div class="row align-items-center justify-content-between px-40 mx-0">
        <p class="titleFontDetail text-primary font-weight-bold mb-3 mt-3 ml-2">商家撥款明細列表</p>
        <button @click="exportExcel" class="btn btn-success d-flex align-items-center py-0 justify-content-center mr-2 excel">
          <img
            src="../../assets/img/xls.png"
            class="mr-3"
            alt=""
          >
          <span class="font-weight-bold fz14">匯出 EXCEL</span>
        </button>
      </div>
      <div class="row px-40 mx-0">
        <div class="table-responsive px-2">
          <table class="table border-bottom table-striped table-hover">
            <thead class="thead d-block w-100">
              <tr class="text-light d-flex w-100">
                <th class="text-center bg-tablehead fz14 w-20 d-flex align-items-center justify-content-center border-bottom-0">序號</th>
                <th class="text-center bg-tablehead fz14  w-20  d-flex align-items-center justify-content-center border-bottom-0">票券編號</th>
                <th class="text-center bg-tablehead fz14  w-20  d-flex align-items-center justify-content-center border-bottom-0">票券條碼</th>
                <th class="text-center bg-tablehead fz14  w-20  d-flex align-items-center justify-content-center border-bottom-0">商品名稱</th>
                <th class="text-center bg-tablehead fz14  w-20  d-flex align-items-center justify-content-center border-bottom-0">票券信託額</th>
              </tr>
            </thead>
            <tbody class="d-block w-100 border-left border-right">
              <tr
                v-for="(item,index) in listRows.slice().reverse()"
                :key="index"
                class="d-flex w-100 border-bottom"
              >
                <td class="text-center bg-white fz14 w-20  d-flex align-items-center justify-content-center border-top-0">
                  {{(index + 1)+(currentPage-1)*perPage}}
                </td>
                <td class="text-center bg-white fz14 w-20  d-flex align-items-center justify-content-center border-top-0">{{item[0]}}</td>
                <td class="text-center bg-white fz14 w-20  d-flex align-items-center justify-content-center border-top-0">{{item[1]}}</td>
                <td class="text-center bg-white fz14 w-20  d-flex align-items-center justify-content-center border-top-0">{{item[2]}}</td>
                <td class="text-center bg-white fz14 text-danger w-20  d-flex align-items-center justify-content-center border-top-0">NT {{item[3] }}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <div
        v-if="yfp_store_request_status === '待審核'"
        class="row px-40 mx-0 justify-content-end"
      >
        <button
          class="btn btn-darkOrange p-0 reviewed"
          data-toggle="modal"
          data-target="#reviewedModal"
        >審核
        </button>
        <button
          class="btn btn-primary p-0 reject mr-2"
          data-toggle="modal"
          data-target="#rejectModal"
        >駁回
        </button>
      </div>
    </div>

    <!-- footer -->
    <footer class="container-fluid fixed-bottom bg-light">
      <pagination
        :perPage="perPage"
        :totalPage="totalPage"
        :currentPage="currentPage"
        :totalCount="totalCount"
        :pages="pages"
        :setCurrentPage="setCurrentPage"
        :prevOrNext="prevOrNext"
        :getList="getList"
        :startPage="startPage"
        :endPage="endPage"
        :setPerPage="setPerPage"
      />
    </footer>

    <!-- Reviewed Modal -->
    <div
      class="modal fade"
      id="reviewedModal"
    >
      <div
        class="modal-dialog"
        role="document"
      >
        <div class="modal-content">
          <div class="modal-header justify-content-center">
            <h5
              class="modal-title"
              id="reviewedModalTitle"
            >您將撥款給商家</h5>
          </div>
          <div class="modal-body d-flex align-items-center justify-content-center">
            <p class="mb-0 d-flex justify-content-center">確定要撥款 <span class="font-weight-bold ml-2">請款單號 {{yfp_store_request_sernum}}</span>
              <span class="text-danger mx-2 font-weight-bold">{{yfp_store_request_price}}</span> 嗎？</p>
          </div>
          <div class="modal-footer d-flex justify-content-around">
            <button
              type="button"
              class="btn btn-outline-log"
              data-dismiss="modal"
            >先不要
            </button>
            <button
              @click="pass"
              type="button"
              class="btn btn-log"
              data-dismiss="modal"
            >確定
            </button>
          </div>
        </div>
      </div>
    </div>
    <!-- reject Modal -->
    <div
      class="modal fade"
      id="rejectModal"
    >
      <div
        class="modal-dialog"
        role="document"
      >
        <div class="modal-content">
          <div class="modal-header justify-content-center">
            <h5
              class="modal-title "
              id="rejectModalTitle"
            >請填寫駁回原因</h5>
          </div>
          <div class="modal-body px-40">
            <label for="reason">原因</label>
            <textarea
              v-model="yfp_store_request_message"
              class="form-control"
              id="reason"
              cols="30"
              rows="10"
            />
            </div>
          <div class="modal-footer d-flex justify-content-around">
            <button
              type="button"
              class="btn btn-outline-log"
              data-dismiss="modal"
            >取消
            </button>
            <button
              @click="reject"
              type="button"
              class="btn btn-log"
              data-dismiss="modal"
            >送出
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { mapActions, mapGetters, mapState } from "vuex";
  import { mapFields } from "vuex-map-fields";
  import pagination from "./pagination";

  export default {
    components: {
      pagination
    },
    data() {
      return {
        msg: "Loading",
        html: '<i class="fa fa-cog fa-spin fa-3x fa-fw"/>',
        status: "待審核"
      };
    },
    computed: {
      ...mapState("shopAppropriateList", [
        "perPage",
        "totalPage",
        "currentPage",
        "totalCount",
        "listRows",
        "yfp_store_request_created_at",
        "yfp_store_request_store_id",
        "yfp_store_request_price",
        "yfp_store_request_status",
        "yfp_store_request_name",
        "yfp_store_request_number",
        "yfp_store_request_check_time",
        "yfp_store_request_sernum",
        "yfp_store_title",
        "yfp_store_request_auditors",
        "count"
      ]),
      ...mapGetters("shopAppropriateList", ["pages", "startPage", "endPage"]),
      ...mapFields("shopAppropriateList", ["id", "yfp_store_request_message"]),
      ...mapState("ui", ["isLoading"])
    },
    methods: {
      ...mapActions("shopAppropriateList", [
        "setCurrentPage",
        "prevOrNext",
        "getList",
        "setPerPage",
        "pass",
        "reject",
        "exportExcel"
      ])
    },
    mounted() {
      this.id = this.$route.params.id;
      this.getList();
    }
  };
</script>

<style lang="scss">
  .shopAppropriateList {
    .header {
      background: transparent
        linear-gradient(89deg, #1b79f4 0%, #27489a 53%, #3b44a9 100%) 0% 0%
        no-repeat padding-box;
      box-shadow: 0px 2px 4px #00000029;
      padding-bottom: 13px;
    }

    .arrowLeftLayout {
      width: 33px;
      height: 33px;
      box-sizing: border-box;
      background: rgba(256, 256, 256, 0.4);
      border-radius: 50%;
    }

    .fz14 {
      font-size: 14px;
      line-height: 14px;
    }

    // topTable
    .topTableBox {
      padding-top: 0;
      padding-bottom: 0;
      background: #f7f7f7;

      .table {
        th {
          padding: 0px;
          height: 45px;
          vertical-align: middle;
          width: 12.5%;
        }
        tbody {
          height: 76px;
        }
        td {
          padding: 0;
          vertical-align: middle;
          height: 76px;
          width: 12.5%;
          display: flex;
          justify-content: center;
          align-items: center;
        }

        th:first-child {
          border-radius: 5px 0 0 0;
        }

        th:last-child {
          border-radius: 0 5px 0 0;
        }

        td {
          &.rejected {
            color: #0f4690;
          }
        }
      }
    }

    .topTable {
      tbody {
        background: white;
      }
    }

    // detail table
    .detailContent {
      background: white;
      height: calc(100vh - 62px - 50px - 202px);
      overflow-y: scroll;
      // padding-bottom: 50px;

      .titleFontDetail {
        font-size: 22px;
      }

      .excel {
        height: 30px;
        width: 170px;
        box-sizing: border-box;
        border-radius: 3px;
        font-size: 13px;

        img {
          width: 20px;
          height: auto;
        }
      }

      tr {
        th {
          border-top: 0px;
        }

        th:first-child {
          border-radius: 5px 0 0 0;
        }

        th:last-child {
          border-radius: 0 5px 0 0;
        }
      }

      .table {
        th {
          height: 45px;
          padding: 0;
          vertical-align: middle;
          box-sizing: border-box;
        }
        tbody {
          display: block;
          overflow: auto;
          height: 400px;
          &::-webkit-scrollbar {
            display: none;
          }
        }
        td {
          height: 50px;
          padding: 0;
          vertical-align: middle;
        }
      }
    }

    // 下方兩個按鈕
    .reviewed,
    .reject {
      width: 100px;
      height: 45px;
      box-sizing: border-box;
      box-shadow: 0px 0px 6px #00000029;
      border-radius: 3px;
      margin-right: 28px;
    }

    // footer
    .footer {
      height: 50px;
      box-sizing: border-box;

      .form-control {
        width: 80px;
        height: 30px;
        font-size: 14px;
      }
    }

    // modal
    #reviewedModal {
      .modal-content {
        border-radius: 20px;
        box-shadow: 0px 0px 10px #00000029;
      }

      .modal-title {
        color: #555151;
        font-weight: bold;
      }

      .modal-dialog {
        max-width: 678px;
        height: 316px;
        box-sizing: border-box;
        border-radius: 20px;
        margin: 150px auto;
      }

      .modal-body {
        height: 150px;
      }

      .modal-footer {
        .btn {
          width: 100px;
          height: 40px;
          border-radius: 22px;
        }
      }
    }

    #rejectModal {
      .modal-content {
        border-radius: 20px;
        box-shadow: 0px 0px 10px #00000029;
      }

      .modal-title {
        color: #555151;
        font-weight: bold;
      }

      .modal-dialog {
        max-width: 562px;
        height: 316px;
        box-sizing: border-box;

        border-radius: 20px;
        margin: 150px auto;
      }

      .modal-body {
        label {
          color: #555151;
          font-weight: bold;
        }
      }

      .modal-footer {
        .btn {
          width: 100px;
          height: 40px;
          border-radius: 22px;
        }
      }
    }
  }
</style>



