<template>
  <div class="admin">
    <BlockUI v-if="isLoading" :message="msg" :html="html"/>
    <headerTop class="sticky-top" :tabActive="tabActive"/>
    <router-view/>
  </div>
</template>

<script>
  import headerTop from "./component/header";
  import {mapState} from "vuex";

  export default {
    components: {
      headerTop
    },
    data() {
      return {
        msg: "Loading",
        html: '<i class="fa fa-cog fa-spin fa-3x fa-fw"/>',
        tabActive : '票券庫存'
      };
    },
    computed: {
      ...mapState("ui", ["isLoading"])
    },
    created () {
      const vm = this;
      const path = this.$route.path;
      if(path === '/admin') {
        // vm.tabActice = '票券庫存'
      }else if(path.match(/merchantdatabuild/g)) {
        vm.tabActive = '商家資料建立與總表'
      }else if(path.match(/shopAppropriateReview/g)){
        vm.tabActive = '商家撥款審核'
      }else if(path.match(/buildcharacter/g)){
        vm.tabActive = '角色建立'
      }else if(path.match(/adminuser/g)){
        vm.tabActive = '管理使用者'
      }else if(path.match(/productsearch/g) || path.match(/productlist/g)){
        vm.tabActive = '信託票券商品查詢'
      }
      
    }
  };
</script>

<style lang="scss">
  .form-control::placeholder {
    color: #c1c1c1;
  }
</style>



