<template>
  <div class="font-weight-bold addNewTicketPage px-5">
    <!-- review Modal -->
    <div
      v-if="method.indexOf('review') >= 0 "
      class="modal fade"
      id="review"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div
        class="modal-dialog"
        role="document"
      >
        <div class="modal-content">
          <div class="modal-header  justify-content-center">
            <h5
              class="modal-title text-danger font-weight-bold"
              id="exampleModalLabel"
            >注意!</h5>
          </div>
          <div class="modal-body text-center">
            按「審核」後， 此商品將立即在Cardo App正式販售。 您確定要審核（上架）嗎？
          </div>
          <div class="modal-footer d-flex justify-content-around">
            <button
              type="button"
              class="btn btn-outline-secondary"
              data-dismiss="modal"
            >取消
            </button>
            <button
              @click.prevent="pass"
              type="button"
              class="btn btn-outline-log"
              data-dismiss="modal"
            >確定
            </button>
          </div>
        </div>
      </div>
    </div>
    <!-- reject Modal -->
    <div
      v-if="method.indexOf('review') >= 0 "
      class="modal fade"
      id="reject"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div
        class="modal-dialog"
        role="document"
      >
        <div class="modal-content">
          <div class="modal-header  justify-content-center">
            <h5
              class="modal-title text-danger font-weight-bold"
              id="exampleModalLabel"
            >注意!</h5>
          </div>
          <div class="modal-body text-center">
            您確定要駁回嗎？
          </div>
          <div class="modal-footer d-flex justify-content-around">
            <button
              type="button"
              class="btn btn-outline-secondary"
              data-dismiss="modal"
            >取消
            </button>
            <button
              @click.prevent="reject"
              type="button"
              class="btn btn-outline-log"
              data-dismiss="modal"
            >確定
            </button>
          </div>
        </div>
      </div>
    </div>
    <!-- <span
      v-text="id ?  '(信託)票券商品' : '新增 (信託)票券商品'"
      class="rightContentDetailTitle d-block text-primary"
    >
    </span> -->
    <div
      v-if="method.indexOf('view') === -1"
      class="d-flex align-items-center justify-content-between"
    >
      <button
        @click="post"
        class="btn btn-success btn-save d-flex align-items-center mr-3 font-weight-bold py-0"
      >
        <img
          src="../../assets/img/save.png"
          alt=""
          class="mr-3"
        >
        <span class="text-white saveBtnSpan">儲存</span>
      </button>
      <span
        v-if="lastUpdate"
        class="fzProductName"
      >最後修改時間：{{lastUpdate}}</span>
    </div>
    <div
      v-if="method.indexOf('review') >= 0 "
      class="d-flex align-items-center"
    >
      <button
        class="btn btn-success btn-save d-flex align-items-center mr-3 font-weight-bold"
        data-toggle="modal"
        data-target="#review"
      >
        <img
          src="../../assets/img/icon_checked.png"
          alt=""
          class="mr-3"
        >
        審核
      </button>
      <button
        class="btn btn-danger btn-finish d-flex align-items-center mr-auto font-weight-bold"
        data-toggle="modal"
        data-target="#reject"
      >
        <img
          src="../../assets/img/reject.png"
          alt=""
          class="mr-3"
        >
        駁回
      </button>
      <span class="">最後修改時間：{{lastUpdate}}</span>
    </div>

    <!-- 標籤內容 -->
    <div
      v-if="errMsg"
      class="alert"
    >
      <span
        class="closebtn"
        @click="resetErrMsg"
      >&times;</span>
      <div v-html="errMsg"></div>
    </div>
    <ul class="nav nav-tabs">
      <li class="nav-item text-primary">
        <a
          class="nav-link text-primary prodInform"
          :class="{'active' : tab === '票券商品資訊'}"
          @click="tab = '票券商品資訊'"
          href="#home"
          data-toggle="tab"
        >票券商品資訊</a>
      </li>
      <li class="nav-item">
        <a
          class="nav-link text-primary trustInfo"
          :class="{'active' : tab === '票券商品信託資訊'}"
          @click="tab = '票券商品信託資訊'"
          data-toggle="tab"
          href="#profile"
        >票券商品信託資訊</a>
      </li>
      <li class="nav-item">
        <a
          class="nav-link text-primary returnNotice"
          :class="{'active' : tab === '退貨注意事項'}"
          @click="tab = '退貨注意事項'"
          data-toggle="tab"
          href="#profile"
        >退貨注意事項</a>
      </li>
    </ul>
    <!-- 商品資訊標籤 -->
    <div
      class="tab-content border-primary"
      id="myTabContent"
    >
      <div
        class="tab-pane fade"
        id="home"
        role="tabpanel"
        :class="{'show active' : tab === '票券商品資訊'}"
      >
        <div class="d-flex px-40">
          <div class="intro">
            <h4 class="font-weight-bold w-100 border-bottom pb-3 mb-30">選擇票券商品分類</h4>
            <p class="d-flex align-items-center tabSubTitle mb-2">
              <span class="text-danger star">*</span>
              <label
                class="text-primary font-weight-bold mr-3"
                for="productName"
              >分類</label>
            </p>
            <div class="mb-40">
              <select
                :disabled="method.indexOf('view') >=0"
                id="yfp_ticket_category"
                v-model="yfp_ticket_category"
                class="form-control font-weight-bold border-camera"
                required
              >
                <option
                  :key="key"
                  :value="key"
                  v-for="(text,key) in category_options"
                  class="text-success"
                >
                  {{text}}
                </option>
              </select>
            </div>
            <h4 class="font-weight-bold w-100 border-bottom pb-3 mb-30">新增票券商品名稱</h4>
            <!-- 商品名稱 -->
            <p class="d-flex align-items-center tabSubTitle mb-2">
              <span class="text-danger star">*</span>
              <label
                class="text-primary font-weight-bold mr-3"
                for="productName"
              >票券商品名稱</label>
              <span class="fzProductName">商品名稱長度必須在3至255個字元之間</span>
            </p>
            <div class="mb-40">
              <input
                :disabled="method.indexOf('view') >=0"
                v-model="yfp_ticket_name"
                type="text"
                class="form-control border-camera"
                id="productName"
                required
              >
            </div>
            <!-- 新增商品圖片 -->
            <h4 class="font-weight-bold w-100 border-bottom pb-3 mb-30">新增票券商品圖片</h4>
            <p class="d-flex align-items-center tabSubTitle">
              <span class="text-danger star">*</span>
              <span class="text-primary font-weight-bold">票券商品圖片</span>
            </p>
            <div
              :style="{'background-image':yfp_ticket_image ? 'url(' + yfp_ticket_image + ')':'none'}"
              class="cameraImgBox d-flex justify-content-center align-items-center mb-2"
            >
              <label for="upload">
                <img
                  src="../../assets/img/uploadImg.png"
                  alt="upload"
                  :class="{'d-none' : yfp_ticket_image}"
                >
              </label>
              <form action="">
                <input
                  :disabled="method.indexOf('view') >=0"
                  @change="resize({event:$event,field:'yfp_ticket_image'})"
                  accept="image/*"
                  ref="fileInput"
                  type="file"
                  id="upload"
                  class="d-none"
                >
              </form>
            </div>
            <span class="imgRestrict d-block">允許每張圖 1 MB, 建議寬度 1024 px 避免檔名使用特殊符號，例如 [ ‘ \ “ ; = ]</span>
            <div
              class=""
              v-if="method.indexOf('view') === -1"
            >
              <button
                type="button"
                class="btn btn-success mt-3 text-center btn-size font-weight-bold mr-3"
              >
                <div
                  @click="triggerFile('fileInput')"
                  class="d-flex justify-content-center align-items-center"
                >
                  <img
                    src="../../assets/img/upload_arrow.png"
                    alt=""
                    class="mr-3"
                  >
                  <span class="text-light mt-0">上傳圖片</span>
                </div>
              </button>
              <button
                @click="resetFile('fileInput','yfp_ticket_image')"
                type="button"
                class="btn btn-danger mt-3 btn-size font-weight-bold"
              >
                <div class="d-flex justify-content-center align-items-center">
                  <img
                    src="../../assets/img/trash_can.png"
                    alt=""
                    class="mr-4"
                  >
                  <span class="text-light mt-0">移除</span>
                </div>
              </button>
            </div>
            <!-- 新增商品詳細資訊 -->
            <h4 class="font-weight-bold w-100 border-bottom pb-3 my-30">新增票券商品詳細資訊</h4>
            <p class="d-flex align-items-center tabSubTitle mb-2">
              <span class="text-danger star">*</span>
              <label
                class="text-primary font-weight-bold mr-3"
                for="productCover"
              >票券商品詳細說明圖片</label>
            </p>
            <div
              :style="{'background-image':yfp_ticket_description_image ? 'url(' + yfp_ticket_description_image + ')':'none'}"
              class="cameraImgBox d-flex justify-content-center align-items-center mb-2"
            >
              <label for="productCover">
                <img
                  src="../../assets/img/uploadImg.png"
                  alt="upload"
                  :class="{'d-none' : yfp_ticket_description_image}"
                >
              </label>
              <form action="">
                <input
                  :disabled="method.indexOf('view') >=0"
                  @change="resize({event:$event,field:'yfp_ticket_description_image'})"
                  ref="fileInput2"
                  type="file"
                  id="productCover"
                  class="d-none"
                >
              </form>
            </div>
            <span class="imgRestrict d-block">允許每張圖 1 MB, 建議寬度 1024 px 避免檔名使用特殊符號，例如 [ ‘ \ “ ; = ]</span>
            <div
              v-if="method.indexOf('view') === -1"
              class=""
            >
              <button
                type="button"
                @click="triggerFile('fileInput2')"
                class="btn btn-success mt-3 text-center btn-size font-weight-bold mr-3"
              >
                <div class="d-flex justify-content-center align-items-center">
                  <img
                    src="../../assets/img/upload_arrow.png"
                    alt=""
                    class="mr-3"
                  >
                  <span class="text-light mt-0">上傳圖片</span>
                </div>
              </button>
              <button
                @click="resetFile('fileInput2','yfp_ticket_description_image')"
                type="button"
                class="btn btn-danger mt-3 btn-size font-weight-bold"
              >
                <div class="d-flex justify-content-center align-items-center">
                  <img
                    src="../../assets/img/trash_can.png"
                    alt=""
                    class="mr-4"
                  >
                  <span class="text-light mt-0">移除</span>
                </div>
              </button>
            </div>

            <!-- 新增票券商品介紹標題 -->
            <p class="d-flex align-items-center tabSubTitle my-3">
              <span class="text-danger star">*</span>
              <label
                class="text-primary font-weight-bold mr-3 mb-0"
                for="productIntroTitle"
              >新增票券商品介紹標題</label>
              <span class="fzProductName">商品名稱長度必須在3至15個字元之間</span>
            </p>
            <div class="mb-40">
              <input
                :disabled="method.indexOf('view') >=0"
                type="text"
                v-model="yfp_ticket_sub_title"
                class="form-control  border-camera"
                id="productIntroTitle"
                required
              >
            </div>
            <!-- 新增票券商品介紹 -->
            <div class="mb-40">
              <p class="d-flex  flex-wrap align-items-center tabSubTitle  mb-2">
                <span class="text-danger star">*</span>
                <span class="text-primary font-weight-bold mr-3">新增票券商品介紹</span>
                <span class="fzProductName">建議不超過五張圖 寬度 860 px</span>
              </p>
              <div>
                <textarea
                  :disabled="method.indexOf('view') >=0"
                  rows="5"
                  v-model="yfp_ticket_content"
                  class="form-control border-camera"
                  placeholder="[商品介紹]"
                  required
                ></textarea>
              </div>
              <div>
                <button
                  @click="yfp_ticket_content = old_yfp_ticket_content"
                  type="button"
                  class="btn btn-log mt-3 text-center btn-size font-weight-bold mr-3 d-flex align-items-center justify-content-center"
                >
                  <div class="d-flex justify-content-center align-items-center">
                    <img
                      src="../../assets/img/back_to_default.png"
                      alt=""
                      class="mr-3"
                    >
                    <span class="text-light mt-0">回預設值</span>
                  </div>
                </button>
              </div>
            </div>

            <!-- 新增票券商品使用說明 -->
            <div class="mb-40">
              <p class="d-flex  flex-wrap align-items-center tabSubTitle  mb-2">
                <span class="text-danger star">*</span>
                <span class="text-primary font-weight-bold mr-0">新增票券商品使用說明</span>
              </p>
              <div>
                <textarea
                  :disabled="method.indexOf('view') >=0"
                  rows="5"
                  v-model="yfp_ticket_use_description"
                  class="form-control border-camera"
                  placeholder="[電子票券使用說明]"
                  required
                ></textarea>
              </div>
              <div>
                <button
                  @click="yfp_ticket_use_description = old_yfp_ticket_use_description"
                  type="button"
                  class="btn btn-log mt-3 text-center btn-size font-weight-bold mr-3  d-flex align-items-center justify-content-center"
                >
                  <div class="d-flex justify-content-center align-items-center">
                    <img
                      src="../../assets/img/back_to_default.png"
                      alt=""
                      class="mr-3"
                    >
                    <span class="text-light mt-0">回預設值</span>
                  </div>
                </button>
              </div>
            </div>

            <!-- 新增票券商品注意事項 -->
            <div class="mb-40">
              <p class="d-flex  flex-wrap align-items-center tabSubTitle  mb-2">
                <span class="text-danger star">*</span>
                <span class="text-primary font-weight-bold mr-0">新增票券商品注意事項</span>
              </p>
              <div>
                <textarea
                  :disabled="method.indexOf('view') >=0"
                  rows="5"
                  v-model="yfp_ticket_notice_description"
                  class="form-control border-camera"
                  placeholder="結帳前請出示本券，不接受手抄或口說序號方式兌換。 本券不適用購買SOGO百貨現金禮券或商品禮券。 本券無法兌換現金或找零，請一次抵用完畢，不可重複使用。"
                  required
                ></textarea>
              </div>
              <div>
                <button
                  @click="yfp_ticket_notice_description = old_yfp_ticket_notice_description"
                  type="button"
                  class="btn btn-log mt-3 text-center btn-size font-weight-bold mr-3 d-flex align-items-center justify-content-center"
                >
                  <div class="d-flex justify-content-center align-items-center">
                    <img
                      src="../../assets/img/back_to_default.png"
                      alt=""
                      class="mr-3"
                    >
                    <span class="text-light mt-0">回預設值</span>
                  </div>
                </button>
              </div>
            </div>

            <!-- 新增票券商品門市資訊說明 -->

            <div class="mb-40">
              <p class="d-flex  flex-wrap align-items-center tabSubTitle  mb-2">
                <span class="text-danger star">*</span>
                <span class="text-primary font-weight-bold mr-0">新增票券商品門市資訊及營業時間</span>
              </p>
              <div>
                <textarea
                  :disabled="method.indexOf('view') >=0"
                  rows="5"
                  v-model="yfp_ticket_store_description"
                  class="form-control border-camera"
                  placeholder="本電子票券可在某某門市使用。"
                  required
                ></textarea>
              </div>
              <div>
                <button
                  @click="yfp_ticket_store_description = old_yfp_ticket_store_description"
                  type="button"
                  class="btn btn-log mt-3 text-center btn-size font-weight-bold mr-3 d-flex align-items-center justify-content-center"
                >
                  <div class="d-flex justify-content-center align-items-center">
                    <img
                      src="../../assets/img/back_to_default.png"
                      alt=""
                      class="mr-3"
                    >
                    <span class="text-light mt-0">回預設值</span>
                  </div>
                </button>
              </div>
            </div>

            <!-- 新增商品活動資訊 -->
            <h4 class="font-weight-bold w-100 border-bottom pb-3 mb-30">新增票券商品地址</h4>
            <div class="mb-3">
              <p class="d-flex align-items-center tabSubTitle  mb-2">
                <label
                  class="text-primary font-weight-bold mr-3"
                  for="franchiseShow"
                >前端顯示「查看分店」按鈕</label>
              </p>
              <div class="">
                <select
                  :disabled="method.indexOf('view') >=0"
                  @change="franchiseShow === 'close' && (yfp_ticket_branch_url = '')"
                  name="franchiseShow"
                  id="franchiseShow"
                  class="form-control border-camera"
                  v-model="franchiseShow"
                >
                  <option value="close">關閉</option>
                  <option value="open">開啟</option>
                </select>
              </div>
            </div>
            <!-- 活動結束日期 -->
            <div class="mb-3">
              <p class="d-flex flex-wrap align-items-center tabSubTitle mb-2">
                <span class="text-danger star">*</span>
                <label
                  class="text-primary font-weight-bold mr-3 mb-0"
                  for="yfp_ticket_address"
                >置入網址</label>
                <span class="fzProductName">於前端點選「查看分店之後」，可連結至您的官網查看分店資訊。</span>
              </p>
              <div>
                <input
                  :disabled="franchiseShow === 'close' || method.indexOf('view') >=0"
                  id="yfp_ticket_address"
                  type="text"
                  v-model="yfp_ticket_branch_url"
                  class="form-control border-page"
                />
              </div>
            </div>
            <!-- 商品活動地點 -->
            <div class="mb-3">
              <p class="d-flex flex-wrap align-items-center tabSubTitle  mb-2">
                <span class="text-danger star">*</span>
                <label
                  class="text-primary font-weight-bold mr-3 mb-0"
                  for="location"
                >新增票券商品地址</label>
                <span class="fzProductName">輸入地址後，此商品將顯示於APP探索中的地圖；系統將根據此地址在google map抓取經緯度 請正確填寫。</span>
              </p>
              <div class="">
                <input
                  :disabled="method.indexOf('view') >=0"
                  type="text"
                  v-model="yfp_ticket_address"
                  class="form-control"
                  id="location"
                  required
                />
              </div>
            </div>
            <div class="mb-3">
              <p class="d-flex flex-wrap align-items-center tabSubTitle  mb-2">
                <label
                  class="text-primary font-weight-bold mr-3 mb-0"
                  for="yfp_ticket_branch"
                >核銷分店</label>
                <span class="fzProductName">留空代表全分店可核銷</span>
              </p>
              <div class="">
                <multiselect
                  v-model="yfp_ticket_branch"
                  :multiple="true"
                  :options="branch"
                  :disabled="method.indexOf('view') >=0"
                  :close-on-select="false"
                  :clear-on-select="false"
                  :hide-selected="true"
                  :preserve-search="true"
                  key="yfp_store_branch_id"
                  placeholder="選擇核銷分店"
                  label="yfp_store_branch_name"
                  track-by="yfp_store_branch_name"
                  id="yfp_ticket_branch"
                >
                </multiselect>
              </div>
            </div>
          </div>

          <div class="priceNum">
            <h4 class="font-weight-bold w-100 border-bottom pb-3 mb-3">票券商品發行價格與數量</h4>
            <!-- 商品價格設定 -->
            <div class="priceSetting">
              <p class="d-flex align-items-center tabSubTitle mb-2">
                <span class="text-danger star">*</span>
                <label
                  class="text-primary font-weight-bold"
                  for="originalPrice"
                >票券商品原價</label>
              </p>
              <div class="mb-2">
                <input
                  :disabled="method.indexOf('view') >=0"
                  @change="calculatePrice('yfp_ticket_base_price')"
                  v-model="yfp_ticket_base_price"
                  type="text"
                  class="form-control"
                  id="originalPrice"
                  required
                >
              </div>
              <div class="w-100 p-3 highlight">
                <span class="enterDiscount">輸入票券商品折數</span>
                <span class="text-danger enterDiscountDes">系統自動將發行價格計算折扣後價格(無限進位)</span>
                <hr class="mt-1">
                <p class="d-flex align-items-center tabSubSubTitle mb-0">
                  <span class="text-danger star">*</span>
                  <label
                    class="text-primary font-weight-bold mb-1"
                    for="priceDiscount"
                  >票券商品折數</label>
                </p>
                <div class="mb-3">
                  <input
                    :disabled="method.indexOf('view') >=0"
                    @change="calculatePrice('priceDiscount')"
                    maxlength="2"
                    v-model="priceDiscount"
                    type="number"
                    id="priceDiscount"
                    class="form-control text-right"
                    placeholder="折"
                    required
                  />
                </div>
                <!-- 商品發行價格 -->
                <p class="d-flex align-items-center tabSubSubTitle mb-0">
                  <span class="text-danger star">*</span>
                  <label
                    class="text-primary font-weight-bold mb-1"
                    for="realPrice"
                  >票券商品發行價格（銷售價格）</label>
                </p>
                <div>
                  <input
                    :disabled="method.indexOf('view') >=0"
                    @change="calculatePrice('yfp_ticket_price')"
                    v-model="yfp_ticket_price"
                    type="number"
                    id="realPrice"
                    class="form-control text-right"
                    placeholder="元"
                    required
                  >
                </div>
              </div>
            </div>
            <!-- 數量 -->
            <div class="mb-3">
              <p class="d-flex align-items-center tabSubTitle  mb-2 mt-30">
                <span class="text-danger star">*</span>
                <label
                  class="text-primary font-weight-bold mr-3 mb-0"
                  for="num"
                >數量</label>
              </p>
              <div class="">
                <input
                  :disabled="method.indexOf('view') >=0"
                  type="text"
                  v-model="yfp_ticket_num"
                  class="form-control"
                  id="num"
                  required
                />
              </div>
            </div>
            <!-- 顯示商品已賣出數量 -->
            <div class="mb-8">
              <p class="d-flex align-items-center tabSubTitle  mb-2">
                <!-- <span class="text-danger star">*</span> -->
                <label
                  class="text-primary font-weight-bold mr-3 mb-0"
                  for="yfp_ticket_show_sell_information"
                >前端顯示已賣出數量</label>
              </p>
              <div class="">
                <select
                  :disabled="method.indexOf('view') >=0"
                  class="form-control font-weight-bold"
                  id="yfp_ticket_show_sell_information"
                  v-model="yfp_ticket_show_sell_information"
                  :class="{'text-success' : yfp_ticket_show_sell_information === '1','text-danger':yfp_ticket_show_sell_information === '0'}"
                >
                  <option
                    value="1"
                    class="text-success"
                  >啟用
                  </option>
                  <option
                    value="0"
                    class="text-danger"
                  >未啟用
                  </option>
                </select>
              </div>
            </div>
            <!-- 商品狀態 -->
            <h4 class="font-weight-bold w-100 border-bottom pb-3 mb-34">票券商品狀態</h4>
            <!-- 排序 -->
            <div class="">
              <p class="d-flex align-items-center tabSubTitle  mb-1">
                <span class="text-danger star">*</span>
                <label
                  class="text-primary font-weight-bold mr-3 mb-0"
                  for="order"
                >排序</label>
              </p>
              <div class="mb-3">
                <input
                  :disabled="method.indexOf('view') >=0"
                  v-model="yfp_ticket_order"
                  type="text"
                  class="form-control"
                  id="order"
                  required
                >
              </div>
              <!-- 上架日期 -->
              <div class="">
                <p class="d-flex align-items-center tabSubTitle  mb-1">
                  <span class="text-danger star">*</span>
                  <label
                    class="text-primary font-weight-bold mr-3 mb-0"
                    for="yfp_ticket_start_date"
                  >上架日期</label>
                  <span class="fzProductName">商品上架日與次排序</span>
                </p>
                <div class="mb-3">
                  <datetime
                    input-class="form-control"
                    :disabled="method.indexOf('view') >=0"
                    input-id="yfp_ticket_start_date"
                    type="datetime"
                    class="form-control py-0 px-0"
                    v-model="yfp_ticket_start_date"
                  />
                </div>
              </div>
              <!-- 下架日期 -->
              <div class="">
                <p class="d-flex align-items-center tabSubTitle  mb-1">
                  <span class="text-danger star">*</span>
                  <label
                    class="text-primary font-weight-bold mr-3 mb-0"
                    for="yfp_ticket_end_date"
                  >下架日期</label>
                  <span class="fzProductName">商品上架日與次排序</span>
                </p>
                <div class="mb-3">
                  <datetime
                    input-class="form-control"
                    :disabled="method.indexOf('view') >=0"
                    input-id="yfp_ticket_end_date"
                    type="datetime"
                    v-model="yfp_ticket_end_date"
                    class="form-control py-0 px-0"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!-- 商品信託資訊 -->
      <div
        class="tab-pane fade px-30 pt-30"
        :class="{'show active' : tab === '票券商品信託資訊'}"
        id="trustInfo"
        role="tabpanel"
      >
        <h4 class="font-weight-bold w-100 border-bottom pb-3 mb-3">票券商品信託資訊</h4>
        <h5 class="font-weight-bold w-100 border-bottom pb-3 mb-3">【範例】禮券定型化契約應記載事項</h5>
        <div class="mb-40">
          <p class="d-flex  flex-wrap align-items-center tabSubTitle  mb-2">
            <span class="text-primary font-weight-bold mr-0">範例：商品信託資訊（零售業等商品(服務)禮券定型化契約應記載事項）</span>
          </p>
          <textarea
            :disabled="method.indexOf('view') >=0"
            cols="30"
            rows="10"
            class="form-control border-camera w-75 mt-4"
            disabled
          >
            (一)發行人名稱、地址、統一編號及負責人姓名。 (二)商品(服務)禮券之面額或使用之項目、次數。 (三)商品(服務)禮券發售編號。 (四)使用方式。 二、發行人應依下列方式之一提供消費者期間至少一年之履約保證機制，並記載 於禮券券面明顯處: (一)本商品(服務)禮券內容表彰之金額，已經○金融機構提供足額履約保證， 前開保證期間自中華民國○年○月○日(出售日)至中華民國○年○月○日 止(至少一年)。 146 (二)本商品(服務)禮券，已與○公司(同業同級，市占率至少百分之五以上) 等相互連帶保證，持本禮券可依面額向上列公司購買等值之商品(服務)。 上列公司不得為任何異議或差別待遇，亦不得要求任何費用或補償。前開相 互連帶保證期間自中華民國○年○月○日(出售日)至中華民國○年○月○ 日止(至少一年)。 (三)本商品(服務)禮券所發行之金額，已存入發行人於○金融機構開立之信託專 戶，專款專用;所稱專用，係指供發行人履行交付商品或提供服務義務使用。 前開信託期間自中華民國○年○月○日(出售日)至中華民國○年○月○日 止(至少一年)。 (四)本商品(服務)禮券已加入由○商業同業公會辦理之○同業禮券聯合連帶保 證協定，持本禮券可依面額向加入本協定之公司購買等值之商品(服務)。 前開連帶保證期間自中華民國○年○月○日(出售日)至中華民國○年○月 ○日止(至少一年)。 (五)其他經經濟部許可之履約保證方式。(須敘明該履約保證方式內容，及經濟 部許可同意公文字號)。 ______________________________________________ 發行人：某某某股份有限公司。 本券為請於指定日期前兌換，不得零售或轉售。 本券為不記名，任何人持本券皆可使用，請自行妥善保管，如遭他人盜用，本券不再補發。 本券所兌換之商品或折抵消費之金額不予開立統一發票。 本券有效與否，以發行人票券系統所記錄之狀態為憑。 如系統因網路連線有所遲延，依兌換商家系統端資訊為準。 本券為有價證券，請勿擅自偽造、變造，以免觸犯刑責。
          </textarea>
        </div>
        <!-- 發行商品資訊及履約保證 -->
        <h4 class="font-weight-bold w-100 border-bottom pb-3 mb-3">發行商資訊及履約保證</h4>
        <p class="d-flex  flex-wrap align-items-center tabSubTitle  mb-2">
          <span class="text-primary font-weight-bold mr-0">(1) 發行人名稱</span>
        </p>
        <input
          type="text"
          :disabled="method.indexOf('view') >=0"
          class="form-control w-75 mb-4"
          disabled
          placeholder="XXX股份有限公司"
        >
        <p class="d-flex  flex-wrap align-items-center tabSubTitle  mb-2">
          <span class="text-primary font-weight-bold mr-0">(2) 聯絡地址</span>
        </p>
        <input
          :disabled="method.indexOf('view') >=0"
          type="text"
          class="form-control w-75 mb-4"
          disabled
          placeholder="公司地址"
        >
        <p class="d-flex  flex-wrap align-items-center tabSubTitle  mb-2">
          <span class="text-primary font-weight-bold mr-0">(3) 負責人</span>
        </p>
        <input
          :disabled="method.indexOf('view') >=0"
          type="text"
          class="form-control w-75 mb-4"
          disabled
          placeholder="某某某"
        >
        <p class="d-flex  flex-wrap align-items-center tabSubTitle  mb-2">
          <span class="text-primary font-weight-bold mr-0">(4) 統一編號</span>
        </p>
        <input
          :disabled="method.indexOf('view') >=0"
          type="text"
          class="form-control w-75 mb-4"
          disabled
          placeholder="12345678"
        >
        <p class="d-flex  flex-wrap align-items-center tabSubTitle  mb-2">
          <span class="text-primary font-weight-bold mr-0">(5) 履約保證</span>
        </p>
        <textarea
          :disabled="method.indexOf('view') >=0"
          type="text"
          class="form-control w-75 mb-4"
          disabled
          placeholder="本商品(服務)禮券所發行之金額，已存入發行人於永豐銀行開立之信託專戶，專款專用;所稱專用，係指供發行人履行交付商品或提供服務義務使用。 前開信託期間自出售日起算一年。"
        ></textarea>

        <!-- <div class="mb-40">
          <p class="d-flex  flex-wrap align-items-center tabSubTitle  mb-2">
            <span class="text-danger star">*</span>
            <span class="text-primary font-weight-bold mr-0">票券商品信託資訊（信託應記載條款）</span>
          </p>
          <div>
            <textarea v-model="yfp_ticket_trust_information"></textarea>
          </div>
          <div>
            <button
              @click="yfp_ticket_trust_information = old_yfp_ticket_trust_information"
              type="button"
              class="btn btn-log mt-3 text-center btn-size font-weight-bold mr-3"
            >
              <div class="d-flex justify-content-center align-items-center">
                <img
                  src="../../assets/img/back_to_default.png"
                  alt=""
                  class="mr-3"
                >
                <span class="text-light">回預設值</span>
              </div>
            </button>
          </div>
        </div> -->
        <!-- 信託長度 -->
        <p class="d-flex  flex-wrap align-items-center tabSubTitle  mb-2">
          <span class="text-primary font-weight-bold mr-0">信託期間長度</span>
        </p>
        <input
          type="text"
          class="form-control w-50 mb-8"
          :placeholder="mon+'個月'"
          disabled
        >
      </div>
      <!-- 退貨注意事項 -->
      <div
        class="tab-pane fade px-30 pt-30"
        :class="{'show active' : tab === '退貨注意事項'}"
        id="returnNotice"
        role="tabpanel"
      >
        <h4 class="font-weight-bold w-100 border-bottom pb-3 mb-4">退貨注意事項</h4>
        <p class="d-flex align-items-center tabSubTitle  mb-1">
          <span class="text-danger star">*</span>
          <label
            class="text-primary font-weight-bold mr-3 mb-0"
            for="yfp_ticket_return_information"
          >退貨注意事項</label>
        <div>
            <textarea
              :disabled="method.indexOf('view') >=0"
              class="form-control w-50"
              required
              id="yfp_ticket_return_information"
              rows="10"
              v-model="yfp_ticket_return_information"
            ></textarea>
        </div>
        <div>
          <button
            @click="yfp_ticket_return_information = old_yfp_ticket_return_information"
            type="button"
            class="btn btn-log mt-3 text-center btn-size font-weight-bold mr-3 d-flex align-items-center justify-content-center"
          >
            <div class="d-flex justify-content-center align-items-center">
              <img
                src="../../assets/img/back_to_default.png"
                alt=""
                class="mr-3"
              >
              <span class="text-light">回預設值</span>
            </div>
          </button>
        </div>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
  import {mapActions, mapState} from "vuex";
  import {mapFields} from "vuex-map-fields";
  import Multiselect from 'vue-multiselect'

  export default {
    components: {Multiselect},
    data() {
      return {
        tab: "票券商品資訊",
        value: null,
        method: 'view',
      };
    },
    methods: {
      ...mapActions("trustTicketPage", ["initForm", "resize", "post"]),
      ...mapActions("reviewTrustTicket", ["pass", "reject"]),
      ...mapActions("ui", ["resetErrMsg"]),
      calculatePrice(k) {
        this.priceDiscount = parseFloat(this.priceDiscount);
        this.yfp_ticket_base_price = this.yfp_ticket_base_price >> 0;
        this.yfp_ticket_price = this.yfp_ticket_price >> 0;
        if (this.priceDiscount > 100 || this.priceDiscount <= 0)
          this.priceDiscount = 100;
        if (k === "yfp_ticket_base_price" || k === "priceDiscount") {
          this.yfp_ticket_price = Math.floor(
            (this.yfp_ticket_base_price * this.priceDiscount) / 100
          );
        } else {
          this.yfp_ticket_base_price = Math.floor(
            (this.yfp_ticket_price * 100) / this.priceDiscount
          );
        }
      },
      triggerFile(key) {
        this.$refs[key].click();
      },
      resetFile(key, image) {
        this.$refs[key].value = "";
        this[image] = "";
      }
    },
    computed: {
      ...mapState("ui", ["errMsg"]),
      ...mapState("trustTicketPage", ["branch"]),
      ...mapFields("trustTicketPage", [
        "id",
        "franchiseShow",
        "lastUpdate",
        "yfp_ticket_name",
        "yfp_ticket_sub_title",
        "yfp_ticket_address",
        "yfp_ticket_branch_url",
        "yfp_ticket_content",
        "yfp_ticket_num",
        "yfp_ticket_start_date",
        "yfp_ticket_end_date",
        "yfp_event_num",
        "yfp_ticket_order",
        "yfp_ticket_show_sell_information",
        "yfp_ticket_category",
        "yfp_ticket_base_price",
        "priceDiscount",
        "yfp_ticket_price",
        "category_options",
        "yfp_ticket_image",
        "mon",
        "yfp_ticket_trust_information",
        "yfp_ticket_return_information",
        "yfp_ticket_use_description",
        "yfp_ticket_notice_description",
        "yfp_ticket_store_description",
        "yfp_ticket_description_image",
        "old_yfp_ticket_trust_information",
        "old_yfp_ticket_return_information",
        "old_yfp_ticket_content",
        "old_yfp_ticket_use_description",
        "old_yfp_ticket_notice_description",
        "old_yfp_ticket_store_description",
        "yfp_ticket_branch"
      ])
    },
    created() {
      this.resetErrMsg();
      this.initForm();
    }
  };
</script>
<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>
<style lang="scss">
  body {
    background: #ffffff;
  }
  .saveBtnSpan {
    // font-size: 31px;
    line-height: 31px;
    margin-top: 2px;
    display: flex;
    align-items: center;
    vertical-align: sub;
    box-sizing: border-box;
  }

  .alert {
    padding: 20px;
    background-color: #f44336; /* Red */
    color: white;
    margin-bottom: 15px;
  }

  .fzProductName {
    font-size: 14px;
    color: rgba(75, 75, 75, 0.5);
  }

  /* The close button */
  .closebtn {
    margin-left: 15px;
    color: white;
    font-weight: bold;
    float: right;
    font-size: 22px;
    line-height: 20px;
    cursor: pointer;
    transition: 0.3s;
  }

  /* When moving the mouse over the close button */
  .closebtn:hover {
    color: black;
  }

  .rightContentDetailTitle {
    color: #3b44a9;
    font-size: 212%;
    margin-bottom: 2%;
    vertical-align: center;
  }

  // 最上方button樣式
  .addNewTicketPage {
    .btn-save,
    .btn-finish {
      width: 134px;
      height: 52px;
      border-radius: 26px;
      padding-top: 6px;
      padding-left: 19px;
      box-sizing: border-box;
    }
  }

  // 切換頁簽樣式
  .nav-tabs {
    border-bottom: 0px;
    margin-top: 55px;

    .nav-link {
      &.prodInform {
        border-color: #dee2e6 #dee2e6 transparent;
        border-width: 2px 2px 0px 2px;

        &:hover {
          border-color: #3b44a9 #3b44a9;
          border-width: 2px 2px 0px 2px;
        }
      }

      &.trustInfo {
        border-color: #dee2e6 #dee2e6 transparent;
        border-width: 2px 2px 0px 0px;

        &:hover {
          border-color: #3b44a9 #3b44a9 #dee2e6;
          border-width: 2px 2px 0px;
        }
      }

      &.returnNotice {
        border-color: #dee2e6 #dee2e6 transparent;
        border-width: 2px 2px 0px 0px;

        &:hover {
          border-color: #3b44a9 #3b44a9 #dee2e6;
          border-width: 2px 2px 0px;
        }
      }
    }

    .nav-item {
      margin-bottom: 0px;
    }
  }

  // 頁簽啟用樣式
  .nav-tabs .nav-link.active {
    border-color: #3b44a9 #3b44a9 #dee2e6;
    position: relative;
    z-index: 1000;
    border-width: 2px 2px 0px;
  }

  // 頁簽啟用內部樣式
  .active {
    h4 {
      margin-bottom: 1rem;
    }

    // 商品圖文介紹
    .intro {
      margin-top: 40px;
      width: 55%;
      // border-bottom: 2px solid #c9c9c9;
      box-sizing: border-box;

      .star {
        width: 20px;
      }

      img[alt="upload"] {
        cursor: pointer;
      }

      .cameraImgBox {
        width: 340px;
        height: 230px;
        box-sizing: border-box;
        background: rgba(201, 201, 201, 0.16);
        cursor: pointer;
        // background-image: url("../assets/logo.png");
        background-size: cover;
        background-position: center center;
        background-repeat: no-repeat;
      }

      .imgRestrict {
        font-size: 14px;
        color: rgba(75, 75, 75, 0.5);
      }

      .btn-size {
        width: 144px;
        border-radius: 3px;
        box-sizing: border-box;

        span {
          font-size: 16px;
          line-height: 16px;
          // margin-top: 2px;
        }
      }

      img[alt="plugin"] {
        width: 100%;
      }
    }

    .tabSubTitle {
      font-size: 20px;
    }

    .star {
      width: 20px;
    }

    // 發行價格與數量
    .priceNum {
      box-sizing: border-box;
      margin-top: 40px;
      width: 35%;
      margin-left: 10%;

      .fzProductName {
        font-size: 14px;
        color: rgba(75, 75, 75, 0.5);
      }

      .tabSubTitle {
        font-size: 20px;
      }

      .tabSubSubTitle {
        font-size: 14px;
      }

      .star {
        width: 20px;
      }

      .priceSetting {
        width: 100%;
        box-sizing: border-box;
        padding: 16px;
        border: 2px solid #9fc4f5;
        border-radius: 5px;

        .highlight {
          background: rgba(201, 201, 201, 0.17);
          border-radius: 5px;

          .enterDiscount {
            font-size: 14px;
          }

          .enterDiscountDes {
            font-size: 13px;
          }
        }
      }
    }
  }

  .tab-content {
    border-left: 2px solid #3b44a9;
    border-right: 2px solid #3b44a9;
    border-bottom: 2px solid #3b44a9;
    border-top: 2px solid #3b44a9;
    margin-bottom: 16px;
    margin-top: -2px;
    position: relative;
    z-index: 100;
  }

  //
  #returnNotice {
    .btn-size {
      width: 144px;
      border-radius: 3px;
      box-sizing: border-box;

      span {
        font-size: 16px;
        line-height: 16px;
        // margin-top: 2px;
      }
    }
  }

  // 票券起訖日樣式
  input#yfp_ticket_start_date,
  input#yfp_ticket_end_date {
    height: calc(1.5em + 0.75rem);
    padding-left: 1rem;
    border: 0;
    width: 100%;
  }
</style>



