import api from '../api'
import pagination from './common/pagination'
import {getField, updateField} from 'vuex-map-fields';

export const getters = Object.assign({getField}, pagination.getters)
export const state = Object.assign({
  buildcharacterSearch: '',
  id: 0,
  roles: [],
  fullRoles: ['票券庫存查詢', '商家資料建立與總表－【編輯】', '商家資料建立與總表－【審核】', '商家撥款審核', '角色建立', '管理使用者', '信託票券商品查詢'],
}, pagination.state)

export const actions = Object.assign({
  getList({commit, state}) {
    commit('ui/loading', {isLoading: true}, {root: true})
    api.buildcharacter.list({
      search: state.buildcharacterSearch,
      perPage: state.perPage,
      page: state.currentPage,
    }).then((rep) => {
      commit('paginationRows', {rows: rep.data.body.list})
    }).catch((error) => {
      commit('ui/ajaxModalError', {error: error}, {root: true})
    }).finally(() => {
      commit('ui/loading', {isLoading: false}, {root: true})
    })
  },
  trash({commit, state}, {id}) {
    commit('ui/loading', {isLoading: true}, {root: true})
    api.buildcharacter.trash({
      id: id,
    }).then((rep) => {
      commit('paginationRows', {rows: rep.data.body.list})
    }).catch((error) => {
      commit('ui/ajaxModalError', {error: error}, {root: true})
    }).finally(() => {
      commit('ui/loading', {isLoading: false}, {root: true})
    })
  },
  saveRoles({commit, state, dispatch}) {
    commit('ui/loading', {isLoading: true}, {root: true})
    api.buildcharacter.save({
      id: state.id,
      roles: state.roles
    }).then(() => {
      dispatch('getList')
      dispatch('ui/closeModal', {id: '#addCharacterFunctionModal'}, {root: true})
    }).catch((error) => {
      commit('ui/ajaxModalError', {error: error}, {root: true})
      commit('ui/loading', {isLoading: false}, {root: true})
    })
  },
}, pagination.actions);

export const mutations = Object.assign({updateField}, pagination.mutations)



