const getters = {
  pages(state) {
    const c = state.currentPage
    const t = state.totalPage
    if (t <= 10) {
      return [...Array(state.totalPage).keys()].map((i) => i + 1)
    } else if (c <= 5) {
      return [1, 2, 3, 4, 5, 6, 7, 8, 9, '...', t]
    } else if (c >= t - 4) {
      return [1, '...', t - 8, t - 7, t - 6, t - 5, t - 4, t - 3, t - 2, t - 1, t]
    } else {
      return [1, '...', c - 3, c - 2, c - 1, c, c + 1, c + 2, c + 3, '...', t]
    }
  },
  startPage(state) {
    return (state.currentPage - 1) * state.perPage + 1
  },
  endPage(state) {
    let end = state.currentPage * state.perPage
    return end > state.totalCount ? state.totalCount : end
  }
}

const state = {
  perPage: 15,
  totalPage: 1,
  currentPage: 1,
  totalCount: 1,
  paginationSearch: '',
  listRows: []
}

const actions = {
  setCurrentPage({state, commit, dispatch}, {page}) {
    if (page !== state.currentPage) {
      commit('currentPage', {page: page})
      dispatch('getList');
    }
  },
  prevOrNext({commit, state, dispatch}, {n}) {
    let i = state.currentPage + n
    i < 1
      ? i = 1
      : i > state.totalPage
      ? i = state.totalPage
      : null
    if (i !== state.currentPage) {
      commit('currentPage', {page: i})
      dispatch('getList');
    }
  },
  startSearch({commit, state, dispatch}, {search}) {
    commit('search', {search: search.trim()})
    dispatch('getList')
  },
  setPerPage({commit, state, dispatch}, {perPage}) {
    commit('perPage', {perPage: perPage})
    commit('currentPage', {page: 1})
    dispatch('getList')
  }
}

const mutations = {
  currentPage(state, {page}) {
    state.currentPage = page
  },
  search(state, {search}) {
    state.paginationSearch = search
  },
  perPage(state, {perPage}) {
    state.perPage = perPage
  },
  paginationRows(state, {rows}) {
    state.totalPage = rows.pages
    state.totalCount = rows.count
    state.listRows = rows.rows.reverse();
  },
}

export default {
  actions,
  state,
  mutations,
  getters,
}



