<template>
  <div class="merchantdatabuild">
    <!-- searchBar -->
    <div class="searchBar">
      <searchbar
        :setSearchEndDate="setSearchEndDate"
        :setSearchStartDate="setSearchStartDate"
        :searchEndDate="searchEndDate"
        :searchStartDate="searchStartDate"
        :searchText="searchText"
        :startSearch="startSearch"
        :setSearchText="setSearchText"
        :duration="duration"
        :setSearchSelect="setSearchSelect"
        :searchSelect="searchSelect"
      />
    </div>
    <div class="container-fluid detailContent bg-white pt-0">
      <div class="row px-40">
        <p class="titleFontDetail text-primary font-weight-bold mb-3 mt-3 ml-2">
          商家資料總表
        </p>
        <div class="table-responsive px-2">
          <table class="table border-bottom table-striped table-hover">
            <thead class="thead h45 w-100">
            <tr class=" text-light w-100">
              <th class="bg-tablehead  text-center border-top-0 border-bottom border-right">建立日期</th>
              <th class="bg-tablehead  text-center border-top-0 border-left">
                商家名稱
              </th>
              <th class="bg-tablehead  text-center border-top-0 border-left">
                統一編號
              </th>
              <th class="bg-tablehead  text-center border-top-0 border-left">
                信託編號
              </th>
              <th class="bg-tablehead  text-center border-top-0 border-left">
                審核狀態
              </th>
              <th class="bg-tablehead  text-center border-top-0 border-left">
                商家資料
              </th>
              <th class="bg-tablehead  text-center border-top-0 border-left">
                可售票
              </th>
            </tr>
            </thead>
            <tbody class="bg-white">
            <tr class="text-dark" v-for="(item) in listRows.slice().reverse()" :key="item[3]">
              <td class="text-center border-left font-weight-bold fz14 border-top-0">{{item[0]}}</td>
              <td class="text-center font-weight-bold fz14">{{item[1]}}</td>
              <td class="text-center font-weight-bold fz14">{{item[2]}}</td>
              <td class="text-center font-weight-bold fz14">{{item[3]}}</td>
              <td v-if="item[4] === '停權'" class="text-center text-darkOrange font-weight-bold fz14">已停權</td>
              <td v-else-if="item[8] === 1" class="text-center reviewing font-weight-bold fz14 text-darkOrange">未送審</td>
              <td v-else-if="item[8] === 2 || item[8] === 5"
                  class="text-center reviewing font-weight-bold fz14 text-darkOrange">審核中
              </td>
              <td v-else-if="item[8] === 6 || item[8] === 3" class="text-center text-review font-weight-bold fz14">已審核
              </td>
              <td v-else-if="item[8] === 4 || item[8] === 7" class="text-center text-darkOrange font-weight-bold fz14">
                <p class="mb-2">已駁回</p>
                <p class="mb-0" data-toggle="modal" data-target="#returnModal" @click="reason = item[6]">
                  <span class="clamationMark">!</span>
                  駁回理由
                </p>
                <!-- {{item[6]}} -->
              </td>
              <td class="d-flex justify-content-center align-items-center">
                <div v-if="employee && !manager && item[4] !== '停權'" class="d-flex">
                  <router-link v-if="item[8] === 3 || item[8] === 5 || item[8] === 6 || item[8] === 7"
                               class="text-decoration-none mx-1"
                               :to="'/edit/'+item[3]">
                    <button
                      class="btn btn-outline-log fz14 d-flex-align-items-center justify-content-center btnSize font-weight-bold">
                      查看詳情
                    </button>
                  </router-link>

                  <router-link v-if="item[8] === 1 || item[8] === 4 " class="text-decoration-none mx-1"
                               :to="'/edit/'+item[3]">
                    <button
                      class="btn btn-outline-review fz14 d-flex align-items-center justify-content-center btnSize font-weight-bold">
                      編輯
                    </button>
                  </router-link>
                  <button
                    v-if="item[8] === 2 || item[8] === 5"
                    class="btn btn-outline-secondary fz14 d-flex align-items-center justify-content-center btnSize font-weight-bold mx-1"
                    disabled>
                    編輯
                  </button>
                  <router-link v-if="item[8] === 3 || item[8] === 6 || item[8] === 7"
                               class="text-decoration-none mx-1"
                               :to="'/review/'+item[3]">
                    <button
                      class="btn btn-outline-review fz14 d-flex align-items-center justify-content-center btnSize font-weight-bold">
                      編輯
                    </button>
                  </router-link>
                </div>
                <!--經辦結束-->
                <!--主管-->
                <div v-if="manager && item[4] !== '停權'" class="d-flex">
                  <router-link v-if="item[8] === 3 || item[8] >= 5" class="text-decoration-none mx-1"
                               :to="'/edit/'+item[3]">
                    <button
                      class="btn btn-outline-log fz14 d-flex-align-items-center justify-content-center btnSize font-weight-bold">
                      查看詳情
                    </button>
                  </router-link>
                  <router-link v-if="item[8] === 2" class="text-decoration-none mx-1" :to="'/edit/'+item[3]">
                    <button
                      class="btn btn-outline-darkOrange fz14 d-flex align-items-center justify-content-center btnSize font-weight-bold">
                      審核
                    </button>
                  </router-link>
                  <router-link v-if="item[8] === 5" class="text-decoration-none mx-1" :to="'/review/'+item[3]">
                    <button
                      class="btn btn-outline-darkOrange fz14 d-flex align-items-center justify-content-center btnSize font-weight-bold">
                      審核
                    </button>
                  </router-link>
                </div>
              </td>
              <td class="text-center border-right font-weight-bold fz14">
                {{item[5]}}
              </td>
            </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
    <!-- Reviewed Modal -->
    <div
      class="modal fade"
      id="returnModal"
    >
      <div
        class="modal-dialog"
        role="document"
      >
        <div class="modal-content">
          <div class="modal-header justify-content-center">
            <h5
              class="modal-title font-weight-bold"
              id="reviewedModalTitle"
            >駁回理由</h5>
          </div>
          <div class="modal-body d-flex align-items-center justify-content-center">
            {{reason}}
          </div>
          <div class="modal-footer d-flex justify-content-around">
            <button
              type="button"
              class="btn btn-outline-log"
              data-dismiss="modal"
            >確定
            </button>
          </div>
        </div>
      </div>
    </div>

    <!-- footer -->
    <footer class="container-fluid fixed-bottom bg-light">
      <pagination
        :perPage="perPage"
        :totalPage="totalPage"
        :currentPage="currentPage"
        :totalCount="totalCount"
        :pages="pages"
        :setCurrentPage="setCurrentPage"
        :prevOrNext="prevOrNext"
        :getList="getList"
        :startPage="startPage"
        :endPage="endPage"
        :setPerPage="setPerPage"
      />
    </footer>
  </div>
</template>

<script>
  import searchbar from "./searchbar";
  import pagination from "./pagination";
  import {mapState, mapGetters, mapActions} from "vuex";

  export default {
    components: {
      searchbar,
      pagination
    },
    data() {
      return {
        duration: "商家建立區間",
        reason: ""
      };
    },
    computed: {
      ...mapState("merchant", [
        "perPage",
        "totalPage",
        "currentPage",
        "totalCount",
        "listRows",
        "searchText",
        "searchStartDate",
        "searchEndDate",
        "searchSelect"
      ]),
      ...mapState("account", ["manager", "employee"]),
      ...mapGetters("merchant", ["pages", "startPage", "endPage"])
    },
    methods: {
      ...mapActions("merchant", [
        "setCurrentPage",
        "prevOrNext",
        "getList",
        "setPerPage",
        "setSearchText",
        "setSearchStartDate",
        "setSearchEndDate",
        "startSearch",
        "setSearchSelect"
      ])
    },
    mounted() {
      this.getList();
    }
  };
</script>

<style lang="scss">
  body {
    background: #ffffff;
  }

  .merchantdatabuild {
    .searchBar .form-control::placeholder {
      color: #c1c1c1;
    }

    .fz14 {
      font-size: 14px;
      line-height: 14px;
    }

    .fz18 {
      font-size: 18px;
    }

    .searchBar {
      padding: 24px 52px;
      background: #f7f7f7;
      box-sizing: border-box;
      margin-top: 2px;
    }

    .detailContent {
      padding-top: 16px;
      height: calc(100vh - 60px - 57px - 50px - 93px);
      overflow-y: scroll;

      &::-webkit-scrollbar {
        display: none;
      }

      .titleFontDetail {
        font-size: 22px;
      }
    }

    // table setting

    .table {
      thead {
        display: block;
        width: 100%;

        tr {
          width: 100%;
          display: flex;
        }
      }

      th {
        height: 45px;
        box-sizing: border-box;
        border-bottom: 0;
        width: calc(100% / 7);

        &.border-left {
          border: 1px solid #efecec;
        }
      }

      tbody {
        display: block;
        overflow: auto;
        height: 500px;
        width: 100%;

        &::-webkit-scrollbar {
          display: none;
        }

        tr {
          width: 100%;
          display: flex;
        }

        td {
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          width: calc(100% / 7);
        }
      }

      td {
        height: 65px;
        box-sizing: border-box;

        .btnSize {
          width: 100px;
          height: 30px;
          box-sizing: border-box;
          border-radius: 5px;
        }

        .reviewing {
          color: #c1c1c1;
        }
      }

      th,
      td {
        vertical-align: middle;
      }

      thead {
        font-size: 14px;
      }

      th:first-child {
        border-top-left-radius: 5px;
      }

      th:last-child {
        border-top-right-radius: 5px;
      }
    }

    // footer
    .footer {
      height: 50px;
      box-sizing: border-box;

      .form-control {
        width: 80px;
        height: 30px;
        font-size: 14px;
      }
    }

    // 驚嘆號
    .clamationMark {
      font-size: 14px;
      line-height: 14px;
      height: 14px;
      width: 14px;
      box-sizing: border-box;
      background: #fc4524;
      color: white;
      display: inline-block;
      text-align: center;
      border-radius: 50%;
      cursor: pointer;
    }

    p[data-target="#returnModal"] {
      cursor: pointer;
    }

    // modal
    #returnModal {
      .modal-content {
        border-radius: 20px;
        box-shadow: 0px 0px 10px #00000029;
      }

      .modal-title {
        color: #555151;
        font-weight: bold;
      }

      .modal-dialog {
        max-width: 500px;
        height: 316px;
        box-sizing: border-box;
        border-radius: 20px;
        margin: 150px auto;
      }

      .modal-footer {
        .btn {
          width: 100px;
          height: 40px;
          border-radius: 22px;
        }
      }
    }
  }
</style>



