<template>
  <div v-if="store.yfp_store_id" id="app">
    <section class="sheet padding-15mm">
      <!-- Write HTML just like a web page -->
      <p class="nameTitle mb-0" v-text.trim="store.yfp_store_title"/>
      <p class="docTitle" style="margin-bottom: 8mm;">信託財產返還指示書</p>
      <!-- 致 自 -->
      <!-- <div class="fromTo">
        <div class="fz3">
          致:<span class="bankName fz3" v-text="vars['back_name']"></span>
          <p class="personName" v-text="`${vars['back_code']} / ${vars['back_number']}`"></p>
        </div>
        <div class="fz3">
          自:<span class="fromCompanyName  fz3" v-text="store.yfp_store_title"></span>
        </div>
      </div> -->
      <!-- fax -->
      <table class="table mb-0" style="margin-left: 10mm;">
        <thead>
          <tr>
            <td class="border-0 p-0">
              <div class="fz3">
                致:<span class="bankName fz3" v-text="vars['back_name']"  style="color:#006fc0"></span>
                <p class="personName" v-text="`${vars['back_code']} / ${vars['back_number']}`"  style="color:#006fc0; margin-top:2mm;"></p>
              </div>
            </td>
            <td class="border-0  p-0">
              <div class="fz3 pl-5">
                自:<span class="fromCompanyName  fz3" v-text="store.yfp_store_title" style="color:#006fc0"></span>
              </div>
            </td>
          </tr>
          <tr>
            <th scope="col" style="width:50%"  class="border-0 p-0">
              <div class="fz3" style="margin-bottom:2mm;">FAX:
                <span v-text.trim="store.yfp_store_touch_fax"/>
              </div>
            </th>
            <th scope="col" style="width:50%"  class="border-0 p-0 pl-5">
              <div class="fz3" style="margin-bottom:2mm;">FAX:
                <span v-text.trim="store.yfp_store_touch_fax"/>
              </div>
              
            </th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <th scope="row"  class="border-0 p-0">
              <div class="fz3 ">TEL:
                <span class="fz3" v-text.trim="store.yfp_store_tel"/>
              </div>
            </th>
            <td  class="border-0 p-0"> 
              <div class="fz3  pl-5">TEL:
                <span class="fz3" v-text.trim="store.yfp_store_tel"/>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
      <!-- <div class="fax">
        <div class="fz3">FAX:
          <span v-text.trim="vars['bank_fax']"/>
        </div>
        <div class="fz3">FAX:
          <span v-text.trim="store.yfp_store_touch_fax"/>
        </div>
      </div> -->
      <!-- Tel -->
      <!-- <div class="tel">
        <div class="fz3">TEL:
          <span class="fz3" v-text.trim="vars['bank_phone']"/>
        </div>
        <div class="fz3">TEL:
          <span class="fz3" v-text.trim="store.yfp_store_tel"/>
        </div>
      </div> -->
      <!-- 表格 -->
      <div class="detailInfo">
        <div class="all">
          <div class="left w-50">
            <div class="instructorDate">
              <p class="title mb-0">指示日期 :</p>
              <span>年</span>
              <span>月</span>
              <span>日</span>
            </div>
          </div>
          <div class="right">
            <div class="accountNum">
              <p class="title mb-0">專戶帳號 :</p>
              <span class="accountNumContent" v-text.trim="store.yfp_store_trust_number"/>
            </div>
          </div>
        </div>
        <div class="middleBox">
          <p class="title">指示內容 :</p>
          <p class="firstParagraph">
            一、玆請貴行依本指示通知，執行本公司開立於貴行之「永豐商業銀行受託信託財產專戶」
          </p>
          <p class="secondLine">中支付相關款項，如有匯費請自該筆款項中內扣。</p>
          <p class="secondPoint">
            二、指示事項 :
          </p>
          <p class="secondPointDetail">
            依據 <span class="secondPointMonth">{{date}}</span>
            禮券發行、使用及費用狀況，申請返還信託財產，明細如下:
          </p>
          <p class="applyMoney">
            1、申請返還金額-(1)禮券已核銷金額(A) :
            <span class="money">${{price_a | currency}}</span>
          </p>
          <p class="expiredMoney">
            -(2)禮券信託過期金額(B) : <span class="money">${{price_b | currency}}</span>
          </p>
          <p class="voucherCantUse">
            -(3)禮券作廢金額(C) : <span class="money">${{price_c | currency}}</span>
          </p>
          <!-- 回補準備金 -->
          <p class="prepareMoney">
            2、回補準備金<span class="moneyName"
          >-(1)信用卡手續費準備金(D) :</span
          >
            <span class="money"><input type="number" v-model="price_d"/></span>
          </p>
          <p class="handlingFee">
            -(2)豐收款手續費準備金(E) :
            <span class="money"><input type="number" v-model="price_e"/></span>
          </p>
          <p class="showAndSellDiffer">
            -(3)面額與發行價格差額(F) :
            <span class="money"><input type="number" v-model="price_f"/></span>
          </p>
          <p class="backToDes">
            請將已扣除之準備金，回存本公司開立於貴行之「永豐商業銀行受託信託財產專戶」。
          </p>
          <!-- 扣除信託手續費 -->
          <p class="reduceTrustHandlingFee">
            3、扣除信託手續費-(1)申請返還金額(A+B)*費率 :
            <span class="percentage"> {{store.yfp_store_trust_par}} %</span>(G) :
            <span class="money">${{price_g | currency}}</span>
          </p>
          <p class="yearLowestPrice ">
            -(2)年度最低金額(最低收費金額-已繳款金額)(H) : $<input type="number" v-model="price_h" class="lowestPay"/>
          </p>
          <div class="bankName">存入戶名 : <span v-text="vars['back_name']"></span></div>
          <div class="bankName">存入銀行/分行 : <span v-text="`${vars['bank_full_name']}`"></span></div>
          <div class="bankName">存入帳號 : <span v-text="vars['back_number']"></span></div>
          <!-- 實際返還信託金額 -->
          <div class="actuallyReturn">
            4、實際返還信託金額(A+B+C-D-E-F-G-H 小數點四捨五入) (I) :
            <span class="money">${{price_i}}</span>
          </div>
          <div class="bankName blue">存入戶名 : {{store.yfp_store_bank_name}}</div>
          <!--          <div class="bankName blue">存入銀行/分行 : XX商業銀行-XX分行</div> 沒有名稱只有代碼 -->
          <div class="bankName blue">存入銀行代號 : {{store.yfp_store_apply_bank}}</div>
          <div class="bankName blue">存入帳號 : {{store.yfp_store_bank_number}}</div>
        </div>
        <div class="signName">
          有權簽章人簽章：
          <div class="boxFlex">
            <div class="box">
              <div class="signBoxTop">核章</div>
              <div class="signBoxButtom"></div>
            </div>
          </div>
        </div>
      </div>
      <div class="bankSituation">
        銀行執行狀況（以下由銀行填寫）：
      </div>
    </section>
  </div>
</template>

<script>
  import './assets/apply/all.css'
  import {mapState, mapGetters, mapActions} from "vuex";
  import {mapFields} from "vuex-map-fields";

  export default {
    name: "App",
    methods: {
      ...mapActions("apply", [
        "init",
      ])
    },
    computed: {
      price_i() {
        return this.price_a + this.price_b + this.price_c - this.price_d - this.price_e - this.price_f - this.price_g - this.price_h;
      },
      ...mapFields("apply", [
        "price_d",
        "price_e",
        "price_f",
        "price_h",
      ]),
      ...mapState("apply", [
        "store",
        "price_a",
        "price_b",
        "price_c",
        "price_g",
        "date",
        "vars",
      ]),
    },
    mounted() {
      this.init();
    }
  };
</script>


<style lang="scss" scoped>
  @import "./assets/all.scss";
  .lowestPay{
    width: 120px;
  }
</style>


