<template>
  <div class="voucherstorelistdetail">
    <BlockUI
      v-if="isLoading"
      :message="msg"
      :html="html"
    />
    <div class="container-fluid">
      <div class="row py-3 headerUi px-40 align-items-center ">
        <router-link
          class="text-decoration-none"
          to="/admin/voucherstoresearch"
        >
          <div class="arrowLeftLayout d-flex align-items-center justify-content-center">
            <i class="fas fa-arrow-left text-tablebtn"/>
          </div>
        </router-link>
        <p class="mb-0 ml-3 text-light">返回</p>
        <p class="mb-0 ml-auto text-light font-weight-bold">票券庫存明細</p>
      </div>
    </div>
    <!-- filter -->
    <div class="searchBar py-4 d-flex align-items-center justify-content-between sticky-top">
      <div class="input-group mb-0 w-20">
        <input
          v-model="searchText"
          type="text"
          class="form-control border-log border-right-0 filterHeightControl fz14"
          placeholder="| 搜尋 交易訂單編號 或 票券編號"
          @keyup.enter="startSearch"
        >
        <div class="input-group-append">
          <button
            class="btn btn-outline-log  border-left-0"
            id="basic-addon2"
            @click="startSearch"
          >
            <i class="fas fa-search"/></button>
        </div>
      </div>
      <div class="d-flex align-items-center">
        <span class="fz14 ml-3 text-dark font-weight-bold">票券發行區間</span>
        <input
          type="date"
          v-model="searchStartDate"
          class="form-control dateWidthControl border-log ml-3 fz14"
          placeholder="2019/11/15"
        />
        <input
          type="date"
          v-model="searchEndDate"
          class="form-control dateWidthControl border-log ml-3 fz14"
        />
      </div>
      <div class="d-flex align-items-center">
        <span class="fz14 mx-3 text-dark font-weight-bold">票券狀態</span>
        <div class="btn-group btn-group-sm btn-outline-group btn-group-toggle">
          <button
            class="btn btn-outline-btngroup first font-weight-bold"
            @click.prevent="filterStatus.length === 5  ? filterStatus = [] : filterStatus = ['未使用','已使用','請款中','已審核','已作廢']"
            :class="{'active' : filterStatus.length === 5}"
          >全部
          </button>
          <button
            class="btn btn-outline-btngroup font-weight-bold"
            @click.prevent="filterStatus = _.xor(filterStatus,['未使用'])"
            :class="{'active' : filterStatus.includes('未使用')}"
          >未使用
          </button>
          <button
            class="btn btn-outline-btngroup font-weight-bold"
            @click.prevent="filterStatus = _.xor(filterStatus,['已使用'])"
            :class="{'active' : filterStatus.includes('已使用')}"
          >已使用
          </button>
          <button
            class="btn btn-outline-btngroup font-weight-bold"
            @click.prevent="filterStatus = _.xor(filterStatus,['請款中'])"
            :class="{'active' : filterStatus.includes('請款中')}"
          >請款中
          </button>
          <button
            class="btn btn-outline-btngroup font-weight-bold"
            @click.prevent="filterStatus = _.xor(filterStatus,['已審核'])"
            :class="{'active' : filterStatus.includes('已審核')}"
          >已審核
          </button>
          <button
            class="btn btn-outline-btngroup last font-weight-bold"
            @click.prevent="filterStatus = _.xor(filterStatus,['已作廢'])"
            :class="{'active' : filterStatus.includes('已作廢')}"
          >已作廢
          </button>
        </div>
      </div>

      <!-- filter button -->
      <button
        @click="startSearch"
        class="btn btn-sm btn-tablebtn filterButton font-weight-bold"
      >篩選
      </button>
    </div>
    <!-- excel -->
    <div class="pr-4 bg-white py-3">
      <excel :exportExcel="exportExcel" />
    </div>

    <!-- table -->
    <div class="table-responsive px-4 tableHeight pb-3 bg-white">
      <table class="table mb-0 table-striped ">
        <thead class=" text-light">
        <tr>
          <th class="text-center fz14 border-top-0 border-bottom-0 bg-tablehead">交易訂單編號</th>
          <th class="text-center fz14 border-top-0  border-bottom-0 bg-tablehead">票券編號</th>
          <th class="text-center fz14 border-top-0  border-bottom-0 bg-tablehead">票券商品名稱</th>
          <th
            class="text-center fz14 border-top-0  border-bottom-0 bg-tablehead"
            width="100"
          >票券條碼
          </th>
          <th class="text-center fz14 border-top-0  border-bottom-0 bg-tablehead">票券狀態</th>
          <!-- <th class="text-center fz14 border-top-0  border-bottom-0 bg-tablehead">統一編號</th> -->
          <th
            class="text-center fz14 border-top-0  border-bottom-0 bg-tablehead"
            width="80"
          >商品原價
          </th>
          <th
            class="text-center fz14 border-top-0  border-bottom-0 bg-tablehead"
            width="70"
          >信託額
          </th>
          <th class="text-center fz14 border-top-0  border-bottom-0 bg-tablehead">信託到期日</th>
          <th class="text-center fz14 border-top-0  border-bottom-0 bg-tablehead">付款資訊</th>
          <th
            class="text-center fz14 border-top-0  border-bottom-0 bg-darkOrange border-left"
            width="81"
          >請款狀態
          </th>
        </tr>
        </thead>
        <tbody class="bg-light">
        <tr
          class="fz14"
          v-for="(item) in listRows"
          :key="item[1]"
        >
          <td class="text-center">{{item[0]}}</td>
          <td class="text-center text-log font-weight-bold">
            <router-link
              class="text-decoration-none"
              :to="'/voucherdetailinfo/'+id+'/'+item[1]"
            >
              {{item[1]}}
            </router-link>
          </td>
          <td class="text-center">{{item[2]}}</td>
          <td class="text-center">{{item[3]}}</td>
          <td class="text-center">
            <div class="d-flex flex-column align-items-center">
                <span
                  v-if="item[4] === '未使用'"
                  class="bg-loglight text-log py-1 notUsed mb-2"
                >未使用</span>
              <span
                v-if="item[4] === '已審核'"
                class="reviewed mb-2 py-1"
              >已審核</span>
              <span
                v-if="item[4] === '已作廢'"
                class="py-1 cantUse mb-2"
              >已作廢</span>
              <span
                v-if="item[4] === '請款中'"
                class="py-1 applying mb-2"
              >請款中</span>
              <span
                v-if="item[4] === '已使用'"
                class=" py-1 used mb-2"
              >已使用</span>
              <!-- 狀態 -->
              <span v-if="item[4] === '未使用'">購票時間</span>
              <p v-if="item[4] === '未使用'" class="mb-0">
                <span v-for="(itm, idx) in 10">{{item[5][idx]}}</span>
              </p>
              <p v-if="item[4] === '未使用'" class="mb-0">
                <span v-for="(itm, idx) in 10">{{item[5][idx+10]}}</span>
              </p>
              <span v-if="item[4] === '已審核'">審核時間</span>
              <p v-if="item[4] === '已審核'" class="mb-0">
                <span v-for="(itm, idx) in 10">{{item[5][idx]}}</span>
              </p>
              <p v-if="item[4] === '已審核'" class="mb-0">
                <span v-for="(itm, idx) in 10">{{item[5][idx+10]}}</span>
              </p>
              <span v-if="item[4] === '已作廢'">作廢時間</span>
              <p v-if="item[4] === '已作廢'" class="mb-0">
                <span v-for="(itm, idx) in 10">{{item[5][idx]}}</span>
              </p>
              <p v-if="item[4] === '已作廢'" class="mb-0">
                <span v-for="(itm, idx) in 10">{{item[5][idx+10]}}</span>
              </p>
              <span v-if="item[4] === '請款中'">請款時間</span>
              <p v-if="item[4] === '請款中'" class="mb-0">
                <span v-for="(itm, idx) in 10">{{item[5][idx]}}</span>
              </p>
              <p v-if="item[4] === '請款中'" class="mb-0">
                <span v-for="(itm, idx) in 10">{{item[5][idx+10]}}</span>
              </p>
              <span v-if="item[4] === '已使用'">使用時間</span>
              <p v-if="item[4] === '已使用'" class="mb-0">
                <span v-for="(itm, idx) in 10">{{item[5][idx]}}</span>
              </p>
              <p v-if="item[4] === '已使用'" class="mb-0">
                <span v-for="(itm, idx) in 10">{{item[5][idx+10]}}</span>
              </p>
            </div>
          </td>
          <!-- <td class="text-center">
            <div class="d-flex justify-content-center align-items-center">
              <span class="text-log">{{item[6]}}</span>
              <span class="copy ml-1 d-flex align-items-center justify-content-center">
                <i class="fas fa-copy fa-xs text-log" />
              </span>
            </div>
          </td> -->
          <td class="text-center">{{item[7]}}</td>
          <td class="text-center">{{item[8]}}</td>
          <td class="text-center">
              <span
                v-if="item[10]"
                class="dueDate"
              >{{item[9]}}</span>
            <!-- <span
              v-if="item[10]"
              class="text-darkOrange"
            >止</span> -->
            <span
              v-if="!item[10]"
              class="passDueDate"
            >{{item[9]}} 已到期</span>
          </td>
          <td class="text-center">
            <div class="d-flex flex-column align-items-center justify-content-center ">
              <span
                class="paidInfo bg-loglight text-log d-flex align-items-center justify-content-center">{{item[11]}}</span>
              <span>豐收款交易編號 {{item[12]}}</span>
            </div>
          </td>
          <td class="text-center text-dark font-weight-bold">{{item[13]}}</td>
        </tr>
        </tbody>
      </table>
      <!-- 備註 -->
      <!-- <p class="font-weight-bold text-right fz14 mb-1">備註1：票券狀態之發行時間等同於消費者購票時間。</p>
      <p class="font-weight-bold text-right fz14 mb-0">備註2：票券條碼即商家核銷用之號碼。</p> -->
    </div>
    <!-- footer -->
    <footer class="container-fluid fixed-bottom bg-light">
      <pagination
        :perPage="perPage"
        :totalPage="totalPage"
        :currentPage="currentPage"
        :totalCount="totalCount"
        :pages="pages"
        :setCurrentPage="setCurrentPage"
        :prevOrNext="prevOrNext"
        :getList="getList"
        :startPage="startPage"
        :endPage="endPage"
        :setPerPage="setPerPage"
      />
    </footer>
  </div>
</template>

<script>
  import excel from "./excel";
  import {mapActions, mapGetters, mapState} from "vuex";
  import {mapFields} from "vuex-map-fields";
  import pagination from "./pagination";

  export default {
    components: {
      excel,
      pagination
    },
    data() {
      return {
        msg: "Loading",
        html: '<i class="fa fa-cog fa-spin fa-3x fa-fw"/>',
        id: 0
      };
    },
    computed: {
      ...mapState("voucherStoreListDetail", [
        "perPage",
        "totalPage",
        "currentPage",
        "totalCount",
        "listRows"
      ]),
      ...mapGetters("voucherStoreListDetail", ["pages", "startPage", "endPage"]),
      ...mapFields("voucherStoreListDetail", [
        "searchText",
        "searchStartDate",
        "searchEndDate",
        "filterStatus"
      ]),
      ...mapState("ui", ["isLoading"])
    },
    methods: {
      ...mapActions("voucherStoreListDetail", [
        "setCurrentPage",
        "prevOrNext",
        "getList",
        "setPerPage",
        "exportExcel",
        "startSearch"
      ])
    },
    mounted() {
      this.id = this.$route.params.id;
      this.searchText = ''
      this.searchStartDate = ''
      this.searchEndDate = ''
      this.filterStatus = []
      this.setCurrentPage({page: 1})
      this.getList();
    }
  };
</script>

<style lang="scss" scoped>
  .voucherstorelistdetail {
    .headerUi {
      background: transparent linear-gradient(89deg, #1b79f4 0%, #27489a 53%, #3b44a9 100%) 0% 0% no-repeat padding-box;
    }

    .arrowLeftLayout {
      width: 33px;
      height: 33px;
      box-sizing: border-box;
      background: rgba(256, 256, 256, 0.4);
      border-radius: 50%;
    }

    .fz14 {
      font-size: 14px;
      line-height: 14px;
    }

    .fz12 {
      font-size: 12px;
      line-height: 12px;
    }

    // search bar
    .searchBar {
      padding-left: 24px;
      padding-right: 24px;
      box-sizing: border-box;
      background: #f7f7f7;
      border-bottom: 1px solid #e8e7e7;
      box-shadow: 0px 3px 6px #efecec;
    }

    .filterHeightControl {
      // width: 360px;
      height: 45px;
      box-sizing: border-box;
      border-top-left-radius: 25px;
      border-bottom-left-radius: 25px;

      &::placeholder {
        color: #c1c1c1;
      }
    }

    .input-group > .input-group-append > .btn {
      border-top-right-radius: 25px;
      border-bottom-right-radius: 25px;
      background: white;

      &:hover {
        background: #1b79f4;
        color: white;
      }
    }

    // input date
    .dateWidthControl {
      width: 160px;
      box-sizing: border-box;
      border-radius: 25px;
      height: 45px;
      color: #c1c1c1;
    }

    // btn-group
    .btn-group {
      .btn {
        width: 100px;
        box-sizing: border-box;

        &.active,
        &:hover {
          background: #3343f7;
          color: white;
        }
      }
    }

    .btn-group > .btn:first-child {
      border-top-left-radius: 15px;
      border-bottom-left-radius: 15px;
    }

    .btn-group > .btn:last-child {
      border-top-right-radius: 15px;
      border-bottom-right-radius: 15px;
    }

    .filterButton {
      width: 80px;
      box-sizing: border-box;
      height: 45px;
      // margin-left: 16px;
    }

    // table
    .tableHeight {
      height: calc(100vh - 65px - 93px - 62px - 50px);
      overflow-y: scroll;

      &::-webkit-scrollbar {
        display: none;
      }
    }

    thead {
      display: block;
      width: 100%;

      tr {
        width: 100%;
        display: flex;

        th {
          height: 45px;
          vertical-align: middle;
          font-weight: bold;
          // width: calc(100% / 10);
          display: flex;
          align-items: center;
          justify-content: center;
        }

        // 各表頭寬度設定
        th:nth-child(1) {
          width: 20%;
        }

        th:nth-child(2) {
          width: 5%;
        }

        th:nth-child(3) {
          width: 20%;
        }

        th:nth-child(4) {
          width: 20%;
        }

        th:nth-child(5) {
          width: 7%
        }

        th:nth-child(6) {
          width: 5%
        }

        th:nth-child(7) {
          width: 5%;
        }

        th:nth-child(8) {
          width: 7%;
        }

        th:nth-child(9) {
          width: 8%;
        }

        th:nth-child(10) {
          width: 8%;
        }
      }
    }

    td,
    th {
      vertical-align: middle;
    }

    thead > tr > :first-child {
      border-top-left-radius: 5px;
    }

    thead > tr > :last-child {
      border-top-right-radius: 5px;
    }

    tbody {
      display: block;
      overflow: auto;
      height: 590px;
      width: 100%;

      &::-webkit-scrollbar {
        display: none;
      }

      &:last-child {
        border-bottom: 1px solid #dee2e6;
      }

      tr {
        width: 100%;
        display: flex;

        td {
          height: 100px;
          display: flex;
          align-items: center;
          justify-content: center;
          // width: calc(100% / 10);
          font-size: 14px;
        }

        // 各td寬度設定
        td:nth-child(1) {
          width: 20%;
        }

        td:nth-child(2) {
          width: 5%;
        }

        td:nth-child(3) {
          width: 20%;
        }

        td:nth-child(4) {
          width: 20%;
        }

        td:nth-child(5) {
          width: 7%
        }

        td:nth-child(6) {
          width: 5%
        }

        td:nth-child(7) {
          width: 5%;
        }

        td:nth-child(8) {
          width: 7%;
        }

        td:nth-child(9) {
          width: 8%;
        }

        td:nth-child(10) {
          width: 8%;
        }
      }
    }

    td {
      font-size: 14px;

      .notUsed {
        padding-top: 2px;
        width: 90px;
        box-sizing: border-box;
        padding-bottom: 2px;
        border-radius: 3px;
        font-weight: bold;

        & + span {
          color: #555151;
        }
      }

      .reviewed {
        padding: 2px 4px;
        width: 90px;
        border-radius: 4px;
        box-sizing: border-box;
        color: #33c666;
        background: rgba(51, 198, 102, 0.15);
        font-weight: bold;

        & + span {
          color: #555151;
        }
      }

      .dueDate {
        background: #f45029;
        padding: 2px 4px;
        color: white;
        border-radius: 4px;
        box-sizing: border-box;
        font-weight: bold;
      }

      .passDueDate {
        color: #555151;
        padding: 2px 4px;
        box-sizing: border-box;
        border-radius: 4px;
        background: rgba(0, 0, 0, 0.13);
        font-weight: bold;
      }

      .cantUse {
        padding: 2px 4px;
        box-sizing: border-box;
        border-radius: 4px;
        color: #838080;
        background: rgba(131, 128, 128, 0.15);
        width: 90px;
        font-weight: bold;

        & + span {
          color: #555151;
        }
      }

      .used {
        background: rgba(110, 101, 222, 0.15);
        color: #6e65de;
        padding: 2px 4px;
        box-sizing: border-box;
        border-radius: 4px;
        width: 90px;
        font-weight: bold;

        & + span {
          color: #555151;
        }
      }

      .applying {
        color: #f75029;
        background: rgba(244, 80, 14, 0.15);
        padding-top: 2px;
        width: 90px;
        box-sizing: border-box;
        padding-bottom: 2px;
        border-radius: 3px;
        font-weight: bold;

        & + span {
          color: #555151;
        }
      }
    }

    span.copy {
      width: 20px;
      height: 20px;
      background: rgba(193, 193, 193, 0.5);
      cursor: pointer;
      border-radius: 1px;

      &:hover {
        background: rgba(193, 193, 193, 1);
      }

      transition: all 0.5s;
    }

    // 付款資訊
    .paidInfo {
      width: 90px;
      height: 22px;
      background: rgba(103, 227, 108, 0.15);
      color: #33c666;
      box-sizing: border-box;
      // padding-top: 3px;
      border-radius: 3px;
      margin-bottom: 8px;
      font-weight: bold;

      & + span {
        color: #c1c1c1;
      }
    }
  }

  // footer
  .footer {
    height: 50px;
    box-sizing: border-box;

    .form-control {
      width: 80px;
      height: 30px;
      font-size: 14px;
    }
  }
</style>



