<template>
  <div class="edit">
    <BlockUI
      v-if="isLoading"
      :message="msg"
      :html="html"
    />
    <div class="container-fluid">
      <div class="row py-3 headerUi px-40 align-items-center">
        <router-link
          class="text-decoration-none"
          to="/admin/merchantdatabuild"
        >
          <div class="arrowLeftLayout d-flex align-items-center justify-content-center">
            <i class="fas fa-arrow-left text-tablebtn" />
          </div>
        </router-link>
        <p class="mb-0 ml-3 text-light font-weight-bold">返回</p>
        <p class="mb-0 ml-auto text-light font-weight-bold">新增商家資料</p>
      </div>
      <!-- 存檔按鈕 -->
      <div
        v-if="editable"
        class="row align-items-center h76 px-40 py-4 save"
      >
        <button
          @click="saveAll"
          v-if="section_1 || section_2 || section_3 || section_4"
          class="btn btn-headSaveBtn font-weight-bold"
          :disabled="yfp_store_trust_length_mon<12"
        >存檔
        </button>
        <button
          @click="section_1 = true;section_2 = true;section_3 = true;section_4 = true;"
          v-if="!section_1 && !section_2 && !section_3 && !section_4"
          class="btn btn-edit bg-light border-0 text-log font-weight-bold"
        ><i class="fas fa-pencil-alt mr-3" />繼續編輯
        </button>
      </div>
      <!-- 主管才看的到的狀態 -->
      <div
        class="row py-3 managerTopContentBox"
        v-if="manager"
      >
        <div class="managerTopContent">
          <p class="title">發券資格</p>
          <table class="table">
            <thead class="d-block w-100">
              <tr class="d-flex w-100">
                <th class="fz14 first border-bottom-0 w-25">發行公司名稱</th>
                <th class="fz14 border-bottom-0 w-25">信託專戶客戶代號</th>
                <th class="fz14 border-bottom-0 w-25">建立日期</th>
                <th class="fz14 last border-bottom-0 w-25">發券資格</th>
              </tr>
            </thead>
            <tbody class="w-100 d-block">
              <tr class="d-flex w-100">
                <td class="text-center fz14 first w-25 d-flex align-items-center justify-content-center">{{yfp_store_title}}</td>
                <td class="text-center fz14 w-25 d-flex align-items-center justify-content-center">{{yfp_store_id}}</td>
                <td class="text-center fz14 w-25 d-flex align-items-center justify-content-center">{{yfp_store_created_at}}</td>
                <td class="text-center last w-25">
                  <p
                    v-if="yfp_store_status === '審核中'"
                    class="text-darkOrange fz14 font-weight-bold mb-0"
                  >待審核</p>
                  <p
                    v-if="yfp_store_status === '已審核'"
                    class="text-review fz14 font-weight-bold mb-0 d-flex align-items-center justify-content-center h-100"
                  ><i class="far fa-check-circle mr-3"></i>通過</p>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <!-- main content -->
      <div class="row main flex-column">
        <div class="firstBox">
          <div class="boxHeader d-flex align-items-center">
            <p class="text-light mb-0 ml-4 font-weight-bold">商家基本資料</p>
            <p class="remind mb-0 d-flex align-items-center">
              <span class="redMark"></span>
              <span class="ml-3 font-weight-bold">為必填欄位</span>
            </p>
            <button
              v-if="section_1 && editable"
              @click="save1"
              class="btn btn-headSaveBtn ml-auto mr-3 p-0 font-weight-bold"
            >儲存
            </button>
            <button
              @click="section_1 = !section_1"
              v-if="!section_1 && editable"
              class="btn btn-edit bg-light text-log border-log mr-3 ml-auto p-0 font-weight-bold"
            >
              編輯
            </button>
          </div>
          <div class="boxBody d-flex">
            <div class="left w-50">
              <div class="d-flex align-items-center mb-20">
                <span class="font-weight-bold">商家名稱</span>
                <input
                  type="text"
                  class="form-control disabled"
                  :class="{'border-0' : !section_1,'bg-white' : !section_1,'textDisabled' : !section_1}"
                  disabled
                  :placeholder="yfp_store_title"
                >
              </div>
              <div class="d-flex align-items-center mb-20">
                <span class="font-weight-bold">統一編號</span>
                <input
                  type="text"
                  class="form-control disabled "
                  disabled
                  :placeholder="yfp_store_vat"
                  :class="{'border-0' : !section_1,'bg-white' : !section_1,'textDisabled' : !section_1}"
                >
              </div>
              <div class="d-flex align-items-center mb-20">
                <span class="font-weight-bold">商家代號</span>
                <input
                  type="text"
                  class="form-control disabled"
                  disabled
                  :placeholder="yfp_store_code"
                  :class="{'border-0' : !section_1,'bg-white' : !section_1,'textDisabled' : !section_1}"
                >
              </div>
              <div class="d-flex align-items-center mb-5">
                <span class="font-weight-bold">客戶信託編號</span>
                <input
                  type="text"
                  v-model="yfp_store_customer_trust_number"
                  :disabled="!section_1"
                  v-bind:class="{ disabled: !section_1,'bg-white': !section_1,'border-0': !section_1,'textDisabled': !section_1}"
                  class="form-control clientTurstNum border-log"
                >
              </div>
            </div>
            <div class="right w-50">
              <div class="d-flex align-items-center mb-20">
                <span class="font-weight-bold">發行系統</span>
                <input
                  type="text"
                  class="form-control disabled"
                  disabled
                  placeholder="永豐紙業電子票券平台（預設）"
                  :class="{'border-0' : !section_1,'bg-white' : !section_1,'textDisabled' : !section_1}"
                >
              </div>
              <div class="d-flex align-items-center mb-20">
                <span class="font-weight-bold">發行票券商註記</span>
                <input
                  type="text"
                  class="form-control disabled"
                  disabled
                  placeholder="發行票券商（預設）"
                  :class="{'border-0' : !section_1,'bg-white' : !section_1,'textDisabled' : !section_1}"
                >
              </div>
              <div class="d-flex align-items-center mb-20">
                <span class="font-weight-bold">可發行之票券類型</span>
                <input
                  type="text"
                  class="form-control disabled"
                  disabled
                  placeholder="電子自銷（預設）"
                  :class="{'border-0' : !section_1,'bg-white' : !section_1,'textDisabled' : !section_1}"
                >
              </div>
              <div class="d-flex align-items-center mb-5">
                <span class="font-weight-bold">豐收款商店代號</span>
                <input
                  type="text"
                  v-model="yfp_store_customer_business_code"
                  :disabled="!section_1"
                  v-bind:class="{ disabled: !section_1,'bg-white': !section_1,'border-0': !section_1,'textDisabled': !section_1}"
                  class="form-control border-log"
                >

              </div>
            </div>
          </div>
        </div>
        <!-- second box -->
        <div class="secondBox mt-34">
          <div class="boxHeader d-flex align-items-center">
            <p class="text-light mb-0 ml-4 font-weight-bold">負責人資料</p>
          </div>
          <div class="boxBody d-flex border-bottom-0">
            <div class="left w-50">
              <div class="d-flex align-items-center mb-20">
                <span class="font-weight-bold">公司負責人</span>
                <input
                  type="text"
                  class="form-control disabled"
                  disabled
                  :placeholder="yfp_store_manager"
                >
              </div>
              <div class="d-flex align-items-center mb-20">
                <span class="font-weight-bold">負責人職稱</span>
                <input
                  type="text"
                  class="form-control  disabled"
                  disabled
                  :placeholder="yfp_store_touch_title"
                >
              </div>
              <div class="d-flex align-items-center mb-20">
                <span class="font-weight-bold">手機號碼</span>
                <input
                  type="tel"
                  class="form-control  disabled"
                  disabled
                  :placeholder="yfp_store_touch_mobil"
                >
              </div>
              <!-- <div class="d-flex align-items-center mb-5">
                <span class="font-weight-bold">身分證字號</span>
                <input
                  type="text"
                  class="form-control disabled"
                  disabled
                  :placeholder="yfp_store_touch_id"
                >
              </div> -->
            </div>
            <div class="right w-50">
              <div class="d-flex align-items-center mb-20">
                <span class="font-weight-bold">公司地址</span>
                <input
                  type="text"
                  class="form-control disabled"
                  disabled
                  :placeholder="yfp_store_address"
                >
              </div>
              <div class="d-flex align-items-center mb-20">
                <span class="font-weight-bold">電子信箱</span>
                <input
                  type="text"
                  class="form-control disabled"
                  disabled
                  :placeholder="yfp_store_touch_mail"
                >
              </div>
              <div class="d-flex align-items-center mb-20">
                <span class="font-weight-bold">公司電話</span>
                <input
                  type="text"
                  class="form-control disabled"
                  disabled
                  :placeholder="yfp_store_tel"
                >
              </div>
              <div class="d-flex align-items-center mb-5">
                <span class="font-weight-bold">傳真電話</span>
                <input
                  :placeholder="yfp_store_touch_fax"
                  type="text"
                  class="form-control disabled"
                  disabled
                >
              </div>
            </div>
          </div>
          <div class="boxFooter d-flex border-top-0 border-bottom-0">
            <div class="h40 bg-log d-flex align-items-center justify-content-center">
              <span class="text-light fz14">系統窗口負責人</span>
            </div>
            <input
              :value="yfp_store_touch_man"
              type="text"
              class="form-control border-top-0 border-bottom-0 disabled"
              disabled
            />
            <div class="h40 bg-log d-flex align-items-center justify-content-center">
              <span class="text-light fz14">窗口聯絡電話</span>
            </div>
            <input
              :value="yfp_store_touch_tel"
              type="text"
              class="form-control border-top-0 border-bottom-0 disabled"
              disabled
            />
          </div>
        </div>
        <!-- third box -->
        <div class="thirdBox mt-34">
          <div class="boxHeader d-flex align-items-center">
            <p class="text-light mb-0 ml-4 font-weight-bold">信託相關資料</p>
            <p class="remind mb-0 d-flex align-items-center">
              <span class="redMark"></span>
              <span class="ml-3 font-weight-bold">為必填欄位</span>
            </p>
            <button
              v-if="section_2  && editable"
              @click="save2"
              :disabled="yfp_store_trust_length_mon<12"
              class="btn btn-headSaveBtn ml-auto mr-3 p-0 font-weight-bold"
            >儲存
            </button>
            <button
              @click="section_2 = !section_2"
              v-if="!section_2  && editable"
              class="btn btn-edit bg-light text-log border-log mr-3 ml-auto p-0 font-weight-bold"
            >
              編輯
            </button>
          </div>
          <div class="boxBody d-flex">
            <div class="left w-50">
              <div class="d-flex align-items-center mb-20">
                <span class="font-weight-bold forwardRedBox">信託專戶帳號</span>
                <input
                  type="text"
                  class="form-control border-log bg-white required text-right"
                  v-model="yfp_store_trust_number"
                  placeholder="000-000-000-000"
                  :disabled="!section_2"
                  v-bind:class="{ disabled: !section_2,'bg-white': !section_2,'border-0': !section_2,'text-dark': !section_2}"
                  required
                >
              </div>
              <div class="d-flex align-items-center mb-5">
                <span class="font-weight-bold forwardRedBox">信託管理費率</span>
                <input
                  type="text"
                  v-model="yfp_store_trust_par"
                  class="form-control border-log text-right bg-white required mr-3"
                  placeholder="00.00%"
                  :disabled="!section_2"
                  v-bind:class="{ disabled: !section_2,'bg-white': !section_2,'border-0': !section_2,'textDisabled': !section_2}"
                  required
                >%
              </div>
            </div>
            <div class="right w-50">
              <div class="d-flex align-items-center mb-20">
                <span class="font-weight-bold forwardRedBox">信託期間長度</span>
                <ValidationProvider name="信託期間長度" rules="required|min_value:12" v-slot="{ errors }">
                  <input
                    type="number"
                    class="form-control border-log text-right bg-white required"
                    placeholder="12(預設)"
                    min="12"
                    v-model="yfp_store_trust_length_mon"
                    :disabled="!section_2"
                    v-bind:class="{ disabled: !section_2,'bg-white': !section_2,'border-0': !section_2,'textDisabled': !section_2}"
                    required
                  >
                  <span class="text-danger fz14 d-block w-100 text-right">{{ errors[0] }}</span>
                </ValidationProvider>
              </div>
              <div class="d-flex align-items-center mb-5">
                <span class="font-weight-bold forwardRedBox">信託契約期間</span>
                <input
                  type="date"
                  v-model="yfp_store_contract_from"
                  class="form-control border-log text-dark text-right pl-0 pr-1 bg-white required startDate"
                  :disabled="!section_2"
                  v-bind:class="{ disabled: !section_2,'bg-white': !section_2,'border-0': !section_2,'textDisabled': !section_2,'bgNone': !section_2}"
                  required
                >
                <span v-if="!section_2">~</span>
                <input
                  type="date"
                  v-model="yfp_store_contract_to"
                  class="form-control border-log text-dark text-right pl-0 pr-1 ml-0 bg-white required"
                  :disabled="!section_2"
                  v-bind:class="{ disabled: !section_2,'bg-white': !section_2,'border-0': !section_2,'textDisabled': !section_2,'bgNone': !section_2}"
                  required
                >
              </div>
            </div>
          </div>
        </div>
        <!-- fourth box -->
        <div class="fourthWidth d-flex">
          <div class="fourthBox mt-34 ">
            <div class="boxHeader bg-darkOrange d-flex align-items-center">
              <p class="text-light mb-0 ml-4 font-weight-bold">請款資訊</p>
              <p class="remind mb-0 d-flex align-items-center">
                <span class="redMark"></span>
                <span class="ml-3 font-weight-bold">為必填欄位</span>
              </p>
              <button
                v-if="section_3  && editable"
                @click="save3"
                class="btn btn-headSaveBtn ml-auto mr-3 p-0 font-weight-bold"
              >儲存
              </button>
              <button
                @click="section_3 = !section_3"
                v-if="!section_3 && editable"
                class="btn btn-edit bg-light text-log border-log mr-3 ml-auto p-0 font-weight-bold"
              >
                編輯
              </button>
            </div>
            <div class="boxBody pb-5">
              <div class="d-flex align-items-center">
                <span class="font-weight-bold">銀行戶頭名稱</span>
                <input
                  type="text"
                  v-model="yfp_store_bank_name"
                  class="form-control border-log  text-left bg-white required"
                  :disabled="!section_3"
                  v-bind:class="{ disabled: !section_3,'bg-white': !section_3,'border-0': !section_3,'textDisabled': !section_3}"
                  required
                >
              </div>
              <div class="d-flex align-items-center mt-20">
                <span class="font-weight-bold">請款匯入銀行代號</span>
                <select
                  type="text"
                  v-model="yfp_store_apply_bank"
                  class="form-control border-log  text-left bg-white required d-flex align-items-center"
                  placeholder="銀行代號"
                  :disabled="!section_3"
                  v-bind:class="{ disabled: !section_3,'bg-white': !section_3,'border-0': !section_3,'textDisabled': !section_3}"
                  required
                >
                  <option
                    value=""
                    disabled
                    selected
                  >請選擇
                  </option>
                  <option
                    :value="item.code"
                    v-for="(item) in bankJson"
                    :key="item.code"
                  >
                    {{item.code}} {{item.name}}
                  </option>
                </select>
              </div>
              <div class="d-flex align-items-center mt-20">
                <span class="font-weight-bold">請款匯入銀行帳號</span>
                <input
                  :disabled="!section_3"
                  v-bind:class="{ disabled: !section_3,'bg-white': !section_3,'border-0': !section_3,'textDisabled': !section_3}"
                  v-model="yfp_store_bank_number"
                  type="text"
                  class="form-control border-log  text-left bg-white required"
                  required
                >
              </div>
            </div>
          </div>
          <div class="fifthBox mt-34">
            <div class="boxHeader  d-flex align-items-center">
              <p class="text-light mb-0 ml-4 font-weight-bold">費率詳細資訊</p>
              <p class="remind mb-0 d-flex align-items-center">
                <span class="redMark"></span>
                <span class="ml-3 font-weight-bold">為必填欄位</span>
              </p>
              <button
                v-if="section_4 && editable"
                @click="save4"
                class="btn btn-headSaveBtn ml-auto mr-3 p-0 font-weight-bold"
              >儲存
              </button>
              <button
                @click="section_4 = !section_4"
                v-if="!section_4 && editable"
                class="btn btn-edit bg-light text-log border-log mr-3 ml-auto p-0 font-weight-bold"
              >
                編輯
              </button>
            </div>
            <div class="boxBody  pl-4">
              <div class="d-flex align-items-center">
                <span class="font-weight-bold">永紙收取費率</span>
                <input
                  type="text"
                  class="form-control border-log text-dark text-right bg-light ml-34 required bg-white"
                  placeholder="00.00%"
                  v-model="yfp_store_atm_par"
                  :disabled="!section_4"
                  v-bind:class="{ disabled: !section_4,'bg-white': !section_4,'border-0': !section_4,'textDisabled': !section_4}"
                  required
                >%
              </div>
              <div class="d-flex align-items-center mt-20">
                <span class="font-weight-bold">信用卡收單費率</span>
                <input
                  type="text"
                  class="form-control border-log text-dark text-right bg-light required bg-white"
                  placeholder="00.00%"
                  v-model="yfp_store_card_par"
                  :disabled="!section_4"
                  v-bind:class="{ disabled: !section_4,'bg-white': !section_4,'border-0': !section_4,'textDisabled': !section_4}"
                  required
                >%
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="row justify-content-center align-items-center footer">
        <button
          @click="saveAll"
          v-if="section_1 || section_2 || section_3 || section_4 && editable"
          class="btn btn-tempSave btn-headSaveBtn"
          :disabled="yfp_store_trust_length_mon<12"
        >暫存
        </button>
        <button
          @click="apply"
          v-if="!section_1 && !section_2 && !section_3 && !section_4 && editable"
          class="btn btn-sendReview"
        >
          送審
        </button>
        <button
          @click="pass"
          v-if="manager && yfp_store_status === '審核中'"
          class="btn btn-tempSave btn-headSaveBtn"
        >
          通過
        </button>
        <button
          data-toggle="modal"
          data-target="#sureSendReview"
          v-if="manager && yfp_store_status === '審核中'"
          class="btn btn-sendReview ml-8"
        >
          駁回
        </button>
      </div>
    </div>

    <!-- Modal -->
    <div
      class="modal fade"
      id="sureSendReview"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div
        class="modal-dialog"
        role="document"
      >
        <div class="modal-content">
          <div class="modal-header justify-content-center">
            <h5 class="modal-title font-weight-bold">請填寫駁回原因</h5>
          </div>
          <div class="modal-body d-flex flex-column justify-content-center px-30">
            <p class="font-weight-bold mb-3">原因</p>
            <textarea
              v-model="yfp_store_reject_message"
              cols="30"
              rows="10"
              class="form-control"
            ></textarea>
          </div>
          <div class="modal-footer d-flex justify-content-around border-top-0">
            <button
              type="button"
              class="btn btn-outline-log"
              data-dismiss="modal"
            >取消
            </button>
            <button
              type="button"
              class="btn btn-log"
              data-dismiss="modal"
              @click="reject"
            >送出
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import bankJson from "../../assets/js/bank.json";
  import { mapFields } from "vuex-map-fields";
  import { mapActions, mapState } from "vuex";

  export default {
    data() {
      return {
        bankJson,
        msg: "Loading",
        html: '<i class="fa fa-cog fa-spin fa-3x fa-fw"/>'
      };
    },
    methods: {
      ...mapActions("edit", [
        "init",
        "save1",
        "save2",
        "save3",
        "save4",
        "saveAll",
        "apply",
        "pass",
        "reject"
      ])
    },
    computed: {
      editable() {
        return (
          (this.yfp_store_status === "未送審" ||
            this.yfp_store_status === "退回") &&
          this.employee
        );
      },
      auditability() {
        return this.yfp_store_status === "審核中" && this.manager;
      },
      ...mapFields("edit", [
        "section_1",
        "section_2",
        "section_3",
        "section_4",
        "id",
        "yfp_store_id",
        "yfp_store_created_at",
        "yfp_store_title",
        "yfp_store_vat",
        "yfp_store_code",
        "yfp_store_customer_business_code",
        "yfp_store_manager",
        "yfp_store_touch_id",
        "yfp_store_touch_title",
        "yfp_store_address",
        "yfp_store_touch_mail",
        "yfp_store_tel",
        "yfp_store_touch_fax",
        "yfp_store_trust_number",
        "yfp_store_trust_par",
        "yfp_store_trust_length_mon",
        "yfp_store_contract_from",
        "yfp_store_contract_to",
        "yfp_store_bank_name",
        "yfp_store_apply_bank",
        "yfp_store_bank_number",
        "yfp_store_atm_par",
        "yfp_store_card_par",
        "yfp_store_customer_trust_number",
        "yfp_store_touch_mobil",
        "yfp_store_touch_man",
        "yfp_store_touch_tel",
        "yfp_store_status",
