<template>
  <div>
    <BlockUI
      v-if="isLoading"
      :message="msg"
      :html="html"
    />
    <div class="typer">
      <vue-typer
        text="Welcome Back   ; -)"
        :repeat="Infinity"
        :pre-type-delay="1000"
      />
    </div>
    <div class="loginInput">
      <h4 class="login text-white text-center mb-40">Log In</h4>
      <input
        type="text"
        class="form-control mb-4"
        id="id"
        required
        v-model="name"
        placeholder="輸入帳號"
      />
      <input
        type="password"
        v-model="password"
        class="form-control mb-3"
        id="password"
        required
        placeholder="輸入密碼"
        @keyup.enter="handleSubmit"
      />
      <div class="text-right mb-3">
        <router-link
          to="/forgetpassword"
          class="forgetPassword font-weight-bold text-decoration-none"
        >Forgot password?
        </router-link>
      </div>
      <div
        v-if="errMsg"
        class="alert"
      >
              <span
                class="closebtn"
                @click="resetErrMsg"
              >&times;</span>
        <div v-html="errMsg"></div>
      </div>
      <button
        type="button"
        @click="handleSubmit"
        class="btn btn-sm btn-light text-log font-weight-bold btn-lg btn-block loginBtn"
      >
        登入
      </button>
    </div>
  </div>
</template>

<script>
  import {mapState, mapActions} from 'vuex';

  export default {
    data() {
      return {
        name: "",
        password: "",
        msg: "Loading",
        html: '<i class="fa fa-cog fa-spin fa-3x fa-fw"/>'
      }
    },
    computed: {
      ...mapState("ui", ["errMsg", "isLoading"])
    },
    mounted() {
      this.resetErrMsg()
    },
    methods: {
      ...mapActions("account", ["login"]),
      ...mapActions("ui", ["resetErrMsg"]),
      handleSubmit() {
        const {name, password} = this;
        this.resetErrMsg();
        this.login({name, password});
      }
    }
  }
</script>

<style lang="scss" scoped>

  // type effect
  .typer {
    width: 35vw;
    margin: 16vh auto 15px;
    font-size: 28px;
    font-weight: bold;
  }

  .vue-typer .custom.char.typed {
    color: white;
  }

  .loginInput {
    background: rgba(0, 0, 0, 0.2);
    box-shadow: 0px 0px 14px #00000029;
    border-radius: 8px;
    opacity: 1;
    padding-top: 50px;
    padding-left: 64px;
    padding-right: 64px;
    padding-bottom: 50px;
    width: 700px;
    box-sizing: border-box;
    margin: 0 auto;
    // margin-top: 10vh;
  }

  .forgetPassword {
    color: #ff5b34;
    font-style: italic;
  }

  .form-control {
    border: 1px solid #3b579d;
  }

  .alert {
    padding: 20px;
    background-color: #f44336; /* Red */
    color: white;
    margin-bottom: 15px;
  }

  /* The close button */
  .closebtn {
    margin-left: 15px;
    color: white;
    font-weight: bold;
    float: right;
    font-size: 22px;
    line-height: 20px;
    cursor: pointer;
    transition: 0.3s;
  }

  /* When moving the mouse over the close button */
  .closebtn:hover {
    color: black;
  }

  // input pic
  #shopNum {
    padding-left: 30px;
    background-image: url("../../assets/img/login_shop.png");
    background-repeat: no-repeat;
    background-position: right 30px center;
    background-color: rgba(0, 0, 0, 0.2);
    color: rgb(256, 256, 256);
    border: 0;
    height: 60px;
    box-sizing: border-box;
    border-radius: 30px;
  }

  #id {
    padding-left: 30px;
    background-image: url("../../assets/img/login_member.png");
    background-repeat: no-repeat;
    background-position: right 30px center;
    background-color: rgba(0, 0, 0, 0.2);
    color: rgb(256, 256, 256);
    border: 0;
    height: 60px;
    box-sizing: border-box;
    border-radius: 30px;
  }

  #password {
    padding-left: 30px;
    background-image: url("../../assets/img/login_password.png");
    background-repeat: no-repeat;
    background-position: right 30px center;
    background-color: rgba(0, 0, 0, 0.2);
    color: rgb(256, 256, 256);
    border: 0;
    height: 60px;
    box-sizing: border-box;
    border-radius: 30px;
  }

  //login button
  .loginBtn {
    // height: calc(1.5em + 0.75rem + 5px);
    height: 60px;
    box-sizing: border-box;
    border-radius: 30px;
    padding: 6px 0;
    font-size: 16px;
  }

  .form-control::-webkit-input-placeholder {
    /* Chrome/Opera/Safari */
    color: rgba(256, 256, 256, 0.4);
  }
</style>



