<template>
  <!-- 新增角色Modal -->
  <div class="modal fade" id="addCharacterModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel"
       aria-hidden="true">
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header justify-content-center">
          <h5 class="modal-title font-weight-bold text-dark" id="exampleModalLabel">新增角色</h5>
        </div>
        <div class="modal-body">
          <p class="mb-0 d-flex align-items-center">
            <span class="text-danger">*</span>
            <span class="text-primary font-weight-bold">輸入角色名稱</span>
          </p>
          <input type="text" class="form-control my-2" v-model="name">
          <p class="text-page fz14 text-right">{{name.length}}字數</p>
        </div>
        <div class="modal-footer justify-content-around">
          <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
          <button @click="submit" type="button" class="btn btn-log">確定新增</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {mapActions} from "vuex";
  import {mapFields} from "vuex-map-fields";

  export default {
    computed: {
      ...mapFields("addcharactermodal", [
        "name",
      ]),
    },
    methods: {
      ...mapActions("addcharactermodal", [
        "submit",
      ])
    },
  }
</script>

<style lang="scss" scoped>
  .fz14 {
    font-size: 14px;
  }

  .modal-dialog {
    max-width: 650px;
  }

  .modal-content {
    max-width: 650px;
  }

  .modal-body {
    p {
      &:first-child {
        font-size: 20px;
        margin-top: 50px;
      }
    }

    input[type="text"] {
      height: 54px;
      box-sizing: border-box;
      border-radius: 2px;
    }
  }

  .modal-footer .btn {
    width: 100px;
    height: 40px;
    box-sizing: border-box;
    border-radius: 22px;
    border-width: 2px;
  }
</style>



