<template>
  <div class="productlist">
    <PSB :getList="getList"/>
    <div class="container-fluid detailContent bg-white pt-0">
      <div class="row px-40">
        <p class="titleFontDetail text-primary font-weight-bold my-3 ml-4 d-flex w-100 align-items-end">
          架上商品列表 ({{listRows.filter(x => x[9]).length}}/{{listRows.length}})
          <span class="text-danger notify ml-auto">＊上架商品等同於銷售中的商品。</span>
        </p>
        <div class="table-responsive px-4">
          <table class="table border-bottom border-left border-right">
            <thead class="thead">
            <tr>
              <td class="bg-tablehead text-center border-top-0 text-white "></td>
              <td class="bg-tablehead text-center border-top-0 text-white ">圖片</td>
              <td class="bg-tablehead text-center border-top-0 text-white ">商品</td>
              <td class="bg-tablehead text-center border-top-0 text-white ">發行價格</td>
              <td class="bg-tablehead text-center border-top-0 text-white ">剩餘數量/總數量</td>
              <td class="bg-tablehead text-center border-top-0 text-white ">上架時間</td>
              <td class="bg-tablehead text-center border-top-0 text-white ">下架時間</td>
              <td class="bg-tablehead text-center border-top-0 text-white ">審核狀態</td>
              <td class="bg-tablehead text-center border-top-0 text-white ">票券上架狀態</td>
              <td class="bg-tablehead text-center border-top-0 text-white "></td>
            </tr>
            </thead>
            <tbody>
            <tr v-for="(item,i) in listRows">
              <td class="text-center">{{i+1}}</td>
              <td class="text-center">
                <img :src="item[1]" alt="">
              </td>
              <td class="text-center">{{item[2]}}</td>
              <td class="text-center">{{item[5]}}</td>
              <td v-if="item[3] > 0" class="text-center">{{item[4]}}/{{item[3]}}</td>
              <td v-if="item[3] === 0" class="text-center"> -</td>
              <td class="text-center">{{item[6]}}</td>
              <td class="text-center">{{item[7]}}</td>
              <td class="text-center">{{item[8]}}</td>
              <td v-if="item[9]" class="text-center">上架</td>
              <td v-if="!item[9]" class="text-center">下架</td>
              <td class="text-center">
                <router-link :to="'/admin/addtrustticketpage/'+item[0]"
                             class="text-decoration-none text-log font-weight-bold">
                  <button class="btn btn-search">
                    <i class="fas fa-search"></i>
                  </button>
                </router-link>
              </td>
            </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import PSB from './productsearchbar'
  import {mapActions, mapState} from "vuex";

  export default {
    components: {
      PSB,
    },
    computed: {
      ...mapState("productlist", [
        "listRows",
      ]),
    },
    methods: {
      ...mapActions("productlist", [
        "getList",
      ]),
    },
    mounted() {
      this.getList();
    }
  }
</script>

<style lang="scss" scoped>
  .detailContent {
    height: calc(100vh - 60px - 57px - 93px);
  }

  .titleFontDetail {
    font-size: 22px;
  }

  .notify {
    font-size: 14px;
  }

  table {
    td {
      font-size: 14px;
      vertical-align: middle;

      .btn-search {
        width: 30px;
        height: 30px;
        box-sizing: border-box;
        font-size: 14px;
        color: white;
        background: #3343F7;
        border-radius: 3px;
        display: flex;
        align-items: center;
        justify-content: center;

        &:hover {
          opacity: .6
        }
      }
    }
  }

  tbody {
    td {
      height: 70px;

      img {
        width: 65px;
        height: 65px;
      }
    }
  }
</style>



