import api from '../api'
import {getField, updateField} from 'vuex-map-fields';
import helper from './common/helper'

export const getters = Object.assign({getField}, helper.getters)

export const state = Object.assign({
  store: {},
  price_a: 0,
  price_b: 0,
  price_c: 0,
  date: '',
  price_d: 0,
  price_e: 0,
  price_f: 0,
  price_g: 0,
  price_h: 0,
  price_i: 0,
  vars: [],
}, helper.state)

export const actions = Object.assign({
  init({commit, state, rootState}) {
    commit('ui/loading', {isLoading: true}, {root: true})
    api.apply.init({
      id: location.hash.substr(2) >> 0,
    }).then((rep) => {
      commit('setValue', {
        key: 'store',
        val: rep.data.body.store
      })
      commit('setValue', {
        key: 'price_a',
        val: rep.data.body.used
      })
      commit('setValue', {
        key: 'price_c',
        val: rep.data.body.cancel
      })
      commit('setValue', {
        key: 'price_b',
        val: rep.data.body.expired
      })
      commit('setValue', {
        key: 'price_g',
        val: ((rep.data.body.used + rep.data.body.expired) * rep.data.body.store.yfp_store_trust_par / 100).toFixed(0)
      })
      commit('setValue', {
        key: 'date',
        val: rep.data.body.date
      })
      commit('setValue', {
        key: 'date',
        val: rep.data.body.date
      })
      commit('setValue', {
        key: 'vars',
        val: rep.data.body.vars
      })
    }).catch((error) => {
      window.alert('已被登出 請重新登入');
      // commit('ui/ajaxModalError', {error: error}, {root: true})
    }).finally(() => {
      commit('ui/loading', {isLoading: false}, {root: true})
    })
  },
}, helper.actions);

export const mutations = Object.assign({updateField}, helper.mutations)



