import api from '../api'
import router from '../router'
import jwt_decode from 'jwt-decode'
import {getField, updateField} from 'vuex-map-fields';

const user = JSON.parse(localStorage.getItem('userAdmin'))

export const getters = Object.assign({getField})

export const state = {
  user: user,
  manager: user ? user.manager : null,
  employee: user ? user.employee : null,
  name: user ? user.name : null,
  mail: user ? user.mail : null,
  new_password: '',
  new_password_confirm: '',
  jwt: null,
  roles: user ? user.roles : [],
  buttonKey: 0,
}

export const actions = {
  sendCode({rootState, commit, state}, {mail}) {
    api.account.sendCode({mail: mail}).then(rep => {
      router.push('/enterverifycode')
    }).catch(error => {
      commit('ui/ajaxModalError', {error: error}, {root: true})
    }).finally(() => {
      state.buttonKey = state.buttonKey + 1
    })
  },
  validCode({rootState, commit, state}, {code}) {
    api.account.validCode({code: code}).then(() => {
      localStorage.setItem('validCode', code)
      router.push('/loginresetpassword')
    }).catch(error => {
      commit('ui/ajaxModalError', {error: error}, {root: true})
    }).finally(() => {
      state.buttonKey = state.buttonKey + 1
    })
  },
  resetPassword({rootState, commit, state}, {password}) {
    api.account.resetPassword({password: password, code: localStorage.getItem('validCode')}).then(() => {
      localStorage.removeItem('validCode')
      router.push('/')
    }).catch(error => {
      commit('ui/ajaxModalError', {error: error}, {root: true})
    }).finally(() => {
      state.buttonKey = state.buttonKey + 1
    })
  },
  login({rootState, commit}, {name, password}) {
    commit('ui/loading', {isLoading: true}, {root: true})
    api.account
      .login(name, password)
      .then(rep => {
        commit('login', {
          user: jwt_decode(rep.data.body),
          token: rep.data.body
        })
        router.push('/admin')
      })
      .catch(error => {
        commit('ui/ajaxError', {error: error}, {root: true})
      })
      .finally(() => {
        commit('ui/loading', {isLoading: false}, {root: true})
      })
  },
  logout({commit}) {
    commit('logout')
    router.push('/')
  },
  changePassword({commit, state, dispatch}) {
    commit('ui/loading', {isLoading: true}, {root: true})
    api.account.changePassword({
      new_password: state.new_password,
      new_password_confirm: state.new_password_confirm,
    }).then(() => {
      dispatch('ui/openModal', {id: '#exampleModal'}, {root: true})
      commit('logout')
    }).catch(error => {
      commit('ui/ajaxModalError', {error: error}, {root: true})
    }).finally(() => {
      commit('ui/loading', {isLoading: false}, {root: true})
    })
  },
}

export const mutations = Object.assign({updateField}, {
    login(state, {user, token}) {
      state.user = user.data
      state.jwt = token
      state.manager = state.user.manager
      state.employee = state.user.employee
      state.mail = state.user.mail
      state.name = state.user.name
      state.roles = state.user.roles
      window.localStorage.setItem('jwt', token)
      window.localStorage.setItem('userAdmin', JSON.stringify(state.user))
    },
    logout(state) {
      state.user = null
      state.jwt = null
      window.localStorage.removeItem('jwt')
      window.localStorage.removeItem('userAdmin')
    }
  }
)



