<template>
  <div class="row px-40 align-items-center  border-top footer">
    <div class="d-flex align-items-center">
      <span class="text-dark fz14 font-weight-bold">View</span>
      <select v-model="_perPage" class="form-control ml-3 py-0">
        <option value="15">15</option>
        <option value="20">20</option>
        <option value="25">25</option>
      </select>
      <span class="text-drak fz14 ml-3 font-weight-bold">Items Per Page</span>
    </div>
    <nav aria-label="Page navigation example" class="ml-auto mr-auto">
      <ul class="pagination pagination-sm mb-0 text-dark font-weight-bold">
        <li class="page-item" @click="setCurrentPage({page:1})">
          <a class="page-link border-0 text-dark" href="javascript: void(0)" aria-label="Previous">
            <span aria-hidden="true">&laquo;</span>
          </a>
        </li>
        <li class="page-item" @click="prevOrNext({n:-1})">
          <a class="page-link border-0 text-dark" href="javascript: void(0)" aria-label="Previous">
            <span aria-hidden="true">&lsaquo;</span>
          </a>
        </li>
        <li class="page-item" v-for="page in pages" :key="page">
          <a v-if="Number.isInteger(page)" @click="setCurrentPage({page:page})" class="page-link border-0 text-dark"
             :class="{active: page === currentPage}"
             href="javascript: void(0)">{{page}}</a>
          <a v-if="!Number.isInteger(page)" class="page-link border-0 text-dark"
             href="javascript: void(0)">{{page}}</a>
        </li>
        <li @click="prevOrNext({n:1})" class="page-item">
          <a class="page-link  border-0 text-dark" href="javascript: void(0)" aria-label="Previous">
            <span aria-hidden="true">&rsaquo;</span>
          </a>
        </li>
        <li @click="setCurrentPage({page:totalPage})" class="page-item">
          <a class="page-link border-0 text-dark" href="javascript: void(0)" aria-label="Next">
            <span aria-hidden="true">&raquo;</span>
          </a>
        </li>
      </ul>
    </nav>
    <span class="text-dark fz14">All {{totalCount}} Items</span>
  </div>
</template>

<script>
  export default {
    props: [
      "perPage",
      "totalPage",
      "currentPage",
      "totalCount",
      "pages",
      "setCurrentPage",
      "prevOrNext",
      "getList",
      "startPage",
      "endPage",
      "setPerPage"
    ],
    name: "pagination",
    computed: {
      _perPage: {
        get() {
          return this.perPage
        },
        set(value) {
          this.setPerPage({perPage: value})
        }
      }
    },
  };
</script>

<style scoped>
  .paginationDrop {
    font-size: 14px;
    color: #4b4b4b;
  }

  .pageFilter {
    width: 80px;
    height: 30px;
    border-radius: 5px;
    border: 1px solid #9fc4f5;
  }

  .page-item > .active {
    color: red !important;
  }

  .form-control{
    height: 30px;
    width: 80px;
  }
</style>



