import api from '../api'
import pagination from './common/pagination'
import {getField, updateField} from 'vuex-map-fields';
import ajax from 'axios'

export const getters = Object.assign({getField}, pagination.getters)

export const state = Object.assign({
  searchText: '',
  searchStartDate: '',
  searchEndDate: '',
  filterStatus: [],
}, pagination.state)

export const actions = Object.assign(pagination.actions, {
  startSearch({commit, dispatch}) {
    commit('currentPage', {page: 1})
    dispatch('getList')
  },
  getList({commit, state, rootState}) {
    commit('ui/loading', {isLoading: true}, {root: true})
    api.voucherStoreListDetail.list({
      search: state.paginationSearch,
      perPage: state.perPage,
      page: state.currentPage,
      searchText: state.searchText,
      searchStartDate: state.searchStartDate,
      searchEndDate: state.searchEndDate,
      filterStatus: state.filterStatus,
      id: rootState.route.params.id >> 0,
    }).then((rep) => {
      commit('paginationRows', {rows: rep.data.body.list})
    }).catch((error) => {
      commit('ui/ajaxError', {error: error}, {root: true})
    }).finally(() => {
      commit('ui/loading', {isLoading: false}, {root: true})
    })
  },
  exportExcel({commit, state, rootState}) {
    commit('ui/loading', {isLoading: true}, {root: true})
    ajax.post(process.env.API_URL + 'store_detail/excel', {
      search: state.paginationSearch,
      perPage: state.perPage,
      page: state.currentPage,
      searchText: state.searchText,
      searchStartDate: state.searchStartDate,
      searchEndDate: state.searchEndDate,
      filterStatus: state.filterStatus,
      id: rootState.route.params.id >> 0,
    }, {
      headers: {
        'Authorization': 'Bearer ' + window.localStorage.getItem('jwt')
      }
    }).then((rep) => {
      let link = window.document.createElement("a");
      link.href = rep.data;
      link.download = "Excel.xls";
      link.click();
    }).catch((error) => {
      commit('ui/ajaxError', {error: error}, {root: true})
    }).finally(() => {
      commit('ui/loading', {isLoading: false}, {root: true})
    })
  },
});

export const mutations = Object.assign({updateField}, pagination.mutations)



