<template>
  <!-- 編輯權限Modal -->
  <div class="modal fade" id="addCharacterFunctionModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel"
       aria-hidden="true">
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header justify-content-center">
          <h5 class="modal-title font-weight-bold text-dark" id="exampleModalLabel">編輯角色權限</h5>
        </div>
        <div class="modal-body">
          <p class="mb-0 d-flex align-items-center">
            <span class="text-primary font-weight-bold">權限列表</span>
          </p>
          <div class="blueBox w-100">
            <div class="custom-control custom-checkbox mb-3">
              <input @click="checkAll()" :checked="roles.length >= fullRoles.length" type="checkbox"
                     class="custom-control-input" id="all">
              <label class="custom-control-label text-dark fz14" for="all">全選</label>
            </div>
            <div class="custom-control custom-checkbox mb-3 ">
              <input name="roles" value="票券庫存查詢" v-model="roles" type="checkbox" class="custom-control-input"
                     id="voucherStoreSearch">
              <label class="custom-control-label text-dark fz14" for="voucherStoreSearch">票券庫存查詢</label>
            </div>
            <div class="custom-control custom-checkbox  mb-3 ">
              <input name="roles" v-model="roles" value="商家資料建立與總表－【編輯】" type="checkbox" class="custom-control-input"
                     id="shopBuildEdit">
              <label class="custom-control-label text-dark fz14" for="shopBuildEdit">商家資料建立與總表－【編輯】</label>
            </div>
            <div class="custom-control custom-checkbox mb-3 ">
              <input name="roles" v-model="roles" value="商家資料建立與總表－【審核】" type="checkbox" class="custom-control-input"
                     id="shopBuildReview">
              <label class="custom-control-label text-dark fz14" for="shopBuildReview">商家資料建立與總表－【審核】</label>
            </div>
            <div class="custom-control custom-checkbox mb-3 ">
              <input name="roles" v-model="roles" value="商家撥款審核" type="checkbox" class="custom-control-input"
                     id="shopApplyMoneyReview">
              <label class="custom-control-label text-dark fz14" for="shopApplyMoneyReview">商家撥款審核</label>
            </div>
            <div class="custom-control custom-checkbox mb-3 ">
              <input name="roles" v-model="roles" value="角色建立" type="checkbox" class="custom-control-input"
                     id="buildCharac">
              <label class="custom-control-label text-dark fz14" for="buildCharac">角色建立</label>
            </div>
            <div class="custom-control custom-checkbox mb-3 ">
              <input name="roles" v-model="roles" value="管理使用者" type="checkbox" class="custom-control-input"
                     id="adminUser">
              <label class="custom-control-label text-dark fz14" for="adminUser">管理使用者</label>
            </div>
            <div class="custom-control custom-checkbox mb-3 ">
              <input name="roles" v-model="roles" value="信託票券商品查詢" type="checkbox" class="custom-control-input"
                     id="trustTicketSearch">
              <label class="custom-control-label text-dark fz14" for="trustTicketSearch">信託票券商品查詢</label>
            </div>
          </div>
        </div>
        <div class="modal-footer justify-content-around">
          <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
          <button @click="saveRoles()" type="button" class="btn btn-log">確定</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {mapFields} from "vuex-map-fields";
  import {mapActions} from "vuex";

  export default {
    computed: {
      ...mapFields("buildcharacter", ["roles", "id", "fullRoles"])
    },
    methods: {
      ...mapActions("buildcharacter", [
        "saveRoles",
      ]),
      checkAll() {
        if (this.roles.length < this.fullRoles.length) {
          this.roles = [...this.fullRoles];
        } else {
          this.roles = []
        }
      }
    },
  }
</script>

<style lang="scss" scoped>
  .fz14 {
    font-size: 14px;
  }

  .modal-dialog {
    max-width: 670px;
  }

  .modal-content {
    max-width: 670px;
    height: auto;
  }

  .modal-body {
    padding: 50px 90px;

    p:first-child {
      span {
        font-size: 20px;
      }
    }

    .blueBox {
      background: #EAF1FA;
      padding: 16px;
    }
  }

  .modal-footer .btn {
    width: 100px;
    height: 40px;
    box-sizing: border-box;
    border-radius: 22px;
    border-width: 2px;
  }

  .custom-checkbox .custom-control-label::before {
    border-radius: 50%;
  }

  .custom-control-label::before {
    top: 0.18rem;
  }
</style>



