<template>
  <div class=" container-fluid">
    <div class="row align-items-center px-40  header">
      <div class="nickName ml-auto">
        暱
        <div class="nickNameBox flex-column">
          <p class="mb-20 insideNickName">暱</p>
          <p class="text-dark font-weight-bold fz16 mb-2">{{name}}</p>
          <p class="text-dark font-weight-bold fz16">{{mail}}</p>
          <hr>
          <router-link to="/admin/resetpassword" type="button"
                       class="btn btn-outline-log btnStyle mb-3 fz14 d-flex justify-content-center align-items-center">
            變更密碼
          </router-link>
          <button type="button" class="btn btn-log btnStyle fz14 d-flex justify-content-center align-items-center"
                  @click="logout">登出帳戶
          </button>
        </div>
      </div>
    </div>
    <!-- navs -->
    <div class="row bg-white px-40 headNav ">
      <router-link v-if="roles.indexOf('票券庫存查詢') >= 0"
                   to="/admin/voucherstoresearch"
                   class="text-decoration-none d-flex  align-items-center border-bottom"
      >
        <div
          @click="navItem = '票券庫存'"
          class="h-100 d-flex align-items-center px-4"
          :class="{
            'tabBorderSet': navItem == '票券庫存',
            'text-undo': navItem !== '票券庫存'
          }"
        >
          <img
            src="../../assets/threetab/search.png"
            alt=""
            :style="{ opacity: navItem == '票券庫存' ? 1 : 0.2 }"
          />
          <span class=" font-weight-bold py-3 voucherStore ml-3 fz14">
            票券庫存查詢
          </span>
        </div>
      </router-link>

      <router-link v-if="roles.indexOf('商家資料建立與總表－【編輯】') >= 0 || roles.indexOf('商家資料建立與總表－【審核】')"
                   to="/admin/merchantdatabuild"
                   class="text-decoration-none d-flex  align-items-center border-bottom"
      >
        <div
          @click="navItem = '商家資料建立與總表'"
          class="h-100 d-flex align-items-center px-4"
          :class="{
            'tabBorderSet': navItem == '商家資料建立與總表',
            'text-undo': navItem !== '商家資料建立與總表'
          }"
        >
          <img
            src="../../assets/threetab/setandall.png"
            alt=""
            :style="{ opacity: navItem == '商家資料建立與總表' ? 1 : 0.2 }"
          />
          <span class=" font-weight-bold py-3 voucherStore ml-3 fz14">
            商家資料建立與總表
          </span>
        </div>
      </router-link>

      <router-link
        v-if="roles.indexOf('商家撥款審核') >= 0"
        to="/admin/shopAppropriateReview"
        class="text-decoration-none d-flex  align-items-center border-bottom"
      >
        <div
          @click="navItem = '商家撥款審核'"
          class="h-100 d-flex align-items-center px-4"
          :class="{
            'tabBorderSet': navItem == '商家撥款審核',
            'text-undo': navItem !== '商家撥款審核'
          }"
        >
          <img
            src="../../assets/threetab/review.png"
            alt=""
            :style="{ opacity: navItem == '商家撥款審核' ? 1 : 0.2 }"
          />
          <span class=" font-weight-bold py-3 voucherStore ml-3 fz14">
            商家撥款審核
          </span>
        </div>
      </router-link>
      <router-link v-if="roles.indexOf('角色建立') >= 0"
                   to="/admin/buildcharacter" class="text-decoration-none">
        <div @click="navItem = '角色建立'" class="h-100 d-flex align-items-center px-4"
             :class="{'tabBorderSet': navItem == '角色建立','text-undo': navItem !== '角色建立'}">
          <img src="../../assets/threetab/buildcharacter.png" alt="" :style="{ opacity: navItem == '角色建立' ? 1 : 0.2 }">
          <span class=" font-weight-bold py-3 voucherStore ml-3 fz14">
            角色建立
          </span>
        </div>
      </router-link>
      <router-link v-if="roles.indexOf('管理使用者') >= 0" to="/admin/adminuser" class="text-decoration-none">
        <div @click="navItem = '管理使用者'" class="h-100 d-flex align-items-center px-4"
             :class="{'tabBorderSet': navItem == '管理使用者','text-undo': navItem !== '管理使用者'}">
          <img src="../../assets/threetab/adminuser.png" alt="" :style="{ opacity: navItem == '管理使用者' ? 1 : 0.2 }">
          <span class=" font-weight-bold py-3 voucherStore ml-3 fz14">
            管理使用者
          </span>
        </div>
      </router-link>
      <router-link v-if="roles.indexOf('信託票券商品查詢') >= 0" to="/admin/productsearch" class="text-decoration-none">
        <div @click="navItem = '信託票券商品查詢'" class="h-100 d-flex align-items-center px-4"
             :class="{'tabBorderSet': navItem == '信託票券商品查詢','text-undo': navItem !== '信託票券商品查詢'}">
          <img src="../../assets/threetab/productsearch.png" alt=""
               :style="{ opacity: navItem == '信託票券商品查詢' ? 1 : 0.2 }">
          <span class=" font-weight-bold py-3 voucherStore ml-3 fz14">
            信託票券商品查詢
          </span>
        </div>
      </router-link>
      <router-link to="/admin/logsearch" class="text-decoration-none">
        <div @click="navItem = 'Log異動查詢'" class="h-100 d-flex align-items-center px-4"
             :class="{'tabBorderSet': navItem == 'Log異動查詢','text-undo': navItem !== 'Log異動查詢'}">
          <img src="../../assets/threetab/log.png" alt=""
               :style="{ opacity: navItem == 'Log異動查詢' ? 1 : 0.2 }">
          <span class=" font-weight-bold py-3 voucherStore ml-3 fz14">
            Log異動查詢
          </span>
        </div>
      </router-link>
    </div>
    <!-- Modal -->
    <div class="modal fade" id="exampleModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel"
         aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-body d-flex flex-column justify-content-center">
            <img src="../../assets/icons8-checkmark.svg" alt="" class="mb-30">
            <p class="text-danger text-center fz22 font-weight-bold">密碼已更新！</p>
            <p class="text-secondary text-center font-weight-bold">密碼更新成功！請您重新登入。</p>
          </div>
          <div class="modal-footer d-flex justify-content-center">
            <button type="button" class="btn btn-outline-log" @click.prevent="logout" data-dismiss="modal">好的</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {mapState, mapActions} from "vuex";

  export default {
    props: ['tabActive'],
    data() {
      return {
        navItem: ""
      };
    },
    computed: {
      ...mapState('account', ['name', 'mail', 'roles'])
    },
    methods: {
      ...mapActions('account', ['logout']),
      ...mapActions('ui', ['resetErrMsg']),
      handleSubmit() {
        const {name, password} = this;
        this.login({name, password});
      }
    },
    watch: {
      tabActive() {
        this.navItem = this.tabActive;
      }
    },
    mounted() {
      this.navItem = this.tabActive;
    },
    created() {
      const vm = this;
      vm.location = window.location.hash;
    }
  };
</script>

<style lang="scss">
  .fz14 {
    font-size: 14px;
  }

  .fz16 {
    font-size: 16px;
  }

  .fz22 {
    font-size: 22px;
  }

  .header {
    background: transparent linear-gradient(89deg, #1b79f4 0%, #27489a 53%, #3b44a9 100%) 0% 0% no-repeat;
    box-shadow: 0px 2px 4px #00000029;
    height: 60px;
    box-sizing: border-box;
  }

  .nickName {
    font-size: 24px;
    color: white;
    width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    border: 2px solid white;
    position: relative;
    box-sizing: border-box;
    cursor: pointer;

    &:hover {
      color: rgba(256, 256, 256, .4);
      border: 2px solid rgba(256, 256, 256, .4);

      .nickNameBox {
        display: flex;
      }
    }
  }

  .nickNameBox {
    position: absolute;
    top: 48px;
    right: 15px;
    background: white;
    width: 340px;
    height: 360px;
    display: none;
    align-items: center;
    box-shadow: 0px 3px 6px #00000029;
    border: 0.5px solid #C3D0DC;
    border-radius: 3px;
    padding-top: 40px;
    z-index: 10000000;

    hr {
      width: 100%;
      border-top: 1px solid #C3D0DC;
      margin-bottom: 24px;
    }

    .btnStyle {
      width: 140px;
      height: 39px;
      border-radius: 23px;
    }
  }

  .insideNickName {
    font-size: 24px;
    color: #C3D0DC;
    width: 60px;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    border: 2px solid #2F7BFB;
    position: relative;
  }


  .headNav {
    box-shadow: 0px 2px 4px #00000029;

    img {
      width: 30px;
      height: 30px;
    }
  }

  .btn-outline-light {
    border-width: 2px;
    border-radius: 15px;

    &:hover {
      color: #3b44a9;
    }
  }

  .fz14 {
    font-size: 14px;
  }

  .voucherStore {
    text-align: center;
    box-sizing: border-box;
    cursor: pointer;
  }

  .tabBorderSet {
    border-bottom: 3px solid #3b44a9;
    color: #3b44a9;
  }

  // modal style
  .modal-backdrop {
    z-index: 1019;
  }

  .modal-backdrop.show {
    opacity: 0;
  }

  .modal-dialog {
    margin-top: 100px;
    z-index: 1021;
  }

  .modal-content {
    max-width: 450px;
    height: 380px;
    box-shadow: 0px 0px 10px #00000029;
    border: 1px solid #EFECEC;
    border-radius: 10px;

  }

  .modal-footer {
    .btn {
      width: 100px;
      height: 40px;
      box-sizing: border-box;
      border-radius: 22px;
      border-width: 2px;
    }
  }

  .arrowLeftLayout {
    width: 33px;
    height: 33px;
    box-sizing: border-box;
    background: rgba(256, 256, 256, 0.4);
    border-radius: 50%;
  }
</style>



