import api from '../api'
import {getField, updateField} from 'vuex-map-fields';
import helper from './common/helper'

export const getters = Object.assign({
  getField,
}, helper.getters)

function initialState() {
  return {
    yfp_ticket_id: '',
    yfp_ticket_name: '',
    yfp_ticket_sub_title: '',
    yfp_ticket_address: '',
    yfp_ticket_branch_url: '',
    yfp_ticket_content: '',
    yfp_ticket_num: '',
    yfp_ticket_start_date: '',
    yfp_ticket_end_date: '',
    yfp_ticket_order: '',
    yfp_ticket_show_sell_information: 0,
    yfp_ticket_category: '',
    yfp_ticket_base_price: 0,
    yfp_ticket_price: 0,
    yfp_ticket_image: '',
    yfp_ticket_return_information: '',
    franchiseShow: 'close',
    priceDiscount: 100,
    mon: 0,
    yfp_event_num: '',
    showFranchise: '',
    method: '',
    id: null,
    category_options: [],
    lastUpdate: null,
    old_yfp_ticket_content: '',
    old_yfp_ticket_return_information: '',
    yfp_ticket_use_description: '',
    yfp_ticket_notice_description: '',
    yfp_ticket_store_description: '',
    old_yfp_ticket_use_description: '',
    old_yfp_ticket_notice_description: '',
    old_yfp_ticket_store_description: '',
    yfp_ticket_description_image: '',
    branch: [],
    yfp_ticket_branch: [],
  }
}

export const state = Object.assign(initialState(), helper.state)

export const actions = Object.assign({
  initForm({commit, rootState}) {
    const method = rootState.route.params.method
    const id = rootState.route.params.id >> 0
    commit('reset')
    commit('setValue', {key: 'method', val: method})
    commit('setValue', {key: 'id', val: id})
    commit('ui/loading', {isLoading: true}, {root: true})
    api.trustTicketPage.init({
      id: id,
      method: method
    }).then((rep) => {
      commit('setValue', {key: 'lastUpdate', val: rep.data.body.init.update})
      commit('setValue', {key: 'category_options', val: rep.data.body.init.category})
      commit('setValue', {key: 'mon', val: rep.data.body.init.mon})
      commit('setValue', {
        key: 'branch',
        val: rep.data.body.init.branch
      })
      if (rep.data.body.data) {
        commit('setValue', {key: 'yfp_ticket_name', val: rep.data.body.data.yfp_ticket_name})
        commit('setValue', {key: 'yfp_ticket_image', val: rep.data.body.data.yfp_ticket_image})
        commit('setValue', {key: 'yfp_ticket_sub_title', val: rep.data.body.data.yfp_ticket_sub_title})
        commit('setValue', {key: 'yfp_ticket_address', val: rep.data.body.data.yfp_ticket_address})
        commit('setValue', {key: 'yfp_ticket_branch_url', val: rep.data.body.data.yfp_ticket_branch_url})
        commit('setValue', {key: 'yfp_ticket_content', val: rep.data.body.data.yfp_ticket_content})
        commit('setValue', {key: 'yfp_ticket_num', val: rep.data.body.data.yfp_ticket_num})
        commit('setValue', {key: 'yfp_ticket_start_date', val: rep.data.body.data.yfp_ticket_start_date})
        commit('setValue', {key: 'yfp_ticket_end_date', val: rep.data.body.data.yfp_ticket_end_date})
        commit('setValue', {key: 'yfp_ticket_order', val: rep.data.body.data.yfp_ticket_order})
        commit('setValue', {
          key: 'yfp_ticket_return_information',
          val: rep.data.body.data.yfp_ticket_return_information
        })
        commit('setValue', {
          key: 'yfp_ticket_show_sell_information',
          val: rep.data.body.data.yfp_ticket_show_sell_information
        })
        commit('setValue', {key: 'yfp_ticket_category', val: rep.data.body.data.yfp_ticket_category})
        commit('setValue', {key: 'yfp_ticket_base_price', val: rep.data.body.data.yfp_ticket_base_price})
        commit('setValue', {key: 'yfp_ticket_price', val: rep.data.body.data.yfp_ticket_price})
        commit('setValue', {key: 'old_yfp_ticket_content', val: rep.data.body.data.yfp_ticket_content})
        commit('setValue', {
          key: 'old_yfp_ticket_return_information',
          val: rep.data.body.data.yfp_ticket_return_information
        })
        commit('setValue', {key: 'yfp_ticket_description_image', val: rep.data.body.data.yfp_ticket_description_image})
        commit('setValue', {key: 'yfp_ticket_use_description', val: rep.data.body.data.yfp_ticket_use_description})
        commit('setValue', {
          key: 'yfp_ticket_notice_description',
          val: rep.data.body.data.yfp_ticket_notice_description
        })
        commit('setValue', {key: 'yfp_ticket_store_description', val: rep.data.body.data.yfp_ticket_store_description})
        commit('setValue', {
          key: 'old_yfp_ticket_use_description',
          val: rep.data.body.data.yfp_ticket_use_description
        })
        commit('setValue', {
          key: 'old_yfp_ticket_notice_description',
          val: rep.data.body.data.yfp_ticket_notice_description
        })
        commit('setValue', {
          key: 'old_yfp_ticket_store_description',
          val: rep.data.body.data.yfp_ticket_store_description
        })
        commit('setValue', {
          key: 'priceDiscount',
          val: Math.round((rep.data.body.data.yfp_ticket_price / rep.data.body.data.yfp_ticket_base_price) * 100)
        })
        commit('setValue', {
          key: 'yfp_ticket_branch',
          val: rep.data.body.data.yfp_ticket_branch
        })
      }
    }).catch((error) => {
      commit('ui/ajaxError', {error: error}, {root: true})
    }).finally(() => {
      commit('ui/loading', {isLoading: false}, {root: true})
    })
  },
}, helper.actions);

export const mutations = Object.assign({
  updateField,
  reset(state) {
    const s = initialState()
    Object.keys(s).forEach(key => {
      state[key] = s[key]
    })
  }
}, helper.mutations)



