<template>
  <div>
    <!-- filter 選項 -->
    <div class="d-flex align-items-center">
      <div class="input-group mb-0 w-25">
        <input
          v-model="text"
          type="text"
          class="form-control border-log border-right-0 filterHeightControl fz14 pl-3"
          placeholder="| 搜尋商家名稱 或 公司統一編號"
          @keyup.enter="startSearch"
        >
        <div class="input-group-append">
          <button
            class="btn btn-outline-log  border-left-0"
            id="basic-addon2"
            @click="startSearch"
          >
            <i class="fas fa-search" />
          </button>
        </div>
      </div>
      <span class="fz14 ml-auto text-dark font-weight-bold">{{duration}}</span>
      <input
        v-model="start"
        type="date"
        class="form-control dateWidthControl border-log ml-3 fz14 "
      >
      <input
        v-model="end"
        type="date"
        class="form-control dateWidthControl border-log ml-3 fz14"
      >
      <span
        class="fz14  text-dark ml-40 font-weight-bold"
        v-if="duration === '請款時間'"
      >審核狀態</span>
      <select
        v-model="select"
        class="form-control dateWidthControl ml-3 border-log"
        v-if="duration === '請款時間'"
      >
        <option
          value=""
          selected
        >全部</option>
        <option value="待審核">待審核</option>
        <option value="已審核">已審核</option>
        <option value="退回">已駁回</option>
      </select>
      <button
        @click="startSearch"
        class="btn btn-sm ml-4 btn-tablebtn px-4 filterBtn font-weight-bold"
      >篩選</button>
    </div>
  </div>
</template>

<script>
  export default {
    props: [
      "duration",
      "startSearch",
      "setSearchText",
      "searchText",
      "searchStartDate",
      "searchEndDate",
      "setSearchStartDate",
      "setSearchEndDate",
      "setSearchSelect",
      "searchSelect"
    ],
    computed: {
      text: {
        get() {
          return this.searchText;
        },
        set(value) {
          this.setSearchText({ searchText: value });
        }
      },
      select: {
        get() {
          return this.searchSelect;
        },
        set(value) {
          this.setSearchSelect({ searchSelect: value });
        }
      },
      start: {
        get() {
          return this.searchStartDate;
        },
        set(value) {
          this.setSearchStartDate({ searchStartDate: value });
        }
      },
      end: {
        get() {
          return this.searchEndDate;
        },
        set(value) {
          this.setSearchEndDate({ searchEndDate: value });
        }
      }
    },
  };
</script>

<style lang="scss">
  .form-control::placeholder {
    color: #c1c1c1;
    // color: Red;
  }

  .fz14 {
    font-size: 14px;
  }

  // filter height
  .filterHeightControl {
    height: 45px;
    box-sizing: border-box;
    border-top-left-radius: 25px;
    border-bottom-left-radius: 25px;
  }

  .input-group > .input-group-append > .btn {
    border-top-right-radius: 25px;
    border-bottom-right-radius: 25px;
    background: white;

    &:hover {
      background: #1b79f4;
      color: white;
    }
  }

  // input date
  .dateWidthControl {
    width: 196px;
    box-sizing: border-box;
    border-radius: 25px;
    height: 45px;
  }

  .filterBtn {
    box-sizing: border-box;
    height: 45px;
    border-radius: 4px;
  }

  input[type="date"] {
    background: url("../../assets/img/calendar.svg");
    background-repeat: no-repeat;
    background-position: left 16px center;
    background-size: 15px 15px;
    padding: 2px 10px 0 40px;
    -moz-appearance: none; /* Firefox */
    -webkit-appearance: none; /* Safari and Chrome */
    appearance: none;

    &::-webkit-inner-spin-button {
      display: none;
      -webkit-appearance: none;
    }
  }
</style>



