import ajax from "axios";

export const init = (data) => {
  return ajax.post(process.env.API_URL + 'trust_ticket_form/data', data, {
    headers: {
      'Authorization': 'Bearer ' + window.localStorage.getItem('jwt')
    }
  })
}

export const submit = (data) => {
  return ajax.post(process.env.API_URL + 'trust_ticket_form/submit', data, {
    headers: {
      'Authorization': 'Bearer ' + window.localStorage.getItem('jwt')
    }
  })
}



