<template>
  <div class="shopAppropriateReview">
    <!-- serach bar -->
    <div class="searchBar">
      <searchbar
        :setSearchEndDate="setSearchEndDate"
        :setSearchStartDate="setSearchStartDate"
        :searchEndDate="searchEndDate"
        :searchStartDate="searchStartDate"
        :searchText="searchText"
        :startSearch="startSearch"
        :setSearchText="setSearchText"
        :duration="duration"
        :setSearchSelect="setSearchSelect"
        :searchSelect="searchSelect"
      />
    </div>
    <!-- table -->

    <div class="container-fluid detailContent bg-white pt-0">
      <div class="row px-40">
        <p class="titleFontDetail text-primary font-weight-bold mb-3 mt-3 ml-2">
          商家撥款審核列表
        </p>
        <div class="table-responsive px-2">
          <table class="table border-bottom table-striped table-hover">
            <thead class="thead">
            <tr class="text-light">
              <th class="bg-tablehead  text-center border-top-0 border-left">
                商家名稱
              </th>
              <th class="bg-tablehead  text-center border-top-0 border-left">
                提出請款時間
              </th>
              <th class="bg-tablehead  text-center border-top-0 border-left">
                請款總張數
              </th>
              <th class="bg-tablehead  text-center border-top-0 border-left">
                請款總金額
              </th>
              <th class="bg-tablehead  text-center border-top-0 border-left">
                執行
              </th>
              <th class="bg-tablehead  text-center border-top-0 border-left">
                下載請款指示書
              </th>
            </tr>
            </thead>
            <tbody>
            <tr
              v-for="(item,index) in listRows"
              :key="index"
            >
              <td class="text-center border-left">{{item[0]}}</td>
              <td class="text-center">{{item[1]}}</td>
              <td class="text-center">{{item[2]}}</td>
              <td class="text-center text-darkOrange font-weight-bold">
                {{item[3] | currency}}
              </td>
              <td
                v-if="item[4] === '待審核'"
                class="waitReview text-center font-weight-bold"
              >
                <div class="d-flex align-items-center justify-content-center pl-3">
                  {{item[4]}}
                  <router-link
                    class="text-decoration"
                    :to="'/shopAppropriateList/'+item[6]"
                  >
                    <button class="btn btn-sm btn-outline-log ml-55 font-weight-bold">
                      查看明細
                    </button>
                  </router-link>
                </div>
              </td>
              <td
                v-if="item[4] === '退回'"
                class="rejected font-weight-bold"
              >
                <div class="d-flex align-items-center justify-content-center ">
                  <div class="text-center">
                    <p class="mb-0">已駁回</p>
                    <p
                      class="mb-0"
                      data-toggle="modal"
                      data-target="#returnModal"
                      @click="reason=item[5]"
                    ><span class="clamationMark">!</span>駁回理由</p>
                  </div>

                  <!-- <br>{{item[5]}} TODO: 駁回理由 style -->

                  <router-link
                    class="text-decoration"
                    :to="'/shopAppropriateList/'+item[6]"
                  >
                    <button class="btn btn-sm btn-outline-log ml-40 font-weight-bold">
                      查看明細
                    </button>
                  </router-link>
                </div>

              </td>
              <td
                v-if="item[4] === '已審核'"
                class="reviewed text-center font-weight-bold"
              >
                <div class="d-flex align-items-center justify-content-center pl-3">

                  已審核
                  <router-link
                    class="text-decoration"
                    :to="'/shopAppropriateList/'+item[6]"
                  >
                    <button class="btn btn-sm btn-outline-log ml-55 font-weight-bold">
                      查看明細
                    </button>
                  </router-link>
                </div>
              </td>
              <td class="downLoad text-center ">
                <div class="d-flex align-items-center justify-content-center">
                  <a target="_blank" :href="'/trustadmindemo/apply.html#'+item[6]">
                    <i class="fas fa-arrow-circle-down mr-3 fa-lg"/> 下載
                  </a>
                </div>
              </td>
            </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
    <!-- Reviewed Modal -->
    <div
      class="modal fade"
      id="returnModal"
    >
      <div
        class="modal-dialog"
        role="document"
      >
        <div class="modal-content">
          <div class="modal-header justify-content-center">
            <h5
              class="modal-title font-weight-bold"
              id="reviewedModalTitle"
            >駁回理由</h5>
          </div>
          <div class="modal-body d-flex align-items-center justify-content-center">
            <!-- <p class="mb-0 d-flex justify-content-center">確定要撥款 <span class="font-weight-bold ml-2">請款單號</span>
              <span class="text-danger mx-2 font-weight-bold"></span> 嗎？</p> -->
            {{reason}}
          </div>
          <div class="modal-footer d-flex justify-content-around">
            <button
              type="button"
              class="btn btn-outline-log"
              data-dismiss="modal"
            >確定
            </button>
            <!-- <button
                    type="button"
                    class="btn btn-log"
                    data-dismiss="modal"
            >確定
            </button> -->
          </div>
        </div>
      </div>
    </div>

    <!-- footer -->
    <footer class="container-fluid fixed-bottom bg-light">
      <pagination
        :perPage="perPage"
        :totalPage="totalPage"
        :currentPage="currentPage"
        :totalCount="totalCount"
        :pages="pages"
        :setCurrentPage="setCurrentPage"
        :prevOrNext="prevOrNext"
        :getList="getList"
        :startPage="startPage"
        :endPage="endPage"
        :setPerPage="setPerPage"
      />
    </footer>
  </div>
</template>

<script>
  import searchbar from "./searchbar";
  import pagination from "./pagination";
  import {mapState, mapGetters, mapActions} from "vuex";

  export default {
    components: {
      searchbar,
      pagination
    },
    data() {
      return {
        duration: "請款時間",
        reason: ""
      };
    },
    computed: {
      ...mapState("shopAppropriateReview", [
        "perPage",
        "totalPage",
        "currentPage",
        "totalCount",
        "listRows",
        "searchText",
        "searchStartDate",
        "searchEndDate",
        "searchSelect"
      ]),
      ...mapGetters("shopAppropriateReview", ["pages", "startPage", "endPage"])
    },
    methods: {
      ...mapActions("shopAppropriateReview", [
        "setCurrentPage",
        "prevOrNext",
        "getList",
        "setPerPage",
        "setSearchText",
        "setSearchStartDate",
        "setSearchEndDate",
        "startSearch",
        "setSearchSelect"
      ])
    },
    created() {
      this.getList();
    }
  };
</script>

<style lang="scss">
  .shopAppropriateReview {
    .searchBar .form-control::placeholder {
      color: #c1c1c1;
    }

    .searchBar {
      padding: 24px 52px;
      background: #f7f7f7;
      box-sizing: border-box;
      margin-top: 2px;
    }

    .detailContent {
      padding-top: 16px;
      height: calc(100vh - 60px - 57px - 50px - 93px);
      overflow-y: scroll;

      &::-webkit-scrollbar {
        display: none;
      }

      .titleFontDetail {
        font-size: 22px;
      }
    }

    // table setting
    .table {
      thead {
        display: block;
        width: 100%;

        tr {
          width: 100%;
          display: flex;
        }
      }

      thead th {
        height: 45px;
        display: flex;
        width: calc(100% / 6);
        box-sizing: border-box;
        border-bottom: 0;
        align-items: center;
        justify-content: center;
        padding: 0;
        font-size: 14px;

        &.border-left {
          border: 1px solid #efecec;
        }
      }

      tbody {
        display: block;
        overflow: auto;
        height: 500px;
        width: 100%;

        &::-webkit-scrollbar {
          display: none;
        }

        tr {
          width: 100%;
          display: flex;
        }
      }

      tbody td {
        display: flex;
        height: 76px;
        box-sizing: border-box;
        padding: 0;
        align-items: center;
        justify-content: center;
        width: calc(100% / 6);
        font-size: 14px;
      }

      tr {
        th:nth-child(1) {
          border-radius: 5px 0 0 0;
        }

        th:last-child {
          border-radius: 0 5px 0 0;
        }

        td:last-child {
          border-right: 1px solid #dee2e6;
        }
      }

      td {
        &.waitReview,
        &.downLoad {
          color: #37a75d;
        }

        &.reviewed {
          color: #c1c1c1;
        }

        &.rejected {
          color: #0f4690;
        }

        button {
          width: 86px;
          height: 30px;
          box-sizing: border-box;
        }
      }
    }

    // footer
    .footer {
      height: 50px;
      box-sizing: border-box;

      .form-control {
        width: 80px;
        height: 30px;
        font-size: 14px;
      }
    }

    // 驚嘆號
    .clamationMark {
      font-size: 14px;
      line-height: 14px;
      height: 14px;
      width: 14px;
      box-sizing: border-box;
      background: #fc4524;
      color: white;
      display: inline-block;
      text-align: center;
      border-radius: 50%;
      cursor: pointer;
    }

    p[data-target="#returnModal"] {
      cursor: pointer;
    }

    // modal
    #returnModal {
      .modal-content {
        border-radius: 20px;
        box-shadow: 0px 0px 10px #00000029;
      }

      .modal-title {
        color: #555151;
        font-weight: bold;
      }

      .modal-dialog {
        max-width: 500px;
        height: 316px;
        box-sizing: border-box;
        border-radius: 20px;
        margin: 150px auto;
      }

      .modal-footer {
        .btn {
          width: 100px;
          height: 40px;
          border-radius: 22px;
        }
      }
    }
  }
</style>



