import Vue from "vue";
import Router from "vue-router";
import admin from "@/components/admin";
import voucherstoresearch from "@/components/component/voucherstoresearch";
import voucherstorelistdetail from "@/components/component/voucherstorelistdetail";
import voucherdetailinfo from "@/components/component/voucherdetailinfo";
import merchantdatabuild from "@/components/component/merchantdatabuild";
import edit from "@/components/component/edit";
import reviewing from "@/components/component/reviewing";
import shopAppropriateReview from "@/components/component/shopAppropriateReview";
import shopAppropriateList from "@/components/component/shopAppropriateList";
import login from "@/components/component/login";
import loginbox from "@/components/component/loginbox"
import forgetpassword from "@/components/component/forgetpassword"
import enterverifycode from "@/components/component/enterverifycode"
import emailverify from "@/components/component/emailverify"
import loginresetpassword from "@/components/component/loginresetpassword"
import resetpassword from "@/components/component/resetpassword"
import buildcharacter from "@/components/component/buildcharacter"
import adminuser from "@/components/component/adminuser"
import productsearch from "@/components/component/productsearch"
import productlist from "@/components/component/productlist"
import addtrustticketpage from "@/components/component/addtrustticketpage"
import review from "@/components/component/review"
import firstpage from "@/components/component/firstpage"
import logsearch from "@/components/component/logsearch"
import changelog from "@/components/component/changelog"
import authoritylog from "@/components/component/authoritylog"
import loginlog from "@/components/component/loginlog"


Vue.use(Router);

const router = new Router({
  routes: [
    {
      path: "/",
      name: "login",
      component: login,
      children: [
        {
          path: '',
          name: 'loginbox',
          component: loginbox
        },
        {
          path: 'forgetpassword',
          name: 'forgetpassword',
          component: forgetpassword
        },
        {
          path: 'enterverifycode',
          name: 'enterverifycode',
          component: enterverifycode
        },
        {
          path: 'emailverify',
          name: 'emailverify',
          component: emailverify
        },
        {
          path: 'loginresetpassword',
          name: 'loginresetpassword',
          component: loginresetpassword
        }
      ]
    },
    {
      path: "/admin",
      name: "admin",
      component: admin,
      children: [
        {
          path: "",
          name: "firstpage",
          component: firstpage
        },
        {
          path: "voucherstoresearch",
          name: "voucherstoresearch",
          component: voucherstoresearch
        },
        {
          path: "merchantdatabuild",
          name: "merchantdatabuild",
          component: merchantdatabuild
        },
        {
          path: "shopAppropriateReview",
          name: "shopAppropriateReview",
          component: shopAppropriateReview
        },
        {
          path: "resetpassword",
          name: "resetpassword",
          component: resetpassword
        },
        {
          path: "buildcharacter",
          name: "buildcharacter",
          component: buildcharacter
        },
        {
          path: "adminuser",
          name: "adminuser",
          component: adminuser
        },
        {
          path: "productsearch",
          name: "productsearch",
          component: productsearch
        },
        {
          path: "productlist/:id",
          name: "productlist",
          component: productlist
        },
        {
          path: "addtrustticketpage/:id",
          name: "addtrustticketpage",
          component: addtrustticketpage
        },,
        {
          path: "logsearch",
          name: "logSearch",
          component: logsearch,
          children:[
            {
              path:'',
              name:'loginlog',
              component:loginlog
            },
            {
              path: 'changelog',
              name: 'changelog',
              component: changelog 
            },
            {
              path:'authoritylog',
              name:'authoritylog',
              component:authoritylog
            }
          ]
        }
      ]
    },
    {
      path: "/voucherstorelistdetail/:id",
      name: "voucherstorelistdetail",
      component: voucherstorelistdetail
    },
    {
      path: "/voucherdetailinfo/:id/:ticketId",
      name: "voucherdetailinfo",
      component: voucherdetailinfo
    },
    {
      path: "/edit/:id",
      name: "edit",
      component: edit
    },
    {
      path: "/review/:id",
      name: "review",
      component: review
    },
    {
      path: "/shopAppropriateList/:id",
      name: "shopAppropriateList",
      component: shopAppropriateList
    }
  ]
});

router.beforeEach((to, from, next) => {
  const publicPages = ["/", "/forgetpassword", "/enterverifycode", "/loginresetpassword", "/emailverify", "/firstpage", "/logsearch"];
  const authRequired = !publicPages.includes(to.path);
  const loggedIn = !!localStorage.getItem("userAdmin");
  if (authRequired && !loggedIn) {
    next({path: '/'})
  }
  next();
});

export default router;



