<template>
  <div class="adminuser">
    <CTRB :getList="getList"/>
    <div class="container-fluid detailContent bg-white pt-0">
      <div class="row px-40">
        <p class="titleFontDetail text-primary font-weight-bold mb-3 mt-3 ml-4">
          使用者權限列表
        </p>
        <div class="table-responsive px-4">
          <table class="table border-bottom border-left border-right">
            <thead class="thead w-100">
            <tr>
              <th class="bg-tablehead text-center border-top-0 text-white border-right">帳號</th>
              <th class="bg-tablehead text-center border-top-0 text-white border-right">角色</th>
              <th class="bg-tablehead text-center border-top-0 text-white border-right">姓名/名稱</th>
              <th class="bg-tablehead text-center border-top-0 text-white border-right">權限列表</th>
              <th class="bg-tablehead text-center border-top-0 text-white border-right">編輯</th>
            </tr>
            </thead>
            <tbody class="bg-white">
            <tr v-for="item in listRows" class="text-dark">
              <td class="text-center  text-dark">{{item[2]}}</td>
              <td class="text-center  text-dark">{{item[3]}}</td>
              <td class="text-center  text-dark">{{item[1]}}</td>
              <td class="text-center  text-dark">
                <div class="d-flex flex-column">
                  <template v-for="role in item[4]">
                    <span v-html="role" class="fz14 text-page"></span>
                  </template>
                </div>
              </td>
              <td>
                <div class="d-flex justify-content-center align-items-center font-weight-bold  h-100">
                  <button @click="edit(item)" class="btn btn-success tableBtn">
                    <i class="fas fa-pencil-alt"></i>
                  </button>
                  <button @click="trash({id:item[0]})" class="btn btn-danger tableBtn ml-3"><i class="fas fa-trash-alt"></i>
                  </button>
                </div>
              </td>
            </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
    <addusermodal/>
    <!-- footer -->
    <footer class="container-fluid fixed-bottom bg-light">
      <pagination
        :perPage="perPage"
        :totalPage="totalPage"
        :currentPage="currentPage"
        :totalCount="totalCount"
        :pages="pages"
        :setCurrentPage="setCurrentPage"
        :prevOrNext="prevOrNext"
        :getList="getList"
        :startPage="startPage"
        :endPage="endPage"
        :setPerPage="setPerPage"
      />
    </footer>
  </div>
</template>

<script>
  import CTRB from './characterrelativebar'
  import addusermodal from './addusermodal'
  import pagination from "./pagination";
  import {mapActions, mapGetters, mapState} from "vuex";
  import {mapFields} from "vuex-map-fields";

  export default {
    components: {
      CTRB,
      addusermodal,
      pagination
    },
    computed: {
      ...mapState("adminuser", [
        "perPage",
        "totalPage",
        "currentPage",
        "totalCount",
        "listRows",
      ]),
      ...mapGetters("adminuser", ["pages", "startPage", "endPage"]),
      ...mapFields("adminuser", ["roles", "yfp_bank_user_id", "yfp_bank_user_name", "yfp_bank_user_account", "yfp_bank_user_yfp_bank_user_roles_id", "yfp_bank_user_password"])
    },
    methods: {
      ...mapActions("ui", [
        "openModal",
      ]),
      ...mapActions("adminuser", [
        "setCurrentPage",
        "prevOrNext",
        "getList",
        "setPerPage",
        "exportExcel",
        "trash"
      ]),
      edit(item) {
        this.yfp_bank_user_id = item[0]
        this.yfp_bank_user_name = item[1]
        this.yfp_bank_user_account = item[2]
        this.yfp_bank_user_yfp_bank_user_roles_id = item[5]
        this.yfp_bank_user_password = ''
        this.openModal({id: '#addUserModal'})
      },
    },
    mounted() {
      this.setCurrentPage({page: 1})
      this.getList()
    }
  }
</script>

<style lang="scss" scoped>
  .fz14 {
    font-size: 14px;
  }

  .detailContent {
    padding-top: 16px;
    height: calc(100vh - 60px - 57px - 50px - 93px);
    overflow-y: scroll;

    &::-webkit-scrollbar {
      display: none;
    }

    .titleFontDetail {
      font-size: 22px;
    }
  }

  // table
  .thead {
    height: 45px;

    th {
      font-size: 14px;

      &:first-child {
        border-top-left-radius: 5px;
      }

      &:last-child {
        border-top-right-radius: 5px;
      }
    }
  }

  tbody {
    tr:first-child {
      td {
        border-top: 0px;
      }
    }

    td {
      font-size: 14px;
      vertical-align: middle;
    }
  }

  .tableBtn {
    width: 30px;
    height: 30px;
    box-sizing: border-box;
    border-radius: 3px;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  // footer
  .footer {
    height: 50px;
    box-sizing: border-box;

    .form-control {
      width: 80px;
      height: 30px;
      font-size: 14px;
    }
  }
</style>



