import api from '../api'
import pagination from './common/pagination'
import searchBar from './common/searchBar'

export const getters = Object.assign({}, pagination.getters, searchBar.getters)

export const state = Object.assign({}, pagination.state, searchBar.state)

export const actions = Object.assign({
  getList({commit, state}) {
    commit('ui/loading', {isLoading: true}, {root: true})
    api.merchant.list({
      search: state.paginationSearch,
      perPage: state.perPage,
      page: state.currentPage,
      searchText: state.searchText,
      searchStartDate: state.searchStartDate,
      searchEndDate: state.searchEndDate,
    }).then((rep) => {
      commit('paginationRows', {rows: rep.data.body.list})
    }).catch((error) => {
      commit('ui/ajaxError', {error: error}, {root: true})
    }).finally(() => {
      commit('ui/loading', {isLoading: false}, {root: true})
    })
  },
}, pagination.actions, searchBar.actions);

export const mutations = Object.assign({}, pagination.mutations, searchBar.mutations)



