import EXIF from "exif-js";
import ajax from "axios";

let Buffer = require('buffer/').Buffer


function base64ToArrayBuffer(base64) {
  base64 = base64.replace(/^data:([^;]+);base64,/gmi, '');
  const binaryString = atob(base64);
  const len = binaryString.length;
  const bytes = new Uint8Array(len);
  for (let i = 0; i < len; i++) {
    bytes[i] = binaryString.charCodeAt(i);
  }
  return bytes.buffer;
}

const getters = {}

const state = {}

const actions = {
  getBase64ImageFromUrl(url) {
    return ajax.get(url, {responseType: 'arraybuffer'}).then(response => Buffer.from(response.data, 'binary').toString('base64'))
  },
  resize({commit}, {event, field, maxWidth = 1024, maxHeight = 768, fixed = false}) {
    let exif
    const input = event.target
    if (input.files && input.files[0]) {
      const img = window.document.createElement("img");
      const canvas = window.document.createElement('canvas')
      const reader = new FileReader()
      reader.onload = (e) => {
        exif = EXIF.readFromBinaryFile(base64ToArrayBuffer(e.target.result));
        img.src = e.target.result
      }
      img.addEventListener('load', () => {
        let ctx = canvas.getContext("2d");
        ctx.drawImage(img, 0, 0);
        let width = img.width;
        let height = img.height;
        if (width > height) {
          if (width > maxWidth) {
            height *= maxWidth / width;
            width = maxWidth;
          }
        } else {
          if (height > maxHeight) {
            width *= maxHeight / height;
            height = maxHeight;
          }
        }
        if (fixed) {
          width = maxWidth;
          height = maxHeight;
        }
        if (4 < exif.Orientation && exif.Orientation < 9) {
          canvas.width = height
          canvas.height = width
        } else {
          canvas.width = width
          canvas.height = height
        }

        ctx = canvas.getContext("2d");
        switch (exif.Orientation) {
          case 2:
            ctx.transform(-1, 0, 0, 1, width, 0);
            break;
          case 3:
            ctx.transform(-1, 0, 0, -1, width, height);
            break;
          case 4:
            ctx.transform(1, 0, 0, -1, 0, height);
            break;
          case 5:
            ctx.transform(0, 1, 1, 0, 0, 0);
            break;
          case 6:
            ctx.transform(0, 1, -1, 0, height, 0);
            break;
          case 7:
            ctx.transform(0, -1, -1, 0, height, width);
            break;
          case 8:
            ctx.transform(0, -1, 1, 0, 0, width);
            break;
          default:
            break;
        }
        ctx.drawImage(img, 0, 0, width, height);
        commit('setValue', {key: field, val: canvas.toDataURL("image/jpge")})
      })
      reader.readAsDataURL(input.files[0])
    }
  }
}

const mutations = {
  setValue: (state, {key, val}) => {
    state[key] = val
  },
}


export default {
  actions,
  state,
  mutations,
  getters,
}



