import api from '../api'
import pagination from './common/pagination'
import {getField, updateField} from 'vuex-map-fields';
import helper from './common/helper'

export const getters = Object.assign({getField}, pagination.getters, helper.getters)

export const state = Object.assign({
  roles: [],
  yfp_bank_user_id: '',
  yfp_bank_user_name: '',
  yfp_bank_user_account: '',
  yfp_bank_user_yfp_bank_user_roles_id: '',
  yfp_bank_user_password: '',
  adminUserSearch: '',
}, pagination.state, helper.state)

export const actions = Object.assign({
  getList({commit, state}) {
    commit('ui/loading', {isLoading: true}, {root: true})
    api.adminuser.list({
      search: state.adminUserSearch,
      perPage: state.perPage,
      page: state.currentPage,
    }).then((rep) => {
      commit('paginationRows', {rows: rep.data.body.list})
      commit('setValue', {key: 'roles', val: rep.data.body.roles})
    }).catch((error) => {
      commit('ui/ajaxModalError', {error: error}, {root: true})
    }).finally(() => {
      commit('ui/loading', {isLoading: false}, {root: true})
    })
  },
  trash({commit, state, dispatch}, {id}) {
    commit('ui/loading', {isLoading: true}, {root: true})
    api.adminuser.trash({
      id: id,
    }).then(() => {
      dispatch('getList')
    }).catch((error) => {
      commit('ui/ajaxError', {error: error}, {root: true})
      commit('ui/loading', {isLoading: false}, {root: true})
    })
  },
  save({commit, state, dispatch}) {
    commit('ui/loading', {isLoading: true}, {root: true})
    api.adminuser.save({
      yfp_bank_user_id: state.yfp_bank_user_id,
      yfp_bank_user_name: state.yfp_bank_user_name,
      yfp_bank_user_account: state.yfp_bank_user_account,
      yfp_bank_user_yfp_bank_user_roles_id: state.yfp_bank_user_yfp_bank_user_roles_id,
      yfp_bank_user_password: state.yfp_bank_user_password,
    }).then(() => {
      dispatch('ui/closeModal', {id: '#addUserModal'}, {root: true})
      dispatch('getList')
    }).catch((error) => {
      dispatch('ui/closeModal', {id: '#addUserModal'}, {root: true})
      commit('ui/loading', {isLoading: false}, {root: true})
      commit('ui/ajaxModalError', {error: error}, {root: true})
    })
  },
}, pagination.actions, helper.actions);

export const mutations = Object.assign({updateField}, pagination.mutations, helper.mutations)



