import api from '../api'
import {getField, updateField} from 'vuex-map-fields';
import helper from './common/helper'


export const getters = Object.assign({getField}, helper.getters)

export const state = Object.assign({
  id: 0,
  yfp_ticket_content_sn: '',
  yfp_ticket_content_id: '',
  yfp_ticket_content_ticket_id: '',
  yfp_ticket_content_status: '',
  yfp_ticket_content_user_id: '',
  yfp_ticket_content_store_id: '',
  yfp_ticket_content_purchase_time: '',
  yfp_ticket_content_used_time: '',
  yfp_ticket_content_request_time: '',
  yfp_ticket_content_cancel_time: '',
  yfp_ticket_content_complete_time: '',
  yfp_ticket_content_price: '',
  yfp_ticket_content_trust_deadline: '',
  yfp_ticket_content_trust_status: '',
  yfp_ticket_content_payment_type: '',
  yfp_ticket_content_order_id: '',
  yfp_ticket_content_tsno: '',
  yfp_ticket_content_apply_sernum: '',
  yfp_ticket_content_branch_id: '',
  yfp_ticket_content_invoice_number: '',
  yfp_ticket_content_random_code: '',
  yfp_ticket_content_staff: '',
  yfp_ticket_content_invoice_index: '',
  yfp_ticket_name: '',
  yfp_ticket_price: '',
  yfp_store_title: '',
  yfp_store_vat: '',
  status: '',
  time: '',
}, helper.state)

export const actions = Object.assign({
  init({commit, state}) {
    commit('ui/loading', {isLoading: true}, {root: true})
    api.voucherDetailInfo.init({
      id: state.id,
    }).then((rep) => {
      commit('setValue', {
        key: 'status',
        val: rep.data.body.status
      })
      commit('setValue', {
        key: 'time',
        val: rep.data.body.time
      })
      commit('setValue', {
        key: 'yfp_store_vat',
        val: rep.data.body.yfp_store_vat
      })
      commit('setValue', {
        key: 'yfp_ticket_price',
        val: rep.data.body.yfp_ticket_price
      })
      commit('setValue', {
        key: 'yfp_ticket_content_invoice_index',
        val: rep.data.body.yfp_ticket_content_invoice_index
      })
      commit('setValue', {
        key: 'yfp_ticket_name',
        val: rep.data.body.yfp_ticket_name
      })
      commit('setValue', {
        key: 'yfp_store_title',
        val: rep.data.body.yfp_store_title
      })
      commit('setValue', {
        key: 'yfp_ticket_content_order_id',
        val: rep.data.body.yfp_ticket_content_order_id
      })
      commit('setValue', {
        key: 'yfp_ticket_content_tsno',
        val: rep.data.body.yfp_ticket_content_tsno
      })
      commit('setValue', {
        key: 'yfp_ticket_content_apply_sernum',
        val: rep.data.body.yfp_ticket_content_apply_sernum
      })
      commit('setValue', {
        key: 'yfp_ticket_content_branch_id',
        val: rep.data.body.yfp_ticket_content_branch_id
      })
      commit('setValue', {
        key: 'yfp_ticket_content_invoice_number',
        val: rep.data.body.yfp_ticket_content_invoice_number
      })
      commit('setValue', {
        key: 'yfp_ticket_content_random_code',
        val: rep.data.body.yfp_ticket_content_random_code
      })
      commit('setValue', {
        key: 'yfp_ticket_content_staff',
        val: rep.data.body.yfp_ticket_content_staff
      })
      commit('setValue', {
        key: 'yfp_ticket_content_sn',
        val: rep.data.body.yfp_ticket_content_sn
      })
      commit('setValue', {
        key: 'yfp_ticket_content_id',
        val: rep.data.body.yfp_ticket_content_id
      })
      commit('setValue', {
        key: 'yfp_ticket_content_ticket_id',
        val: rep.data.body.yfp_ticket_content_ticket_id
      })
      commit('setValue', {
        key: 'yfp_ticket_content_status',
        val: rep.data.body.yfp_ticket_content_status
      })
      commit('setValue', {
        key: 'yfp_ticket_content_user_id',
        val: rep.data.body.yfp_ticket_content_user_id
      })
      commit('setValue', {
        key: 'yfp_ticket_content_store_id',
        val: rep.data.body.yfp_ticket_content_store_id
      })
      commit('setValue', {
        key: 'yfp_ticket_content_purchase_time',
        val: rep.data.body.yfp_ticket_content_purchase_time
      })
      commit('setValue', {
        key: 'yfp_ticket_content_used_time',
        val: rep.data.body.yfp_ticket_content_used_time
      })
      commit('setValue', {
        key: 'yfp_ticket_content_request_time',
        val: rep.data.body.yfp_ticket_content_request_time
      })
      commit('setValue', {
        key: 'yfp_ticket_content_cancel_time',
        val: rep.data.body.yfp_ticket_content_cancel_time
      })
      commit('setValue', {
        key: 'yfp_ticket_content_complete_time',
        val: rep.data.body.yfp_ticket_content_complete_time
      })
      commit('setValue', {
        key: 'yfp_ticket_content_price',
        val: rep.data.body.yfp_ticket_content_price
      })
      commit('setValue', {
        key: 'yfp_ticket_content_trust_deadline',
        val: rep.data.body.yfp_ticket_content_trust_deadline
      })
      commit('setValue', {
        key: 'yfp_ticket_content_trust_status',
        val: rep.data.body.yfp_ticket_content_trust_status
      })
      commit('setValue', {
        key: 'yfp_ticket_content_payment_type',
        val: rep.data.body.yfp_ticket_content_payment_type
      })
    }).catch((error) => {
      commit('ui/ajaxError', {error: error}, {root: true})
    }).finally(() => {
      commit('ui/loading', {isLoading: false}, {root: true})
    })
  },
}, helper.actions);

export const mutations = Object.assign({updateField}, helper.mutations)



