<template>
  <div class="buildCharacter">
    <CTRB :getList="getList"/>
    <div class="container-fluid detailContent bg-white pt-0">
      <div class="row px-40">
        <p class="titleFontDetail text-primary font-weight-bold mb-3 mt-3 ml-4">
          角色權限管理
        </p>
        <div class="table-responsive px-4">
          <table class="table border-bottom border-left border-right">
            <thead class="thead w-100">
            <tr>
              <th class="bg-tablehead text-center border-top-0 text-white border-right">角色名稱</th>
              <th class="bg-tablehead text-center border-top-0 text-white border-right">權限列表</th>
              <th class="bg-tablehead text-center border-top-0 text-white">編輯</th>
            </tr>
            </thead>
            <tbody class="bg-white">
            <tr class="text-dark border-top" v-for="item in listRows">
              <td class="text-center font-weight-bold border-top-0 text-dark">
                {{item[1]}}
              </td>
              <td class="text-center font-weight-bold border-top-0">
                <template v-for="role in item[2]">
                  <span v-html="role" class="text-log"></span>
                  <br/>
                </template>
              </td>
              <td class="font-weight-bold border-top-0">
                <div class="d-flex justify-content-center align-items-center  h-100">
                  <button @click="openModal({id:'#addCharacterFunctionModal'});roles=[...item[2]];id=item[0]"
                        class="btn btn-success tableBtn">
                    <i class="fas fa-pencil-alt"></i>
                  </button>
                  <button @click="trash({id:item[0]})" class="btn btn-danger tableBtn ml-3">
                    <i class="fas fa-trash-alt"></i>
                  </button>
                </div>                
              </td>
            </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
    <addcharactermodal/>
    <addcharacterfunctionmodal/>
    <!-- footer -->
    <footer class="container-fluid fixed-bottom bg-light">
      <pagination
        :perPage="perPage"
        :totalPage="totalPage"
        :currentPage="currentPage"
        :totalCount="totalCount"
        :pages="pages"
        :setCurrentPage="setCurrentPage"
        :prevOrNext="prevOrNext"
        :getList="getList"
        :startPage="startPage"
        :endPage="endPage"
        :setPerPage="setPerPage"
      />
    </footer>
  </div>
</template>

<script>
  import CTRB from './characterrelativebar'
  import addcharactermodal from './addcharactermodal'
  import addcharacterfunctionmodal from './addcharacterfunctionmodal'
  import pagination from "./pagination"

  import {mapActions, mapGetters, mapState} from "vuex";
  import {mapFields} from "vuex-map-fields";

  export default {
    components: {
      CTRB,
      addcharactermodal,
      addcharacterfunctionmodal,
      pagination
    },
    computed: {
      ...mapState("buildcharacter", [
        "perPage",
        "totalPage",
        "currentPage",
        "totalCount",
        "listRows",
      ]),
      ...mapGetters("buildcharacter", ["pages", "startPage", "endPage"]),
      ...mapFields("buildcharacter", ["roles", "id"])
    },
    methods: {
      ...mapActions("ui", [
        "openModal",
      ]),
      ...mapActions("buildcharacter", [
        "setCurrentPage",
        "prevOrNext",
        "getList",
        "setPerPage",
        "exportExcel",
        "trash"
      ])
    },
    mounted() {
      this.setCurrentPage({page: 1})
      this.getList();
    }
  }
</script>

<style lang="scss" scoped>
  .detailContent {
    padding-top: 16px;
    height: calc(100vh - 60px - 57px - 50px - 93px);
    overflow-y: scroll;

    &::-webkit-scrollbar {
      display: none;
    }

    .titleFontDetail {
      font-size: 22px;
    }
  }

  // table
  .thead {
    height: 45px;

    th {
      font-size: 14px;

      &:first-child {
        border-top-left-radius: 5px;
        width: 30%;
      }

      &:last-child {
        border-top-right-radius: 5px;
        width: 20%;
      }
    }
  }

  tbody {
    td {
      font-size: 14px;
      vertical-align:middle;
    }
  }

  .tableBtn {
    width: 30px;
    height: 30px;
    box-sizing: border-box;
    border-radius: 3px;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  // footer
  .footer {
    height: 50px;
    box-sizing: border-box;

    .form-control {
      width: 80px;
      height: 30px;
      font-size: 14px;
    }
  }
</style>



