import ajax from 'axios'

export const login = (user, pass) => {
  return ajax.post(process.env.API_URL + 'user/login', {
    user: user,
    pass: pass
  })
}

export const changePassword = (data) => {
  return ajax.post(process.env.API_URL + 'user/change_password', data, {
    headers: {
      'Authorization': 'Bearer ' + window.localStorage.getItem('jwt')
    }
  })
}

export const sendCode = (data) => {
  return ajax.post(process.env.API_URL + 'user/send_code', data)
}

export const validCode = (data) => {
  return ajax.post(process.env.API_URL + 'user/valid_code', data)
}

export const resetPassword = (data) => {
  return ajax.post(process.env.API_URL + 'user/reset_password', data)
}



