<template>
  <div class="productsearchbar">
    <div class="d-flex align-items-center px-40 PSB_outerbox">
      <!-- 發行票券商家總表 用 -->
      <div class="input-group mb-0 w-25 ml-auto" v-if="location === '#/admin/productsearch'">
        <input v-model="searchProduct" @keyup.enter="getList" type="text"
               class="form-control border-log border-right-0 filterStyleControl pl-3"
               placeholder="搜尋 商家名稱/統編/信託編號">
        <div class="input-group-append">
          <button @click="getList" class="btn btn-outline-log border-left-0">
            <i class="fa fa-search"></i>
          </button>
        </div>
      </div>
      <!-- 商品列表用 -->
      <div class="input-group mb-0 w-25" v-if="location.indexOf('#/admin/productlist') >= 0">
        <input @keyup.enter="getList" v-model="searchProductList" type="text"
               class="form-control border-log border-right-0 filterStyleControl pl-3"
               placeholder="搜尋 票券商品名稱">
        <div class="input-group-append">
          <button @click="getList" class="btn btn-outline-log border-left-0">
            <i class="fa fa-search"></i>
          </button>
        </div>
      </div>
      <div class="d-flex align-items-center  ml-auto" v-if="location.indexOf('#/admin/productlist') >= 0">
        <span class="fz14 ml-auto text-dark font-weight-bold">上架區間</span>
        <input @change="getList" v-model="start" type="date"
               class="form-control dateWidthControl border-log ml-3 fz14"/>
        <input @change="getList" v-model="end" type="date" class="form-control dateWidthControl border-log ml-3 fz14"/>
      </div>
      <div class="d-flex align-items-center ml-2" v-if="location.indexOf('#/admin/productlist') >= 0">
        <span class="fz14 ml-auto text-dark font-weight-bold">票券上架狀態</span>
        <div class="btn-group btn-group-sm btn-outline-group mx-2">
          <button v-bind:class="{ typeClicked: searchType === 0}" @click="changeType({type:0})"
                  class="btn btn-outline-log font-weight-bold first">全部
          </button>
          <button v-bind:class="{ typeClicked: searchType === 1}" @click="changeType({type:1})" class="btn btn-outline-log font-weight-bold">上架</button>
          <button v-bind:class="{ typeClicked: searchType === 2}" @click="changeType({type:2})" class="btn btn-outline-log font-weight-bold last">下架</button>
        </div>
      </div>
      <button v-if="location === '#/admin/productlist'" class="btn filter">篩選</button>
    </div>
  </div>
</template>

<script>
  import {mapFields} from "vuex-map-fields";
  import {mapActions} from "vuex";

  export default {
    props: [
      "getList",
    ],
    computed: {
      ...mapFields("productsearch", ["searchProduct"]),
      ...mapFields("productlist", ["searchProductList", "start", "end", "searchType"]),
    },
    methods: {
      ...mapActions("productlist", [
        "changeType",
      ]),
    },
    data() {
      return {
        location: ''
      }
    },
    created() {
      const vm = this;
      vm.location = window.location.hash;
    }
  }
</script>

<style lang="scss" scoped>
  .PSB_outerbox {
    background: #F7F7F7;
    padding: 24px 0;
  }

  .filterStyleControl {
    height: 40px;
    box-sizing: border-box;
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
    font-size: 14px;
  }

  .input-group > .input-group-append > .btn {
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
    background: white;
    height: 40px;

    &:hover {
      background: #1b79f4;
      color: white;
    }
  }

  .typeClicked {
    background: #1b79f4;
    color: white;
  }

  // input date
  .dateWidthControl {
    width: 196px;
    box-sizing: border-box;
    border-radius: 20px;
    height: 40px;
  }

  input[type="date"] {
    background: url("../../assets/img/calendar.svg");
    background-repeat: no-repeat;
    background-position: left 16px center;
    background-size: 15px 15px;
    padding: 2px 10px 0 40px;
    -moz-appearance: none; /* Firefox */
    -webkit-appearance: none; /* Safari and Chrome */
    appearance: none;

    &::-webkit-inner-spin-button {
      display: none;
      -webkit-appearance: none;
    }
  }

  // 篩選狀態按鈕
  .btn-group {
    .btn {
      width: 60px;
      height: 40px;
      box-sizing: border-box;
      font-size: 14px;
    }

    .btn.first {
      border-radius: 20px 0 0 20px;
    }

    .btn.last {
      border-radius: 0 20px 20px 0;
    }
  }

  .btn.filter {
    width: 100px;
    height: 45px;
    box-sizing: border-box;
    border-radius: 3px;
    font-weight: bold;
    margin-left: 16px;
    background: #3343F7;
    color: white;
  }
</style>



