export const state = {
  isLoading: false,
  errMsg: 'ee',
  errCode: 0,
  loggedIn: false
}

export const actions = {
  openModal({state}, {id}) {
    window.console.log(id)
    $(id).modal('show')
  },
  closeModal({state}, {id}) {
    $(id).modal('hide')
  },
  resetErrMsg: ({commit}) => {
    commit('clearErrorMsg')
  }
};

export const mutations = {
  loading(state, {isLoading}) {
    state.isLoading = isLoading
  },
  ajaxModalError(state, {error}) {
    if (error.response) {
      state.errCode = error.response.status
      if (error.response.status === 401) {
        window.localStorage.removeItem('userAdmin')
        window.localStorage.removeItem('jwt')
        window.location.reload()
        return
      }
      if (error.response.data.hasOwnProperty('error_msg')) {
        window.alert(error.response.data.error_msg)
      } else {
        window.alert(error.toString())
      }
    } else if (error.request) {
      window.alert(error.request)
    } else {
      window.alert(error.message)
    }
  },
  ajaxError(state, {error}) {
    state.errMsg = ''
    state.errCode = 0
    if (error.response) {
      state.errCode = error.response.status
      if (error.response.status === 401) {
        window.localStorage.removeItem('userAdmin')
        window.localStorage.removeItem('jwt')
        window.location.reload()
        return
      }
      if (error.response.data.hasOwnProperty('error_msg')) {
        state.errMsg = error.response.data.error_msg
      } else {
        state.errMsg = error.toString()
      }
    } else if (error.request) {
      state.errMsg = error.request
    } else {
      state.errMsg = error.message
    }
  },
  clearErrorMsg(state) {
    state.errMsg = ''
    state.errCode = 0
  }
};



