<template>
  <!-- 新增使用者Modal -->
  <div class="modal fade" id="addUserModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel"
       aria-hidden="true">
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header justify-content-center">
          <h5 class="modal-title font-weight-bold text-dark" id="exampleModalLabel">新增/編輯使用者</h5>
        </div>
        <div class="modal-body">
          <p class="mb-0 d-flex align-items-center mb-2">
            <span class="text-danger">*</span>
            <span class="text-primary font-weight-bold fz20">選擇使用者角色</span>
          </p>
          <select v-model="yfp_bank_user_yfp_bank_user_roles_id" class="form-control mb-4">
            <option value="" disabled>請選擇</option>
            <option :key="value" :value="value" v-for="(title,value) in roles">{{title}}</option>
          </select>
          <p class="mb-0 d-flex align-items-center mb-2">
            <span class="text-danger">*</span>
            <span class="text-primary font-weight-bold fz20">輸入使用者Email</span>
            <span class="text-danger fz14 ml-2">此為使用者帳號</span>
          </p>
          <input :readonly="yfp_bank_user_id !== ''" v-model="yfp_bank_user_account" type="email"
                 class="form-control mb-4">
          <p class="mb-0 d-flex align-items-center mb-2">
            <span class="text-danger">*</span>
            <span class="text-primary font-weight-bold fz20">設定使用者密碼</span>
          </p>
          <input v-model="yfp_bank_user_password" type="password" class="form-control mb-4">
          <p class="mb-0 d-flex align-items-center mb-2">
            <span class="text-danger">*</span>
            <span class="text-primary font-weight-bold fz20">使用者姓名</span>
          </p>
          <input v-model="yfp_bank_user_name" type="text" class="form-control mb-4">
        </div>
        <div class="modal-footer justify-content-around">
          <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
          <button @click="save()" type="button" class="btn btn-log">確定新增</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {mapActions, mapGetters, mapState} from "vuex";
  import {mapFields} from "vuex-map-fields";

  export default {
    computed: {
      ...mapState("adminuser", [
        "roles",
      ]),
      ...mapGetters("adminuser", ["pages", "startPage", "endPage"]),
      ...mapFields("adminuser", ["roles", "yfp_bank_user_id", "yfp_bank_user_name", "yfp_bank_user_account", "yfp_bank_user_yfp_bank_user_roles_id", "yfp_bank_user_password"])
    },
    methods: {
      ...mapActions("adminuser", [
        "save",
      ]),
    },
  }
</script>

<style lang="scss" scoped>
  .fz14 {
    font-size: 14px;
  }

  .fz20 {
    font-size: 20px;
  }

  .modal-dialog {
    max-width: 860px;
  }

  .modal-content {
    max-width: 860px;
    height: auto;
  }

  .modal-body {
    padding: 50px 90px;

    select.form-control {
      border-radius: 2px;
    }

    input.form-control {
      border-radius: 2px;
    }
  }

  .modal-footer .btn {
    width: 100px;
    height: 40px;
    box-sizing: border-box;
    border-radius: 22px;
    border-width: 2px;
    font-size: 14px;
  }
</style>



