<template>
  <div class="productsearch">
    <PSB :getList="getList"/>
    <div class="container-fluid detailContent bg-white pt-0">
      <div class="row px-40">
        <p class="titleFontDetail text-primary font-weight-bold mb-3 mt-3 ml-4">
          發行票券商家總表 ({{listRows.length}})
        </p>
        <div class="table-responsive px-4">
          <table class="table border-bottom border-left border-right">
            <thead class="thead w-100">
            <tr>
              <td class="bg-tablehead text-center border-top-0 text-white border-right"></td>
              <td class="bg-tablehead text-center border-top-0 text-white border-right">信託編號</td>
              <td class="bg-tablehead text-center border-top-0 text-white border-right">統一編號</td>
              <td class="bg-tablehead text-center border-top-0 text-white border-right">商家名稱</td>
              <td class="bg-tablehead text-center border-top-0 text-white border-right">目前架上票券數量 (發行總數量)</td>
            </tr>
            </thead>
            <tbody class="bg-white">
            <tr v-for="(row,i) in listRows">
              <td class="text-center">{{i+1}}</td>
              <td class="text-center">{{row[0]}}</td>
              <td class="text-center">{{row[3]}}</td>
              <td class="text-center">
                <router-link :to="'/admin/productlist/'+row[0]" class="text-decoration-none text-log font-weight-bold">
                  {{row[2]}}
                </router-link>
              </td>
              <td class="text-center">{{row[5]}} ({{row[4]}})</td>
            </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import PSB from './productsearchbar'
  import {mapActions, mapState} from "vuex";

  export default {
    components: {
      PSB
    },
    computed: {
      ...mapState("productsearch", [
        "listRows",
      ]),
    },
    methods: {
      // ...mapActions("ui", [
      //   "openModal",
      // ]),
      ...mapActions("productsearch", [
        "getList",
      ]),
    },
    mounted() {
      this.getList();
    }
  }
</script>

<style lang="scss" scoped>
  .detailContent {
    height: calc(100vh - 60px - 57px - 93px);
  }

  .titleFontDetail {
    font-size: 22px;
  }

  .table {
    td {
      font-size: 14px;
      vertical-align: middle;
    }

    thead {
      color: white;
    }

    tbody {
      color: black;
    }
  }
</style>



