<template>
  <div class="resetPassword d-flex flex-column align-items-center">
    <img src="../../assets/icons8-key.png" alt="" class="resetIcon">
    <h1 class="text-primary">重設密碼</h1>
    <input v-model="new_password" type="text" class="form-control" placeholder="請輸入密碼">
    <input v-model="new_password_confirm" type="text" class="form-control mt-3" placeholder="請重新輸入密碼">
    <div class="buttonsBox d-flex">
<!--      <router-link to="/admin"-->
<!--                   class="btn btn-outline-log btnStyle cancel d-flex justify-content-center align-items-center">取消-->
<!--      </router-link>-->
      <button @click="changePassword" type="button" class="btn btn-log btnStyle ml-50">設定完成</button>
    </div>
    <!-- Modal -->
    <div class="modal fade" id="exampleModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel"
         aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-body d-flex flex-column justify-content-center">
            <img src="../../assets/icons8-checkmark.svg" alt="" class="mb-30">
            <p class="text-danger text-center fz22 font-weight-bold">密碼已更新！</p>
            <p class="text-secondary text-center">密碼更新成功！請您重新登入。</p>
          </div>
          <div class="modal-footer d-flex justify-content-center">
            <button type="button" class="btn btn-outline-log" @click.prevent="logout" data-dismiss="modal">好的</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {mapFields} from "vuex-map-fields";
  import {mapActions} from "vuex";

  export default {
    computed: {
      ...mapFields("account", ["new_password", "new_password_confirm"])
    },
    methods: {
      ...mapActions("ui", [
        "openModal",
      ]),
      ...mapActions("account", [
        "changePassword",
        "logout"
      ]),
    },
    mounted() {
      this.new_password = ''
      this.new_password_confirm = ''
    }
  }
</script>

<style lang="scss" scoped>
  .fz22 {
    font-size: 22px;
  }

  .resetIcon {
    margin-bottom: 27px;
    margin-top: 60px;
  }

  h1 {
    margin-bottom: 40px;
  }

  input {
    width: 475px;
    height: 60px;
    background: #dceafd 0% 0% no-repeat padding-box;
    border: 1px solid #C9C9C9;
    border-radius: 30px;
    background-image: url("../../assets/img/login_password.png");
    background-repeat: no-repeat;
    background-position: right 30px center;
  }

  .buttonsBox {
    margin-top: 100px;

    .btn {
      width: 268px;
      height: 60px;
      box-shadow: 0px 3px 6px #00000029;
      border-radius: 8px;
    }

    .cancel {
      margin-right: 50px;
    }
  }

  .modal-dialog {

    margin-top: 200px;
  }

  .modal-content {
    max-width: 450px;
    height: 380px;
    box-shadow: 0px 0px 10px #00000029;
    border: 1px solid #EFECEC;
    border-radius: 10px;
  }

  .modal-footer {
    .btn {
      width: 100px;
      height: 40px;
      box-sizing: border-box;
      border-radius: 22px;
      border-width: 2px;
    }
  }
</style>



